import React from 'react';
import * as Consts from '../../../shared/Consts';
import VestingSellerProfile from './VestingSellerProfile';
import VestingSellerProfileActions from './VestingSellerProfileActions';
import VestingConfirmPropertyData from './VestingConfirmPropertyData';
import VestingConfirmPropertyDataActions from './VestingConfirmPropertyDataActions';
import VestingCompletedBy from './VestingCompletedBy';
import VestingCompletedByActions from './VestingCompletedByActions';
import VestingVerification from './VestingVerification';
import VestingVerificationActions from './VestingVerificationActions';
import FooterSaveSubmitReadOnly from './FooterSaveSubmitReadOnly';

export default class Vesting extends React.Component {
  constructor() {
    super();

    this.state = {
      InvalidPropertyAddress: undefined,
      PropertyErrorMessage: undefined,
      VerifingPropertyAddress: false,
      propertyData: undefined,
      vestingCompletedBy: [],
      needCompleteBy: undefined,
      needVerification: undefined,
      vestingType: undefined,
    };
  }

  componentDidMount() {
    const propertyData = this.props.PropertyData;
    var vestingType;
    var ownerNames;
    var needCompleteBy = false;
    var needVerification = false;

    var vestingCompletedBy = [];
    if (propertyData !== undefined) {
      vestingType = propertyData.VestingTypeUsed;
      if (
        Consts.SUPPORTED_VESTING_TYPES.find(
          item =>
            item.need_complete_by === true &&
            item.vesting_type.toUpperCase() === vestingType.toUpperCase(),
        )
      ) {
        needCompleteBy = true;
      } else {
        this.props.UpdateInputValueHandler(
          'Vesting_Form_Completed_By',
          '_not_needed_',
        );
      }

      if (
        Consts.SUPPORTED_VESTING_TYPES.find(
          item =>
            item.need_verification === true &&
            item.vesting_type.toUpperCase() === vestingType.toUpperCase(),
        )
      ) {
        needVerification = true;
      }

      ownerNames = propertyData.OwnerNames.split('/');
      ownerNames.forEach(element => {
        vestingCompletedBy.push({ label: element, value: element });
      });
      vestingCompletedBy.push({
        label: 'I am not listed.',
        value: '_not_listed_',
      });
    }

    this.setState({
      propertyData,
      vestingCompletedBy,
      needCompleteBy,
      needVerification,
      vestingType,
    });
  }

  render() {
    if (this.props.SelectedTab !== Consts.ListingVesting) {
      return null;
    }

    const componentProps = {
      ...this.props,
      VestingType: this.state.vestingType,
      NeedCompleteBy: this.state.needCompleteBy,
      NeedVerification: this.state.needVerification,
    };

    if (
      !Consts.SUPPORTED_VESTING_TYPES.find(
        item =>
          item.vesting_type.toUpperCase() ===
          this.state.vestingType.toUpperCase(),
      )
    ) {
      return (
        <>
          <div className="b_texts" style={{ width: '100%', color: 'red' }}>
            Not supported vesting type {this.state.vestingType}
          </div>
          <div>Supported vesting type: </div>
          {Consts.SUPPORTED_VESTING_TYPES.map(type => (
            <div key={type.vesting_type}>{type.vesting_type}</div>
          ))}
        </>
      );
    }

    return (
      <>
        <div id="step2" className="main_form_info steppers">
          <div className="title2">
            <div className="number_title">02</div> Profile
          </div>
          <div className="b_texts">
            {`Let's get all the relevant information together and prepare any
            necessary documents so that they're ready to share with the
            appropriate parties when the time comes.`}
          </div>
          <VestingConfirmPropertyData {...componentProps} />
          <VestingCompletedBy {...componentProps} />
          <VestingVerification {...componentProps} />
          <VestingSellerProfile {...componentProps} />
        </div>

        <VestingSellerProfileActions {...componentProps} />
        <VestingConfirmPropertyDataActions {...componentProps} />
        <VestingCompletedByActions {...componentProps} />
        <VestingVerificationActions {...componentProps} />
        {this.props.VestingSuspended && (
          <FooterSaveSubmitReadOnly {...this.props} />
        )}
      </>
    );
  }
}
