// @flow
import _get from 'lodash/get';
import React, { useContext, useEffect } from 'react';
import type { Element, AbstractComponent } from 'react';
import withCustomAuthenticator from 'containers/Authenticator/withCustomAuthenticator';
import { withErrorBoundary } from 'components/ErrorBoundary';
import { toastr } from 'react-redux-toastr';
import { PROPERTY_ERROR_MESSAGE } from 'shared/Messages';
import Property from 'components/Property/Loadable';
import { PageWrapper } from 'components/PageWrapper';
import Grid from '@material-ui/core/Grid';
import { useQuery } from '@apollo/client';
import { GET_PROPERTIES } from 'graphql/queries/user/overview';
import BlockUi from 'react-block-ui';
import Loader from 'react-loaders';
import { makeStyles } from '@material-ui/core';
import clsx from 'clsx';
import UserContext from '../../contexts/UserContext';
import { APP_PATHS } from '../../shared/Consts';
import { useHistory } from 'react-router-dom';

const useStyles = makeStyles(theme => ({
  container: {
    minHeight: '56px',
  },
  loading: {
    border: '1px solid #E0E0E0',
    backgroundColor: theme.palette.common.white,
  },
}));

const PropertyList = (): Element<'div' | 'p'> => {
  const { loading, error, data } = useQuery(GET_PROPERTIES);
  const classes = useStyles();
  const history = useHistory();
  const { IsSeller: isSeller } = useContext(UserContext);
  if (!isSeller) history.push({ pathname: APP_PATHS.dashboard });

  const sellerProperties = _get(
    data,
    ['getUserOverview', 'Properties', 'Items'],
    [],
  );

  useEffect(() => {
    const { message } = error || {};
    if (message) {
      toastr.error(PROPERTY_ERROR_MESSAGE.fetchFailure, message);
    }
  }, [error]);
  const isPropertyEmpty =
    !(sellerProperties && sellerProperties.length > 0) && !loading;

  if (isPropertyEmpty) {
    return <p className="incorrect_parameter">Property not found</p>;
  }
  return (
    <PageWrapper>
      <Grid container justifyContent="center">
        <Grid item md={8} sm={8} xs={12}>
          <BlockUi
            tag="div"
            blocking={loading}
            loader={
              <Loader active type="ball-pulse" color="rgba(231,28,99,1)" />
            }
            className={clsx(classes.container, loading && classes.loading)}
          >
            {!loading &&
              sellerProperties.map((property, key) => (
                <Property key={key} property={property} />
              ))}
          </BlockUi>
        </Grid>
      </Grid>
    </PageWrapper>
  );
};

export default (withErrorBoundary(
  withCustomAuthenticator(PropertyList),
): AbstractComponent<Props>);
