// @flow
import React, { useContext, useEffect, Fragment } from 'react';
import type { Element } from 'react';
import UserContext from 'contexts/UserContext';
import withCustomAuthenticator from '../Authenticator/withCustomAuthenticator';
import { useQuery } from '@apollo/client';
import { GET_SELLER_OFFER, GET_BUYER_OFFER } from 'graphql/queries/offers';
import { toastr } from 'react-redux-toastr';
import { Loader } from 'react-loaders';
import { PageWrapper } from 'components/PageWrapper';
import BlockUi from 'react-block-ui';
import { OfferDetails } from 'components/Offers/OfferDetails';
import { useParams, useHistory } from 'react-router-dom';
import { APP_PATHS } from '../../shared/Consts';
import { getOffer } from 'components/Offers/OfferDetails/helpers';

const OfferDetailsPage = (): Element<typeof Fragment> => {
  const history = useHistory();
  const { offerId, propertyId } = useParams();
  const { IsSeller: isSeller, IsBuyer: isBuyer } = useContext(UserContext);

  const waitingRoles = isSeller === undefined && isBuyer === undefined;
  const hasPermission =
    (isSeller && propertyId && offerId) || (isBuyer && offerId && !propertyId);

  const { loading, data, errors, refetch } = useQuery(
    isSeller ? GET_SELLER_OFFER : GET_BUYER_OFFER,
    {
      variables: {
        propertyId,
        offerId,
      },
      errorPolicy: 'all',
      notifyOnNetworkStatusChange: true,
      fetchPolicy: 'network-only',
      skip: !hasPermission,
    },
  );

  useEffect(() => {
    const { message } = errors || {};
    if (message) {
      toastr.error('Something went wrong', message);
    }
  }, [errors]);

  if (waitingRoles) {
    return (
      <PageWrapper>
        <BlockUi
          tag="div"
          blocking={true}
          loader={<Loader active type="ball-pulse" color="rgba(231,28,99,1)" />}
        ></BlockUi>
      </PageWrapper>
    );
  }

  if (!waitingRoles && !hasPermission) {
    history.push({ pathname: APP_PATHS.dashboard });
    return <></>;
  }

  const offer = getOffer(data, isSeller);

  return (
    <OfferDetails
      offer={offer}
      loading={loading}
      isSeller={isSeller}
      refetch={refetch}
    />
  );
};

export default (withCustomAuthenticator(OfferDetailsPage): Element<
  typeof Fragment,
>);
