import React from 'react';
import PhoneInput from 'react-phone-number-input';
import * as Utils from './Utils';
import 'react-phone-number-input/style.css';
import './IntlTelInputer.css';

export default function IntlTelInputer(props) {
  const {
    FieldName,
    Required,
    FieldDef,
    ValueChangeHandler,
    ValidationResults,
    InputValues,
    Periphery,
  } = props;

  let value = '';
  if (InputValues !== undefined && InputValues[FieldName] !== undefined) {
    value = InputValues[FieldName];
  }

  return (
    <>
      {Periphery.Title}
      {Periphery.SubTitle}
      {Periphery.TitleComponent}
      <PhoneInput
        className={Utils.GetClassName(ValidationResults, FieldName, Required)}
        placeholder="Enter phone number"
        defaultCountry="US"
        value={value}
        onChange={ValueChangeHandler.bind(
          this,
          new CustomEvent('tel_onchange'),
          FieldName,
          FieldDef.Type,
        )}
        disabled={props.ReadOnly === true}
      />
      {Periphery.Note}
      {Periphery.SubNote}
      {Periphery.NoteComponent}
      {Utils.GetValidationResult(ValidationResults, FieldName)}
    </>
  );
}
