import React, { useContext } from 'react';
import UserContext from 'contexts/UserContext';
import SellerOfferList from 'containers/SellerOffer/NewOfferList';
import { useHistory } from 'react-router-dom';
import { APP_PATHS } from '../../shared/Consts';

export const OfferPage = props => {
  const history = useHistory();
  const { IsSeller: isSeller } = useContext(UserContext);
  if (!isSeller) history.push({ pathname: APP_PATHS.dashboard });

  return <SellerOfferList />;
};
