import React from 'react';
import Welcome from '../../components/SellerProperty/Listing/Welcome';
import Vesting from '../../components/SellerProperty/Listing/Vesting';
import ListingPrice from '../../components/SellerProperty/Listing/ListingPrice';
import ListingVerification from '../../components/SellerProperty/Listing/ListingVerification';
import ListingSubmission from '../../components/SellerProperty/Listing/ListingSubmission';
import Navigator from '../../components/SellerProperty/Listing/Navigator';
import * as Consts from '../../shared/Consts';
import * as SellPropertyUtils from './SellPropertyUtils';

import './Listing.css';

export default function Listing({ GlobalProps }) {
  var readOnly;
  var vestingSuspended = false;
  if (GlobalProps.PropertyStatus !== undefined) {
    const statusValue = GlobalProps.PropertyStatus;
    vestingSuspended = SellPropertyUtils.GetStatus(
      Consts.STATUS_VESTING_SUSPENDED,
      statusValue,
    );
    const listingSubmitted = SellPropertyUtils.GetStatus(
      Consts.STATUS_LISTING_SUBMITTED,
      statusValue,
    );
    readOnly = vestingSuspended || listingSubmitted || GlobalProps.ReadOnly;
  }

  const stepProps = {
    ...GlobalProps,
    ReadOnly: readOnly,
    VestingSuspended: vestingSuspended,
  };
  return (
    <>
      <Navigator CurrentTab={GlobalProps.SelectedTab} />
      <Welcome {...stepProps} />
      <Vesting {...stepProps} />
      <ListingPrice {...stepProps} />
      <ListingVerification {...stepProps} />
      <ListingSubmission {...stepProps} />
    </>
  );
}
