// @flow
import React, { Fragment, memo } from 'react';
import type { Element, AbstractComponent } from 'react';
import _isEmpty from 'lodash/isEmpty';
import { makeStyles } from '@material-ui/core/styles';
import type { Offer as TOffer } from 'types/Offer';
import {
  TableContainer,
  Table,
  TableBody,
  TableRow,
  TableCell,
} from '@material-ui/core';
import { hideIfInvalidProps } from 'utils/RenderComponent';
import { getHistoryRows } from './helpers';

type Props = {
  offer: TOffer,
};

const checkProps = ({ offer }) => !_isEmpty(offer);

const useStyles = makeStyles(theme => ({
  table: {
    fontFamily: theme.typography.fontFamily.avenirLT,
    backgroundColor: theme.palette.common.white,
    minWidth: 750,
    tableLayout: 'fixed',

    '& tr:first-child': {
      textTransform: 'capitalize',
      '& td': {
        fontWeight: 'bold',
      },
    },
  },
  cell: {
    whiteSpace: 'break-spaces',
    border: `1px solid ${theme.palette.grey[300]}`,
  },
  row: {
    '& td:first-child': {
      textTransform: 'capitalize',
      fontWeight: 'bold',
      backgroundColor: theme.palette.grey[100],
    },
    '& td:nth-child(2)': {
      backgroundColor: theme.palette.grey[100],
    },
  },
}));

const History = ({ offer }: Props): Element<'div' | typeof Fragment> => {
  const classes = useStyles();
  if (_isEmpty(offer)) return <></>;

  const rows = getHistoryRows(offer);

  return (
    <TableContainer>
      <Table className={classes.table}>
        <TableBody>
          {rows.map((row, rowIndex) => {
            const rowKey = `history-row-${rowIndex}`;
            return (
              <TableRow className={classes.row} key={rowKey}>
                {row.map((cell, cellIndex) => {
                  const cellKey = `${rowKey}-cell-${cellIndex}`;
                  return (
                    <TableCell className={classes.cell} key={cellKey}>
                      {cell}
                    </TableCell>
                  );
                })}
              </TableRow>
            );
          })}
        </TableBody>
      </Table>
    </TableContainer>
  );
};

export default (hideIfInvalidProps(checkProps)(
  memo(History),
): AbstractComponent<Props>);
