// @flow
import React from 'react';
import qs from 'query-string';
import type { Element, AbstractComponent } from 'react';
import withCustomAuthenticator from 'containers/Authenticator/withCustomAuthenticator';
import loadable from 'react-loadable';
import LoadingComponent from 'components/Loader';
import _get from 'lodash/get';
import { withErrorBoundary } from 'components/ErrorBoundary';
import _isEmpty from 'lodash/isEmpty';
import { useHistory } from 'react-router-dom';

type Props = {
  location: Object,
};

const OfferDashboard = (props: Props): Element<Object> => {
  const history = useHistory();
  const queryString = qs.parse(_get(props, ['location', 'search'], ''), {
    ignoreQueryPrefix: true,
  });
  if (_isEmpty(queryString)) history.push({ pathname: '/' });

  const offerId = _get(queryString, 'offerId', '');
  const step = _get(queryString, 'step', '');
  const formId = _get(queryString, 'formId', '');
  const OfferContent = loadable({
    loader: () => import(`components/Offers/Step/Step${step}`),
    loading: LoadingComponent,
  });
  return (
    <div className="container">
      <OfferContent offerId={offerId} formId={formId} />
    </div>
  );
};

export default (withErrorBoundary(
  withCustomAuthenticator(OfferDashboard),
): AbstractComponent<Props>);
