import React from 'react';
import FooterMiddleStep from './FooterMiddleStep';
import FooterMiddleStepReadOnly from './FooterMiddleStepReadOnly';
import * as InputValuesHelper from '../../../shared/InputValuesHelper';
import * as Consts from '../../../shared/Consts';

export default class VestingSellerProfileActions extends React.Component {
  render() {
    const { InputValues, NeedCompleteBy, NeedVerification } = this.props;

    if (
      InputValuesHelper.GetInputValue(
        InputValues,
        'Vesting_Property_Data_Correct',
      ) === Consts.OPTION_NO_VALUE
    ) {
      return null;
    }

    if (
      NeedCompleteBy === true &&
      (InputValues.Vesting_Form_Completed_By === undefined ||
        InputValues.Vesting_Form_Completed_By === '_not_listed_')
    ) {
      return null;
    }

    const verifiedLLC = InputValuesHelper.GetInputValue(
      InputValues,
      'Vesting_Verified_LLC',
    );
    const verifiedCorporation = InputValuesHelper.GetInputValue(
      InputValues,
      'Vesting_Verified_Corporation',
    );
    const verifiedPartnership = InputValuesHelper.GetInputValue(
      InputValues,
      'Vesting_Verified_Partnership',
    );
    const verifiedTrustee = InputValuesHelper.GetInputValue(
      InputValues,
      'Vesting_Verified_Trustee',
    );

    if (
      NeedVerification === true &&
      (verifiedLLC === undefined || verifiedLLC === Consts.OPTION_NO_VALUE) &&
      (verifiedCorporation === undefined ||
        verifiedCorporation === Consts.OPTION_NO_VALUE) &&
      (verifiedPartnership === undefined ||
        verifiedPartnership === Consts.OPTION_NO_VALUE) &&
      (verifiedTrustee === undefined ||
        verifiedTrustee === Consts.OPTION_NO_VALUE)
    ) {
      return null;
    }

    return (
      <>
        {this.props.ReadOnly !== true && (
          <FooterMiddleStep
            PreviousStepHandler={() => {
              this.props.SwitchTabHandler(this.props.SelectedTab - 1);
            }}
            SaveHandler={async () => {
              if (
                (await this.props.SaveDataHandler(DataSections, true)) === true
              ) {
                await this.props.GotoDashboardHandler();
              }
            }}
            SaveNextHandler={async () => {
              if (
                (await this.props.SaveDataHandler(DataSections, false)) === true
              ) {
                this.props.SwitchTabHandler(this.props.SelectedTab + 1);
              }
            }}
          />
        )}
        {this.props.ReadOnly === true && (
          <FooterMiddleStepReadOnly {...this.props} />
        )}
      </>
    );
  }
}

const DataSections = [
  Consts.DATA_SECTION_PROPERTY_VESTING,
  Consts.DATA_SECTION_PROPERTY_LMH,
];
