import { useMutation } from '@apollo/client';
import { SELLER_UPDATE_STEP } from 'graphql/mutations';

export const useSellerUpdateStep = () => {
  const [
    doSellerUpdateStep,
    { loading: isSellerUpdatingStep, data: sellerUpdatedStep },
  ] = useMutation(SELLER_UPDATE_STEP);

  return {
    doSellerUpdateStep,
    isSellerUpdatingStep,
    sellerUpdatedStep,
  };
};
