import React from 'react';
import * as Utils from './Utils';
import * as InputValuesHelper from '../../shared/InputValuesHelper';
import Select from 'react-select';
import './SelectNormal.css';
import './SelectValidationFailed.css';
import './SelectNormalRowMode.css';
import './SelectValidationFailedRowMode.css';

export default class CustomSelect extends React.Component {
  constructor(props) {
    super(props);
    this.handleSelectChange = this.handleSelectChange.bind(this);
  }

  handleSelectChange(selectedItem) {
    const selectedValue =
      selectedItem !== undefined && selectedItem !== null
        ? selectedItem.value
        : '';
    this.props.ValueChangeHandler(
      this,
      this.props.FieldName,
      this.props.FieldType,
      selectedValue,
    );
  }

  render() {
    const { FieldName, InputValues, ValidationResults, Periphery, RowNumber } =
      this.props;

    var classNamePrefix = 'react-select';
    if (RowNumber !== undefined) {
      classNamePrefix = 'react-select-row-mode';
    }
    const validationError = Utils.GetValidationResult(
      ValidationResults,
      FieldName,
    );
    if (validationError !== undefined) {
      classNamePrefix = 'react-select-failed';
      if (RowNumber !== undefined) {
        classNamePrefix = 'react-select-failed-row-mode';
      }
    }

    const newOptions = InputValuesHelper.GenerateSelectOptions(this.props);
    var selectOption;
    // var value;
    if (InputValues !== undefined && InputValues[FieldName] !== undefined) {
      if (newOptions !== undefined) {
        const option = newOptions.find(
          option => option.value === InputValues[FieldName],
        );
        if (
          option !== undefined &&
          (option.isDisabled === undefined ||
            (option.isDisabled !== undefined && option.isDisabled !== true))
        ) {
          selectOption = option;
        } else {
          InputValues[FieldName] = undefined;
        }
      }
    }

    var searchable = false;
    if (newOptions !== undefined && newOptions.length > 10) {
      searchable = true;
    }

    return (
      <>
        {Periphery.Title}
        {Periphery.SubTitle}
        {Periphery.TitleComponent}
        <Select
          options={newOptions}
          onChange={this.handleSelectChange}
          value={selectOption}
          isSearchable={searchable}
          // isClearable={true}
          classNamePrefix={classNamePrefix}
          isDisabled={this.props.ReadOnly === true}
        />
        {Periphery.Note}
        {Periphery.SubNote}
        {Periphery.NoteComponent}
        {Utils.GetValidationResult(ValidationResults, FieldName)}
      </>
    );
  }
}
