// @flow
import React, { memo } from 'react';
import type { Element, AbstractComponent } from 'react';
import _get from 'lodash.get';
import _isEmpty from 'lodash/isEmpty';
import {
  MenuItem,
  Select,
  Grid,
  makeStyles,
  FormControl,
  InputLabel,
} from '@material-ui/core';
import { hideIfInvalidProps } from 'utils/RenderComponent';
import { NONE_FILTER } from '../helpers';

type Props = {
  scopes: Object,
  selectedScope: String,
  setSelectedScope: Function,
  isSeller: Boolean,
};

const useStyles = makeStyles(theme => ({
  select: {
    fontFamily: theme.typography.fontFamily.avenirLT,
    fontSize: '0.75rem',
    fontWeight: 'bold',
    borderRadius: 'unset',
    height: '100%',
  },
  menuItem: {
    fontFamily: theme.typography.fontFamily.avenirLT,
    fontSize: '0.75rem',
  },
  formControl: {
    height: '100%',
  },
}));

const checkProps = ({ scopes }) => scopes && !_isEmpty(scopes);

const ScopeSelector = ({
  scopes,
  selectedScope,
  setSelectedScope,
  isSeller,
}: Props): Element<'div'> => {
  const classes = useStyles();

  const scopesKeys = Object.keys(scopes) || [];
  const noneFilterLabel = `Please select ${
    isSeller ? 'a property' : 'an offer'
  }`;
  const scopeLabel = isSeller ? 'Property' : 'Offer';

  return (
    <Grid item xs={12} lg={3}>
      <FormControl
        variant="outlined"
        size="small"
        fullWidth
        className={classes.formControl}
      >
        <InputLabel id="document-scope-selector-label">{scopeLabel}</InputLabel>
        <Select
          value={selectedScope}
          onChange={event => {
            setSelectedScope(event.target.value);
          }}
          variant="outlined"
          className={classes.select}
          disabled={scopesKeys.length === 1}
          fullWidth
          label={scopeLabel}
          labelId="document-scope-selector-label"
        >
          <MenuItem
            value={NONE_FILTER}
            key="none-scope"
            className={classes.menuItem}
          >
            {noneFilterLabel}
          </MenuItem>
          {scopesKeys.map(scope => (
            <MenuItem value={scope} key={scope} className={classes.menuItem}>
              {_get(scopes, [scope, 'name'], '')}
            </MenuItem>
          ))}
        </Select>
      </FormControl>
    </Grid>
  );
};

export default (hideIfInvalidProps(checkProps)(
  memo(ScopeSelector),
): AbstractComponent<Props>);
