import React from 'react';
import * as InputHelper from '../../Shared/InputHelper';
import * as Consts from '../../../shared/Consts';
import FormSectionTitle from '../../Shared/FormSectionTitle';
import InputerForm from '../../Shared/InputerForm';

export default class LeadPaint extends React.Component {
  render() {
    if (
      this.props.InputValues.BYL_315_Active === undefined ||
      this.props.InputValues.BYL_315_Active === Consts.OPTION_NO_VALUE
    ) {
      return null;
    }

    return (
      <>
        <FormSectionTitle Title="Lead Paint (PRE 1978)" />
        <div className="form_inputs">
          <div className="t_texts">
            {InputHelper.GetFieldTitle({
              ...this.props,
              FieldName: 'BYL_140_n',
            })}
          </div>
        </div>

        <div className="form_inputs">
          <InputerForm {...this.props} FieldName="BYL_925_Lead_Based_Paint" />
          <InputerForm
            {...this.props}
            FieldName="BYL_925_Lead_Based_Paint_Explanation"
          />
        </div>
        <div className="form_inputs">
          <InputerForm {...this.props} FieldName="BYL_673_Lead_Based_Paint" />
          <InputerForm
            {...this.props}
            FieldName="BYL_673_Lead_Based_Paint_Explanation"
          />
          <InputerForm
            {...this.props}
            FieldName="BYL_673_Lead_Based_Paint_Reports"
          />
        </div>
      </>
    );
  }
}
