import { gql } from '@apollo/client';

const BUYER_UPDATE_STEP = gql`
  mutation buyerUpdateStep($NewStepId: Int!, $OfferId: String!) {
    offers_BuyerUpdateStep(NewStepId: $NewStepId, OfferId: $OfferId)
  }
`;

const SELLER_UPDATE_STEP = gql`
  mutation sellerUpdateStep($NewStepId: Int!, $OfferId: String!) {
    offers_SellerUpdateStep(NewStepId: $NewStepId, OfferId: $OfferId)
  }
`;

export { BUYER_UPDATE_STEP, SELLER_UPDATE_STEP };
