// @flow
import _isEmpty from 'lodash/isEmpty';
import _get from 'lodash/get';

export const NONE_FILTER = 'none';

export type DocumentItemProps = {
  documents: Array<Object>,
  propertyId: number,
  offerId: number,
  address: string,
};

export const getDocumentsData = ({
  data,
  isSeller = false,
}: {
  data: Object,
  isSeller: boolean,
}): Object => {
  if (_isEmpty(data)) return {};

  const itemsData = _get(
    data,
    `getUserOverview.${isSeller ? 'Properties' : 'Offers'}.Items`,
    [],
  );

  return prepareDocumentsData({ rawDocumentData: itemsData, isSeller });
};

export const prepareDocumentsData = ({
  rawDocumentData,
  isSeller,
  filterOutEmpty = false,
}: {
  rawDocumentData: Array<Object>,
  isSeller: boolean,
  filterOutEmpty?: boolean,
}): Object =>
  rawDocumentData.reduce((result, item) => {
    const scopeDocuments = _get(item, 'Docs', []);
    if (filterOutEmpty && _isEmpty(scopeDocuments)) return result;

    const scopeItemKey = _get(item, isSeller ? 'PropertyId' : 'OfferId', '');
    const scopeItemName = _get(
      item,
      isSeller ? 'PropertyData.FullAddress' : 'OfferNo',
      '',
    );
    return { ...result, [scopeItemKey]: { ...item, name: scopeItemName } };
  }, {});

export const headerCells = [
  {
    id: 'File.name',
    numeric: false,
    disablePadding: false,
    label: 'Document Name',
  },
  {
    id: 'DocTypeName',
    numeric: false,
    disablePadding: false,
    label: 'Type',
  },
  {
    id: 'EventTime',
    numeric: false,
    disablePadding: false,
    label: 'Created Date',
  },
];

const isInvalidSearchFilter = (values: Object) =>
  Object.keys(values).every(key => _isEmpty(_get(values, key)));

const hasText = (doc: Object, text: string) => {
  if (!text) return true;

  const docName = _get(doc, 'File.name', '');
  return docName.toLowerCase().includes(text.toLowerCase());
};

const hasTags = ({
  doc,
  filterTags,
  key,
}: {
  doc: Object,
  filterTags: Object[],
  key: string,
}) => {
  if (_isEmpty(filterTags)) return true;

  const filterValues = filterTags.map(filter => filter.id);
  const docTagValue = _get(doc, [key], '');
  return filterValues.includes(docTagValue);
};

export const searchFilterDocuments = (
  docs: Object[],
  values: Object,
): Object[] => {
  if (isInvalidSearchFilter(values)) return docs;

  const { search, docTypes } = values;

  return docs.filter(
    doc =>
      hasText(doc, search) &&
      hasTags({ doc, key: 'DocType', filterTags: docTypes }),
  );
};
