// @flow
import { useState } from 'react';
import { useLazyQuery } from '@apollo/client';
import _get from 'lodash.get';
import _isEmpty from 'lodash/isEmpty';
import { GET_DOC_DOWNLOAD_URL } from 'graphql/queries/documents';
import JSZip from 'jszip';
import { saveAs } from 'file-saver';

const ZIP_FILE_NAME = 'Nile property documents';
const SUCCESS_RESPONSE_STATUS = 200;

export const useDownloadDocs = (): Object => {
  const [error, setError] = useState({});

  const downloadZip = (files: Object[]) => {
    const zip = new JSZip();
    const folder = zip.folder(ZIP_FILE_NAME);

    Promise.all(
      files.map(file =>
        fetch(file.url).then(response => {
          if (response.status === SUCCESS_RESPONSE_STATUS) {
            if (folder)
              folder.file(_get(file, 'name', 'untitle'), response.blob());
            return;
          }
          throw new Error(response.statusText);
        }),
      ),
    )
      .then(() => {
        zip
          .generateAsync({ type: 'blob' })
          .then(blob => saveAs(blob, ZIP_FILE_NAME))
          .catch(error => setError(error));
      })
      .catch(error => setError(error));
  };

  const [downloadDocs, { loading: isDownloading }] = useLazyQuery(
    GET_DOC_DOWNLOAD_URL,
    {
      onCompleted: async data => {
        const files = _get(data, 'docs_GetDownloadUrls', []);

        if (_isEmpty(files)) {
          setError(new Error('Not found'));
          return;
        }

        if (files.length === 1) {
          // eslint-disable-next-line no-unused-expressions
          window.open(_get(files, [0, 'url'], ''), '_blank')?.focus();
          return;
        }

        downloadZip(files);
      },
      onError: error => setError(error),
    },
  );

  const doDownloadDocs = (objectKeys: Array<string>) => {
    downloadDocs({
      variables: {
        ObjectKeys: objectKeys,
      },
    });
  };

  return { doDownloadDocs, isDownloading, error };
};
