// @flow
import { makeStyles } from '@material-ui/core';
import React, { useContext, Fragment, Element } from 'react';
import headerStyle from './headerStyle';
import { ListItemIcon, ListItemText } from '@material-ui/core';
import { NavLink } from 'react-router-dom';
import clsx from 'clsx';
import UserContext from 'contexts/UserContext';

type Props = {
  label: string,
  icon: Element<'svg'>,
  path: string,
  showIf: Function,
};

const useStyles = makeStyles(headerStyle);

export const LinkItem = (props: Props): Element<'a' | typeof Fragment> => {
  const { label, icon, path, showIf } = props;
  const role = useContext(UserContext);
  const classes = useStyles();
  if (!showIf(role)) return <></>;

  return (
    <NavLink
      to={path}
      className={clsx(classes.menuItem, classes.menuItemLink)}
      activeClassName="active"
      exact
    >
      <ListItemIcon className={classes.menuIcon}>{icon}</ListItemIcon>
      <ListItemText
        primary={
          <span
            className={clsx(classes.menuItemLinkText, classes.menuItemText)}
          >
            {label}
          </span>
        }
      />
    </NavLink>
  );
};
