// @flow
import * as React from 'react';
import { AppBar, makeStyles, Container, Slide, Grid } from '@material-ui/core';
import style from './headerStyle';
import { AUTH_STATUS_SIGNED_IN, APP_PATHS } from 'shared/Consts';
import { AuthHeader } from './AuthHeader';
import { useScrollDirection } from '../../hooks';
import imageLogo from '../../images/logo_n.svg';
import imageLogoS from '../../images/logo_s.svg';
import { Link } from 'react-router-dom';

const useStyles = makeStyles(style);

type Props = {
  authState: string,
};

const Header = (props: Props): React.Element<typeof Container> => {
  const classes = useStyles();
  const { authState } = props;

  const { scrollDown } = useScrollDirection();

  const isLoggedIn = authState === AUTH_STATUS_SIGNED_IN;

  return (
    <Slide direction="down" in={scrollDown} timeout={200}>
      <div className={classes.root}>
        {isLoggedIn ? (
          <AuthHeader />
        ) : (
          <AppBar position="static" color="default" className={classes.header}>
            <Container className={classes.container} maxWidth="lg">
              <Grid container alignItems="center">
                <Grid item md={1} sm={1} xs={1} className={classes.menuGrid}>
                  <Link className={classes.logo} to={APP_PATHS.dashboard}>
                    {process.env.REACT_APP_ENV_NAME ? (
                      <div style={{ textAlign: 'center' }}>
                        <img src={imageLogoS} width="70px" alt="LogoW" />
                        <div className="sub-title-alert">
                          {process.env.REACT_APP_ENV_NAME}
                        </div>
                      </div>
                    ) : (
                      <img src={imageLogo} width="70px" alt="LogoW" />
                    )}
                  </Link>
                </Grid>
              </Grid>
            </Container>
          </AppBar>
        )}
      </div>
    </Slide>
  );
};

export default React.memo(Header);
