import React from 'react';
import * as Consts from '../../../shared/Consts';
import FormSectionTitle from '../../Shared/FormSectionTitle';
import InputerForm from '../../Shared/InputerForm';

export default class ImpactGlazing extends React.Component {
  render() {
    if (
      this.props.InputValues.BYG_586_Active === undefined ||
      this.props.InputValues.BYG_586_Active === Consts.OPTION_NO_VALUE
    ) {
      return null;
    }

    return (
      <>
        <FormSectionTitle Title="Impact Glazing (PRE 1984)" />
        <div className="form_inputs">
          <InputerForm {...this.props} FieldName="BYG_098_Impact_Glazing" />
          <InputerForm
            {...this.props}
            FieldName="BYG_098_Impact_Glazing_Explanation"
          />
        </div>
      </>
    );
  }
}
