import * as Consts from '../../shared/Consts';
import axios from 'axios';

export const GetStatus = (status, statusValues) => {
  if (statusValues[status] !== undefined && statusValues[status] === true) {
    return true;
  } else {
    return false;
  }
};

export const UploadFiles = async (
  uploadUrls,
  sections,
  fieldsDefs,
  postData,
) => {
  const putFileErrors = [];
  const files = [];
  for (const section of sections) {
    const fileFields = fieldsDefs
      .filter(item => item.DataSection === section)
      .filter(item => item.Type === Consts.FIELD_TYPE_MULTIPLE_FILES);
    if (fileFields !== undefined) {
      for (const fileField of fileFields) {
        if (
          postData[section] !== undefined &&
          postData[section][fileField.DataField] !== undefined
        ) {
          for (const file of postData[section][fileField.DataField]) {
            files.push(file);
          }
        }
      }
    }
  }

  if (files.length > 0) {
    // upload files
    for (let i = 0; i < uploadUrls.length; i++) {
      const urlItem = uploadUrls[i];
      const file = files.find(item => item.id === urlItem.id);
      if (file !== undefined) {
        var options = {
          headers: {
            'Content-Type': file.type,
          },
        };
        try {
          const fileToUpload = file.file;
          await axios.put(urlItem.url, fileToUpload, options);
        } catch (error) {
          putFileErrors.push(file.name);
          this.props.NotifyError(
            'Save systems features',
            `Failed to upload file ${file.name}`,
          );
        }
      }
    }
  }
};
