import React from 'react';
import InputerForm from '../../Shared/InputerForm';
import FormSectionTitle from '../../Shared/FormSectionTitle';
import * as Consts from '../../../shared/Consts';

export default class HOAQuestions extends React.Component {
  render() {
    const { InputValues } = this.props;
    if (
      InputValues === undefined ||
      (InputValues !== undefined &&
        InputValues.HAN_HOA_Has_Info !== Consts.OPTION_YES_VALUE)
    ) {
      return null;
    }

    return (
      <>
        <FormSectionTitle Title="Final Questions" />
        <div className="form_inputs">
          <InputerForm {...this.props} FieldName="HAQ_592_Has_Improvement" />
          <InputerForm
            {...this.props}
            FieldName="HAQ_592_Improvements_Explanation"
          />
        </div>
        <div className="form_inputs">
          <InputerForm {...this.props} FieldName="HAQ_141_Has_Issue" />
          <InputerForm {...this.props} FieldName="HAQ_141_Issues_Explanation" />
        </div>
        {/* <div className='form_inputs'>
          <InputerForm {...this.props} FieldName='HAQ_044_Has_Other_HOA' />
          <InputerForm {...this.props} FieldName='HAQ_044_Other_HOA_Explanation' />
        </div> */}
      </>
    );
  }
}
