import React from 'react';
import * as InputHelper from './InputHelper';
import * as Consts from '../../shared/Consts';

export default class FormYesNoExplanation extends React.Component {
  render() {
    const { FieldProps } = this.props;

    var explanationFieldName;
    if (FieldProps.ExpFieldName === undefined) {
      explanationFieldName = `${FieldProps.FieldName}_explanation`;
    } else {
      explanationFieldName = FieldProps.ExpFieldName;
    }

    var explanationFieldExists = false;
    var displayExplanation = false;
    const explanationField = FieldProps.FieldDefs.find(
      field => field.FieldName === explanationFieldName,
    );
    if (explanationField !== undefined) {
      explanationFieldExists = true;
      if (
        FieldProps.InputValues[FieldProps.FieldName] !== undefined &&
        FieldProps.InputValues[FieldProps.FieldName] === Consts.OPTION_YES_VALUE
      ) {
        displayExplanation = true;
      }
    }

    return (
      <div>
        <div className="inputer_form">
          {InputHelper.GenerateInput(FieldProps)}
        </div>
        {explanationFieldExists === true && displayExplanation === true && (
          <div className="inputer_form">
            {InputHelper.GenerateInput({
              ...FieldProps,
              FieldName: explanationFieldName,
            })}
          </div>
        )}
        {FieldProps.SubTitle !== undefined && (
          <div className="inputer_form greyer" style={{ marginLeft: '0' }}>
            <span>{FieldProps.SubTitle}</span>
          </div>
        )}
      </div>
    );
  }
}
