import React from 'react';
import uuidv4 from 'uuid/v4';
import * as Utils from './Utils';
import * as InputValuesHelper from '../../shared/InputValuesHelper';

export default function RadioGroup(props) {
  const {
    FieldName,
    FieldDefs,
    OneRow1Option,
    ValueChangeHandler,
    ValidationResults,
    InputValues,
    Periphery,
  } = props;

  let value = '';
  if (InputValues !== undefined && InputValues[FieldName] !== undefined) {
    value = InputValues[FieldName];
  }

  const field = FieldDefs.find(field => field.FieldName === FieldName);

  const optionsToDisplay = InputValuesHelper.GenerateSelectOptions(
    props,
  ).filter(option => option.isDisabled !== true);

  return (
    <>
      {Periphery.Title}
      {Periphery.SubTitle}
      {Periphery.TitleComponent}
      {OneRow1Option === true &&
        optionsToDisplay.map((item, index) => (
          <div
            key={uuidv4()}
            style={{ paddingTop: '10px', paddingBottom: '10px' }}
          >
            <input
              type="radio"
              id={`${FieldName}_${index}`}
              name={FieldName}
              value={item.value}
              onChange={() => {
                ValueChangeHandler(this, FieldName, field.Type, item.value);
              }}
              checked={value === item.value ? 'checked' : ''}
              disabled={props.ReadOnly === true ? 'disabled' : ''}
            />
            <label htmlFor={`${FieldName}_${index}`}>{item.label}</label>
          </div>
        ))}

      <div
        style={{ display: 'flex', flexWrap: 'wrap', justifyContent: 'start' }}
      >
        {OneRow1Option !== true &&
          optionsToDisplay.map((item, index) => (
            <div
              key={uuidv4()}
              style={{
                paddingTop: '10px',
                paddingBottom: '10px',
                flexBasis: '240px',
              }}
            >
              <input
                type="radio"
                id={`${FieldName}_${index}`}
                name={FieldName}
                value={item.value}
                onChange={() => {
                  ValueChangeHandler(this, FieldName, field.Type, item.value);
                }}
                checked={value === item.value ? 'checked' : ''}
                disabled={props.ReadOnly === true ? 'disabled' : ''}
              />
              <label htmlFor={`${FieldName}_${index}`}>{item.label}</label>
            </div>
          ))}
      </div>
      {Periphery.Note}
      {Periphery.SubNote}
      {Periphery.NoteComponent}
      {Utils.GetValidationResult(ValidationResults, FieldName)}
    </>
  );
}
