import { useMutation } from '@apollo/client';
import { BUYER_UPDATE_STEP } from 'graphql/mutations';

export const useBuyerUpdateStep = () => {
  const [
    doBuyerUpdateStep,
    { loading: isBuyerUpdatingStep, data: buyerUpdatedStep },
  ] = useMutation(BUYER_UPDATE_STEP);

  return {
    doBuyerUpdateStep,
    isBuyerUpdatingStep,
    buyerUpdatedStep,
  };
};
