import React from 'react';
import * as Utils from './Utils';

export default function MultipleChoises(props) {
  const {
    FieldName,
    Options,
    ValueChangeHandler,
    ValidationResults,
    InputValues,
    Periphery,
  } = props;

  let selectedItems;
  if (InputValues !== undefined && InputValues[FieldName] !== undefined) {
    selectedItems = InputValues[FieldName];
  }

  let itemStyle = { flex: '0 0 240px' };
  if (Options !== undefined && Options.length >= 4) {
    itemStyle = { flex: '0 0 240px' };
  }

  var maxOptionLength = 0;
  Options.forEach(element => {
    if (element.value.length > maxOptionLength) {
      maxOptionLength = element.value.length;
    }
  });
  if (maxOptionLength > 30) {
    itemStyle = { flex: '0 0 480px' };
  }

  return (
    <>
      {Periphery.Title}
      {Periphery.SubTitle}
      {Periphery.TitleComponent}
      <div className="form-multiple-choise-wrapper">
        <div className="form-multiple-choise-wrapper__items">
          {Options.map(item => {
            var checked = false;
            if (selectedItems !== undefined && Array.isArray(selectedItems)) {
              if (
                selectedItems.find(
                  selectedItem => selectedItem.value === item.value,
                ) !== undefined
              ) {
                checked = true;
              }
            }
            return (
              <span
                key={item.value}
                className="form-multiple-choise-wrapper__item"
                style={itemStyle}
              >
                <input
                  type="checkbox"
                  id={`${FieldName}_${item.value}`}
                  name={FieldName}
                  // value={'checked'}
                  data-type="multiple-choises"
                  data-label={item.label}
                  data-value={item.value}
                  onChange={ValueChangeHandler}
                  checked={checked}
                  disabled={props.ReadOnly === true ? 'disabled' : ''}
                />
                <label htmlFor={`${FieldName}_${item.value}`}>
                  {item.label}
                </label>
              </span>
            );
          })}
        </div>
        {Periphery.Note}
        {Periphery.SubNote}
        {Periphery.NoteComponent}
        {Utils.GetValidationResult(ValidationResults, FieldName)}
      </div>
    </>
  );
}
