import React from 'react';
import * as FieldDef from '../../../shared/FieldDefinition/PropertyListing';
import * as InputHelper from '../../Shared/InputHelper';
import * as Consts from '../../../shared/Consts';

export default class VestingCompletedBy extends React.Component {
  render() {
    if (this.props.NeedCompleteBy !== true) {
      return null;
    }

    if (
      this.props.InputValues.Vesting_Property_Data_Correct !==
      Consts.OPTION_YES_VALUE
    ) {
      return null;
    }

    const { InputValues, PropertyData } = this.props;

    const FieldProps = {
      ...this.props,
      FieldDefs: FieldDef.FieldDefinitions,
    };

    var vestingCompletedBy = [];
    if (PropertyData !== undefined) {
      const ownerNames = PropertyData.OwnerNames.split('/');
      ownerNames.forEach(element => {
        vestingCompletedBy.push({ label: element, value: element });
      });
      vestingCompletedBy.push({
        label: 'I am not listed.',
        value: '_not_listed_',
      });
    }

    return (
      <>
        <div className="form_inputs">
          <div className="inputer_form halfs">
            {InputHelper.GenerateInput({
              ...FieldProps,
              FieldName: 'Vesting_Form_Completed_By',
              AdditionalOptions: vestingCompletedBy,
              OneRow1Option: true,
            })}
          </div>
        </div>

        {this.props.VestingSuspended === true &&
          InputValues.Vesting_Form_Completed_By === '_not_listed_' && (
            <div className="b_texts" style={{ color: '#000' }}>
              <div>
                Based on the data, we will need to have an Advisor reach out to
                go over the listing.
              </div>
            </div>
          )}
      </>
    );
  }
}
