import React from 'react';
import {
  TableCell,
  TableHead,
  TableSortLabel,
  TableRow,
  Typography,
  Checkbox,
  FormControlLabel,
} from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles(theme => ({
  cell: {
    paddingTop: '8px',
    paddingBottom: '8px',
  },
  label: {
    ...theme.typography.h5,
    color: theme.palette.text.secondaryDark,
    fontWeight: 600,
  },
  centerSortLabel: {
    width: '100%',
    justifyContent: 'center',
  },
  leftSortLabel: {
    width: '100%',
    justifyContent: 'left',
  },
  icon: {
    position: 'absolute',
    right: '0',
    top: '-2px',
  },
  checkBoxLabel: {
    margin: 0,
  },
  checkbox: {
    padding: 0,
  },
}));

export const Header = props => {
  const classes = useStyles();
  const {
    cells,
    order,
    orderBy,
    onRequestSort,
    align = 'center',
    onSelectAllClick,
    numSelected,
    rowCount,
  } = props;
  const createSortHandler = property => event => {
    onRequestSort(event, property);
  };

  return (
    <TableHead>
      <TableRow>
        {onSelectAllClick && (
          <TableCell padding="checkbox">
            <FormControlLabel
              className={classes.checkBoxLabel}
              control={
                <Checkbox
                  color="primary"
                  indeterminate={numSelected > 0 && numSelected < rowCount}
                  checked={rowCount > 0 && numSelected === rowCount}
                  onChange={onSelectAllClick}
                  className={classes.checkbox}
                />
              }
            />
          </TableCell>
        )}
        {cells.map(headCell => (
          <TableCell
            key={headCell.id}
            align={align}
            padding={headCell.disablePadding ? 'none' : 'normal'}
            sortDirection={orderBy === headCell.id ? order : undefined}
            className={classes.cell}
          >
            <TableSortLabel
              align={align}
              active={orderBy === headCell.id}
              direction={orderBy === headCell.id ? order : 'asc'}
              onClick={createSortHandler(headCell.id)}
              classes={{
                root:
                  align === 'center'
                    ? classes.centerSortLabel
                    : classes.leftSortLabel,
                icon: classes.icon,
              }}
            >
              <Typography
                variant="h5"
                className={classes.label}
                align={align}
                style={{ whiteSpace: 'pre-line' }}
              >
                {headCell.label}
              </Typography>
            </TableSortLabel>
          </TableCell>
        ))}
        <TableCell sortDirection={false} align={align} sx={{ pr: 3 }}>
          <Typography variant="h5" className={classes.label}>
            Action
          </Typography>
        </TableCell>
      </TableRow>
    </TableHead>
  );
};
