import React from 'react';
import _get from 'lodash/get';

export const useSelectRow = path => {
  const [selected, setSelected] = React.useState([]);

  const handleSelectAll = (isChecked, rows) => {
    if (isChecked) {
      const newSelectedId = rows.map(row => _get(row, path, ''));
      setSelected(newSelectedId);
      return;
    }
    setSelected([]);
  };

  const handleSelect = name => {
    const selectedIndex = selected.indexOf(name);
    let newSelected = [];

    let params = [];
    switch (selectedIndex) {
      case -1:
        params = [selected, name];
        break;
      case 0:
        params = [selected.slice(1)];
        break;
      case selected.length - 1:
        params = [selected.slice(0, -1)];
        break;
      default:
        params = [
          selected.slice(0, selectedIndex),
          selected.slice(selectedIndex + 1),
        ];
        break;
    }
    newSelected = newSelected.concat(...params);
    setSelected(newSelected);
  };

  const isSelected = name => selected.indexOf(name) !== -1;

  const clearSelected = () => setSelected([]);

  return { selected, isSelected, handleSelect, handleSelectAll, clearSelected };
};
