import React from 'react';
import FooterLastStep from './FooterLastStep';
import * as Consts from '../../../shared/Consts';
import { ContactUsForm } from '../../ContactUs/ContactUsForm';

export default class ListingSubmission extends React.Component {
  render() {
    if (this.props.SelectedTab !== Consts.ListingSubmission) {
      return null;
    }

    return (
      <>
        <div className="steppers">
          <div className="step_title">Thank you for your information!</div>
          <div className="step_texts">
            You have completed the Listing Module. Please return to the
            dashboard and fill in the remaining modules at your leisure.
            <div>&nbsp;</div>
            <div>
              If you have any questions, do not hesitate to{' '}
              <ContactUsForm handleSubmit={this.props.SubmitContactUsHandler}>
                <span className="blue_text">reach out </span>
              </ContactUsForm>
              to us at{' '}
              <a href="mailto:hello@nilehome.com" className="blue_text">
                hello@nilehome.com
              </a>
            </div>
          </div>
        </div>

        <FooterLastStep {...this.props} />
      </>
    );
  }
}
