import React from 'react';
import TextInputer from './TextInputer';
import TextAreaInputer from './TextAreaInputer';
import RadioGroup from './RadioGroup';
import MultipleChoises from './MultipleChoises';
import IntlTelInputer from './IntlTelInputer';
import Select from './Select';
import Label from './Label';
import FileUploader from './FileUploader';
import SingleOption from './SingleOption';
import * as Consts from '../../shared/Consts';
import * as InputValuesHelpers from '../../shared/InputValuesHelper';
import uuidv4 from 'uuid/v4';

export const GenerateInput = props => {
  const {
    FieldDefs,
    FieldName,
    Title,
    RowNumber,
    ReadOnly,
    InputValues,
    AdditionalOptions,
  } = props;

  const options = AdditionalOptions === undefined ? [] : AdditionalOptions;

  if (FieldDefs !== undefined && FieldName !== undefined) {
    var periphery = {
      Title: null,
      SubTitle: null,
      Note: null,
      SubNote: null,
    };

    const fieldDef = FieldDefs.find(item => item.FieldName === FieldName);
    if (fieldDef === undefined) {
      return (
        <div style={{ color: 'red', fontWeight: 'bold', fontSize: '1.2rem' }}>
          ERROR: Field definition for {FieldName} does not exist
        </div>
      );
    } else {
      const generatePeriphery = value => {
        if (typeof value === 'string') {
          return value;
        } else if (typeof value === 'object' && Array.isArray(value)) {
          return value.join(', ');
        }
      };
      if (fieldDef.Title !== undefined) {
        periphery.Title = (
          <div
            className="t_texts"
            dangerouslySetInnerHTML={{
              __html: generatePeriphery(fieldDef.Title),
            }}
          />
        );
      }

      if (fieldDef.TitleComponent !== undefined) {
        periphery.TitleComponent = (
          <div dangerouslySetInnerHTML={{ __html: fieldDef.TitleComponent }} />
        );
      }

      if (fieldDef.SubTitle !== undefined) {
        periphery.SubTitle = (
          <div className="t_texts" style={{ color: '#808080' }}>
            {generatePeriphery(fieldDef.SubTitle)}
          </div>
        );
      }

      if (fieldDef.Note !== undefined) {
        periphery.Note = (
          <div
            className="t_texts"
            style={{ marginTop: '5px', marginBottom: '0' }}
          >
            {generatePeriphery(fieldDef.Note)}
          </div>
        );
      }

      if (fieldDef.NoteComponent !== undefined) {
        periphery.NoteComponent = (
          <div dangerouslySetInnerHTML={{ __html: fieldDef.NoteComponent }} />
        );
      }

      if (fieldDef.SubNote !== undefined) {
        periphery.SubNote = (
          <div
            className="t_texts"
            style={{ color: '#808080', marginTop: '5px', marginBottom: '0' }}
          >
            {generatePeriphery(fieldDef.SubNote)}
          </div>
        );
      }

      var readOnlyOverride = false;
      if (
        IsRowDependsOnValid(RowNumber, FieldDefs, FieldName, InputValues) ===
        false
      ) {
        InputValues[fieldDef.FieldName] = undefined;
        readOnlyOverride = true;
      }
    }

    const fieldProps = {
      ...props,
      Periphery: periphery,
      ReadOnly: (ReadOnly === undefined ? false : ReadOnly) || readOnlyOverride,
    };

    if (
      fieldDef.Type === Consts.FIELD_TYPE_TEXT ||
      fieldDef.Type === Consts.FIELD_TYPE_EMAIL ||
      fieldDef.Type === Consts.FIELD_TYPE_DATE ||
      fieldDef.Type === Consts.FIELD_TYPE_NUMBER ||
      fieldDef.Type === Consts.FIELD_TYPE_CURRENCY ||
      fieldDef.Type === Consts.FIELD_TYPE_INTEGER
    ) {
      return (
        <TextInputer
          FieldName={FieldName}
          Required={fieldDef.Required}
          MaxLength={fieldDef.MaxLength}
          FieldDef={fieldDef}
          {...fieldProps}
        />
      );
    } else if (fieldDef.Type === Consts.FIELD_TYPE_INTL_TEL) {
      let label = '';
      if (Title === undefined) {
        label = fieldDef.Title;
      } else if (Title !== '') {
        label = Title;
      }

      return (
        <IntlTelInputer
          FieldName={FieldName}
          Required={fieldDef.Required}
          Type={fieldDef.Type}
          Label={label}
          FieldDef={fieldDef}
          {...fieldProps}
        />
      );
    } else if (fieldDef.Type === Consts.FIELD_TYPE_TEXTAREA) {
      let label = '';
      if (Title === undefined) {
        label = fieldDef.Title;
      } else if (Title !== '') {
        label = Title;
      }

      return (
        <TextAreaInputer
          FieldName={FieldName}
          Required={fieldDef.Required}
          Type={fieldDef.Type}
          MaxLength={fieldDef.MaxLength}
          Label={label}
          {...fieldProps}
        />
      );
    } else if (fieldDef.Type === Consts.FIELD_TYPE_OPTIONS) {
      return (
        <RadioGroup
          FieldName={FieldName}
          Options={fieldDef.Options.concat(options)}
          {...fieldProps}
        />
      );
    } else if (fieldDef.Type === Consts.FIELD_TYPE_MULTIPLE_CHIOSES) {
      return (
        <MultipleChoises
          FieldName={FieldName}
          Options={fieldDef.Options.concat(options)}
          {...fieldProps}
        />
      );
    } else if (fieldDef.Type === Consts.FIELD_TYPE_YES_NO_OPTIONS) {
      return (
        <RadioGroup
          FieldName={FieldName}
          Options={[
            { label: 'Yes', value: Consts.OPTION_YES_VALUE },
            { label: 'No', value: Consts.OPTION_NO_VALUE },
          ]}
          {...fieldProps}
        />
      );
    } else if (fieldDef.Type === Consts.FIELD_TYPE_SINGLE_OPTION) {
      return <SingleOption FieldName={FieldName} {...fieldProps} />;
    } else if (fieldDef.Type === Consts.FIELD_TYPE_CUSTOME_SELECT) {
      return (
        <Select
          FieldName={FieldName}
          FieldType={fieldDef.Type}
          Options={fieldDef.Options.concat(options)}
          Title={fieldDef.Title}
          {...fieldProps}
        />
      );
    } else if (
      fieldDef.Type === Consts.FIELD_TYPE_MULTIPLE_FILES ||
      fieldDef.Type === Consts.FIELD_TYPE_SINGLE_FILE
    ) {
      const Multiple = fieldDef.Type === Consts.FIELD_TYPE_MULTIPLE_FILES;
      return (
        <FileUploader
          Multiple={Multiple}
          FieldName={FieldName}
          FieldType={fieldDef.Type}
          {...fieldProps}
        />
      );
    } else if (fieldDef.Type === Consts.FIELD_TYPE_LABEL) {
      return <Label FieldName={FieldName} {...fieldProps} />;
    }
  } else {
    return <div>Unknown definition</div>;
  }
};

export const GetFieldTitle = props => {
  return GenerateInput({ ...props });
};

export const GetFieldDescription = props => {
  const { FieldDefs, FieldName } = props;

  var description;
  if (FieldDefs !== undefined && FieldName !== undefined) {
    const fieldDef = FieldDefs.find(item => item.FieldName === FieldName);
    if (fieldDef !== undefined && fieldDef.Description !== undefined) {
      description = fieldDef.Description;
    }
  }

  return description;
};

export const GenerateInputerForm = props => {
  const { FieldDefs, FieldName, InputValues, WidthClassName } = props;
  if (FieldDefs !== undefined && FieldName !== undefined) {
    const fieldDef = FieldDefs.find(item => item.FieldName === FieldName);
    if (fieldDef === undefined) {
      return (
        <div style={{ color: 'red', fontWeight: 'bold', fontSize: '1.2rem' }}>
          ERROR: Field definition for {FieldName} does not exist
        </div>
      );
    } else {
      var abailable = true;

      if (fieldDef.DependsOn !== undefined) {
        if (
          typeof fieldDef.DependsOn === 'object' &&
          Array.isArray(fieldDef.DependsOn) === true
        ) {
          for (var dependsOn of fieldDef.DependsOn) {
            if (
              InputValuesHelpers.IsDependsOnMet(
                InputValues[dependsOn.field],
                dependsOn.value,
              ) === false
            ) {
              abailable = false;
              break;
            }
          }
        } else if (typeof fieldDef.DependsOn === 'string') {
          const dependsOnValue =
            fieldDef.DependsOnValue === undefined
              ? Consts.OPTION_YES_VALUE
              : fieldDef.DependsOnValue;
          if (
            InputValuesHelpers.IsDependsOnMet(
              InputValues[fieldDef.DependsOn],
              dependsOnValue,
            ) === false
          ) {
            abailable = false;
          }
        } else {
          abailable = false;
        }
      }

      if (abailable === true) {
        var className = `inputer_form${
          WidthClassName === undefined ? '' : ` ${WidthClassName}`
        }`;
        return (
          <>
            {fieldDef.Type === Consts.FIELD_TYPE_TABLE && (
              <div className={className}>{GenerateTable({ ...props })}</div>
            )}
            {fieldDef.Type !== Consts.FIELD_TYPE_TABLE && (
              <div className={className}>{GenerateInput({ ...props })}</div>
            )}
          </>
        );
      } else {
        return null;
      }
    }
  } else {
    return (
      <span style={{ color: 'red', fontWeight: 'bold', fontSize: '1.2rem' }}>
        ERROR: properties not provided.
      </span>
    );
  }
};

export const InputFieldAvailable = ({ FieldDefs, FieldName, InputValues }) => {
  if (FieldDefs === undefined || FieldName === undefined) {
    return false;
  }

  const fieldDef = FieldDefs.find(item => item.FieldName === FieldName);
  if (fieldDef === undefined) {
    return false;
  }

  var abailable = true;

  if (fieldDef.DependsOn !== undefined) {
    if (Array.isArray(fieldDef.DependsOn)) {
      for (var dependsOn of fieldDef.DependsOn) {
        if (
          InputValuesHelpers.IsDependsOnMet(
            InputValues[dependsOn.field],
            dependsOn.value,
          ) === false
        ) {
          abailable = false;
          break;
        }
      }
    } else if (typeof fieldDef.DependsOn === 'string') {
      const dependsOnValue =
        fieldDef.DependsOnValue === undefined
          ? Consts.OPTION_YES_VALUE
          : fieldDef.DependsOnValue;
      if (
        InputValuesHelpers.IsDependsOnMet(
          InputValues[fieldDef.DependsOn],
          dependsOnValue,
        ) === false
      ) {
        abailable = false;
      }
    } else {
      abailable = false;
    }
  }
  return abailable;
};

const GenerateTable = props => {
  const { InputValues, FieldDefs, FieldName } = props;
  const fieldDef = FieldDefs.find(item => item.FieldName === FieldName);

  var totalRows = 0;
  if (fieldDef.Rows !== undefined) {
    if (typeof fieldDef.Rows === 'number') {
      totalRows = fieldDef.Rows;
    } else if (typeof fieldDef.Rows === 'string') {
      totalRows = parseInt(InputValues[fieldDef.Rows]);
    }
  }

  var tableFieldDefs = [];
  var rowNums = [];
  for (var row = 1; row <= totalRows; row++) {
    rowNums.push(row);
    for (const element of fieldDef.Cols) {
      tableFieldDefs.push({
        ...element,
        FieldName: `${element.FieldName}_${row}`,
      });
    }
  }

  return (
    <>
      {rowNums.map(row => {
        return (
          <div className="table_container" key={`${fieldDef.FieldName}_${row}`}>
            {fieldDef.Cols.map(col => {
              if (col.Type === Consts.FIELD_TYPE_TABLE_ROW_NUMBER) {
                return (
                  <div
                    className="table_cell table_cell_row_number"
                    style={{ flexBasis: '100%' }}
                    key={uuidv4()}
                  >
                    {col.Title} #{row}
                  </div>
                );
              } else if (col.Type === Consts.FIELD_TYPE_TABLE_ROW_SEPARATOR) {
                return (
                  <div className="table_row_separator" key={uuidv4()}>
                    &nbsp;
                  </div>
                );
              } else {
                const fieldName = `${col.FieldName}_${row}`;
                const dependsOnValid = IsRowDependsOnValid(
                  row,
                  tableFieldDefs,
                  fieldName,
                  InputValues,
                );
                if (col.HiddenWhenLocked && dependsOnValid === false) {
                  InputValues[fieldDef.FieldName] = undefined;
                  return null;
                } else {
                  return (
                    <div
                      className="table_cell"
                      style={{
                        flexBasis:
                          col.FlexBasis === undefined ? '' : col.FlexBasis,
                      }}
                      key={`${col.FieldName}_cell_${row}`}
                    >
                      {GenerateInput({
                        ...props,
                        FieldDefs: tableFieldDefs,
                        FieldName: fieldName,
                        RowNumber: row,
                      })}
                    </div>
                  );
                }
              }
            })}
          </div>
        );
      })}
    </>
  );
};

const IsRowDependsOnValid = (RowNumber, FieldDefs, FieldName, InputValues) => {
  const fieldDef = FieldDefs.find(item => item.FieldName === FieldName);
  if (fieldDef === undefined) {
    return false;
  }

  if (RowNumber !== undefined && fieldDef.DependsOn !== undefined) {
    const dependsOn = fieldDef.DependsOn;

    var dependsOnValid = true;
    for (const dependsOnItem of dependsOn) {
      let fieldName;
      let fieldValue;
      if (
        dependsOnItem.row_field !== undefined &&
        dependsOnItem.row_value !== undefined
      ) {
        fieldName = `${dependsOnItem.row_field}_${RowNumber}`;
        fieldValue = dependsOnItem.row_value;
      } else if (
        dependsOnItem.field !== undefined &&
        dependsOnItem.value !== undefined
      ) {
        fieldName = `${dependsOnItem.field}`;
        fieldValue = dependsOnItem.value;
      }

      const dependsOnValue = InputValues[fieldName];
      if (dependsOnValue === undefined) {
        dependsOnValid = false;
        break;
      } else if (fieldValue === Consts.OPTION_ANY_VALUE) {
        break;
      } else if (typeof dependsOnValue === 'object') {
        if (dependsOnValue === null) {
          dependsOnValid = false;
          break;
        } else if (Array.isArray(dependsOnValue)) {
          if (
            dependsOnValue.find(item => item.value === fieldValue) === undefined
          ) {
            dependsOnValid = false;
            break;
          }
        } else {
          if (dependsOnValue.value !== fieldValue) {
            dependsOnValid = false;
            break;
          }
        }
      } else {
        if (dependsOnValue !== fieldValue) {
          dependsOnValid = false;
          break;
        }
      }
    }

    return dependsOnValid;
  }
};
