// @flow
import _get from 'lodash/get';
import _pick from 'lodash/pick';
import {
  OFFER_STEPS,
  CONTINGENCY_STATUS,
  OFFER_BUYER_TYPE,
  FUNDING_PURCHASE_TYPES,
  CONTINGENCY_TYPES,
} from 'shared/Consts';
import type { Offer, OfferTable } from 'types/Offer';
import { formatPrice } from './number';
import type { TCounter } from 'components/Offers/OfferDetails/types';
import { instructionContent } from 'containers/SellerOffer/NewOfferList/helpers';

const SELLER_COUNTER_OFFER_STEPS = [
  OFFER_STEPS.buyerNeedToReviewNewOffer,
  OFFER_STEPS.sellerNeedToCheckCounter,
];

const getCounterData = (counter: TCounter): OfferTable => {
  return (
    _pick(counter, [
      'OfferPrice',
      'IsBestOffer',
      'FinanceContingency',
      'FinanceContingencyDays',
      'InspectionContingency',
      'InspectionContingencyDays',
      'AppraisalContingency',
      'AppraisalContingencyDays',
      'SaleOfHomeContingency',
      'ClosingPeriod',
      'Furniture',
      'FurnitureOther',
      'RentBackInititated',
      'RentBack',
      'RentBackDailyAmount',
      'RentBackDays',
    ]) || {}
  );
};

export const mappingCounterData = (offer: Offer): Object => {
  const counter = getLatestCounter(offer);

  return { ...offer, MappingCounter: getCounterData(counter) };
};

export const filterStatus = (offer: Offer): Object =>
  _get(offer, 'StepId') !== OFFER_STEPS.advisorDecline;

export const isOfferContingencies = (offer: Offer): boolean => {
  const stepId = Number(_get(offer, ['StepId']));

  return stepId >= OFFER_STEPS.confirmTransfer;
};

export const canRemoveContingency = (offer: Offer): boolean => {
  return _get(offer, ['Step', 'buyer_can_remove_contingencies']);
};

export const isDisabledContingencyAction = (row: any): boolean => {
  return _get(row, ['values', 'status']) === CONTINGENCY_STATUS.done;
};

export const checkDisabledContingencyAction = (contingency: any): boolean => {
  const status = _get(contingency, ['status']);
  return !status || status === CONTINGENCY_STATUS.done;
};

const getContingencyStatus = (offer: Offer, name: string) => {
  const isRemoved = _get(offer, ['Contingencies', `${name}Removed`]);
  const status =
    isRemoved === 'Yes'
      ? CONTINGENCY_STATUS.done
      : CONTINGENCY_STATUS.inProcess;

  return {
    status,
  };
};

export const mappingOfferContingencies = (offer: Offer): Array<Object> => {
  const contingencyNames = [
    {
      name: 'FinanceContingency',
      title: 'Finance Contingency Removal',
    },
    {
      name: 'InspectionContingency',
      title: 'Inspection Contingency Removal',
    },
    {
      name: 'AppraisalContingency',
      title: 'Appraisal Contingency Removal',
    },
    {
      name: 'SaleOfHomeContingency',
      title: 'Homesale Contingency Removal',
    },
  ];

  return contingencyNames.reduce((acc: Array<Object>, contingency: Object) => {
    const { name, title } = contingency;

    const contingencyValue = _get(offer, ['Contingencies', name]);

    if (contingencyValue !== 'Yes') {
      return [...acc, { title }];
    }

    return [
      ...acc,
      {
        title,
        offer,
        ...getContingencyStatus(offer, name),
      },
    ];
  }, []);
};

export const getFullName = (info: {
  FirstName: string,
  LastName: string,
}): string => {
  const { FirstName: firstName, LastName: lastName } = info;
  return `${firstName || ''} ${lastName || ''}`;
};

export const getBuyerName = (offer: Offer): string => {
  const buyerType = _get(offer, 'BuyerType', '');
  if (!buyerType) return '';
  if (buyerType === OFFER_BUYER_TYPE.individual) {
    return getFullName(_get(offer, 'BuyerInfo', {}));
  }
  return _get(offer, 'Entity.Name', '');
};

export const getSellerName = (offer: Offer): string => {
  const sellerInfo = _get(offer, 'Property.SellerInfo', {});
  return getFullName(sellerInfo);
};

export const hasOtherValue = (values: string[]): boolean =>
  values.some(value => value.toLowerCase() === 'other');

export const removeOtherValue = (values: string[]): string[] =>
  values.filter(value => value.toLowerCase() !== 'other');

export const displayFurniture = (
  furniture: string[],
  furnitureOther: string,
): string => {
  if (!furniture || !furniture.length) return '';

  if (hasOtherValue(furniture)) {
    const filteredValues = removeOtherValue(furniture).join(', ');
    return `${filteredValues ? `${filteredValues}, ` : ''}${
      furnitureOther || ''
    }`;
  }

  return furniture.join(', ');
};

const EARNEST_MONEY_DEPOSIT_FEE = 0.03;

export const getEarnestMoneyDeposit = (offerPrice: number): string => {
  return offerPrice ? formatPrice(offerPrice * EARNEST_MONEY_DEPOSIT_FEE) : '';
};

export const displayContingency = (
  offer: Offer,
  path: string,
  purchaseTypePath?: string = 'Funding.PurchaseType',
): string => {
  if (path.includes(CONTINGENCY_TYPES.saleOfHomeContingency)) {
    return _get(offer, `${path}`) || 'N/A';
  }

  if (
    path.includes(CONTINGENCY_TYPES.financeContingency) &&
    _get(offer, purchaseTypePath, '') === FUNDING_PURCHASE_TYPES.cash
  ) {
    return 'N/A';
  }

  const contingency = _get(offer, `${path}`);
  if (!contingency) return 'N/A';

  const duration = _get(offer, `${path}Days`);
  return +duration ? `${duration} days` : 'No';
};

export const displayLoanType = (offer: Offer): string => {
  return _get(offer, 'Funding.PurchaseType', '') === FUNDING_PURCHASE_TYPES.cash
    ? 'N/A'
    : _get(offer, 'Funding.LoanType', '');
};

const BEST_OFFER_VALUE = 'Yes';

export const isBestPriceOffer = (isBestOfferValue: Array<string>): boolean => {
  return isBestOfferValue && isBestOfferValue.includes(BEST_OFFER_VALUE);
};

export const displayOfferPrice = (offer: Offer): string => {
  const isBestOffer = _get(offer, 'MappingCounter.IsBestOffer');
  if (isBestPriceOffer(isBestOffer)) return 'Best & Final';

  return _get(offer, 'MappingCounter.OfferPrice')
    ? formatPrice(_get(offer, 'MappingCounter.OfferPrice'))
    : 'TBD';
};

export const getLatestCounter = (offer: Offer): TCounter => {
  const stepId = _get(offer, ['StepId']);

  if (SELLER_COUNTER_OFFER_STEPS.includes(stepId)) {
    return _get(offer, ['CounterOffer', 'SellerCounter']);
  }

  return _get(offer, ['CounterOffer', 'BuyerCounter']);
};

const BEST_PRICE_INSTRUCTIONS = {
  seller:
    'Your NILE agent is requesting a best & final offer. To move forward with their suggestion and counter the buyer\'s offer, please click the "Accept Counter Suggestion" button. If you disagree with your NILE agent\'s proposed counter and want them to continue negotiation, please click the "Reject Counter Suggestion" button.',
  buyer:
    'You have been prompted to enter your best & final offer. Please click the "Counter" button to proceed',
};

export const getOfferInstruction = ({
  offerStepId,
  isBestOffer,
  isSeller,
}: {
  offerStepId: number,
  isBestOffer: boolean,
  isSeller: Boolean,
}): string => {
  if (
    !isSeller &&
    isBestOffer &&
    offerStepId === OFFER_STEPS.buyerNeedToReviewNewOffer
  )
    return BEST_PRICE_INSTRUCTIONS.buyer;

  if (
    isSeller &&
    isBestOffer &&
    offerStepId === OFFER_STEPS.sellerNeedToCheckCounter
  )
    return BEST_PRICE_INSTRUCTIONS.seller;

  return isSeller ? _get(instructionContent, `${offerStepId}.instruction`) : '';
};
