import { PROPERTY } from './Constants';

export const propertiesReducer = (
  state = { properties: [], isLoading: false, error: null },
  { type, payload },
) => {
  switch (type) {
    case PROPERTY.getAllProperty:
      return {
        ...state,
        properties: [],
        isLoading: true,
        error: null,
      };
    case PROPERTY.getAllPropertySuccess:
      return {
        ...state,
        properties: payload,
        isLoading: false,
        error: null,
      };
    case PROPERTY.getAllPropertyFailure:
      return {
        ...state,
        isLoading: false,
        error: payload,
      };
    default:
      return state;
  }
};
