import React from 'react';

export default function GlobalNavigator({ ActionDef, GlobalProps }) {
  const { DataPageConfig, DataGroupName, HasThankYouPage } = ActionDef;
  const { SelectedTab } = GlobalProps;

  const stepNavClasses = 'step_btn animated_fastest';
  const stepNavActiveClasses = 'step_btn animated_fastest step_active';

  var pageItems = [];
  for (var page = 1; page <= DataPageConfig.TotalPages; page++) {
    if (
      HasThankYouPage &&
      SelectedTab === DataPageConfig.TotalPages + 1 &&
      page === DataPageConfig.TotalPages
    ) {
      pageItems.push({ PageNumber: page, ClassName: stepNavActiveClasses });
    } else {
      pageItems.push({
        PageNumber: page,
        ClassName: SelectedTab === page ? stepNavActiveClasses : stepNavClasses,
      });
    }
  }

  return (
    <>
      <div className="step_title step_title_wide">{DataGroupName}</div>
      {DataPageConfig.Navigator === true && (
        <div className="stickit">
          <div className="steps">
            <div>&nbsp;</div>
            {pageItems.map(pageItem => (
              <div className={pageItem.ClassName} key={pageItem.PageNumber}>
                <div className="up_border" />
                {pageItem.PageNumber}
                <div className="down_border" />
              </div>
            ))}
          </div>
        </div>
      )}
    </>
  );
}
