import React from 'react';
import * as InputHelper from '../../Shared/InputHelper';
import InputerForm from '../../Shared/InputerForm';
import FormSectionTitle from '../../Shared/FormSectionTitle';

export default class TenancyInformation extends React.Component {
  render() {
    if (
      InputHelper.InputFieldAvailable({
        ...this.props,
        FieldName: 'GIT_Section_Indicator',
      }) === false
    ) {
      return null;
    }

    return (
      <>
        <FormSectionTitle Title="Rental Information" />
        <div className="form_inputs">
          <InputerForm
            {...this.props}
            FieldName="GIT_112_Monthly_Rental_Rate"
          />
          <InputerForm
            {...this.props}
            FieldName="GIT_869_Lease_Expiration_Date"
          />
        </div>
        <div className="form_inputs">
          <InputerForm {...this.props} FieldName="GIT_026_Lease_Docs" />
        </div>
        <div className="form_inputs">
          <div className="form_title2">Tenant Information</div>
          <div className="t_texts">Name</div>

          <InputerForm
            {...this.props}
            FieldName="GIT_935_064_Tenant_First_Name"
            WidthClassName="thirds"
          />
          <InputerForm
            {...this.props}
            FieldName="GIT_935_938_Tenant_Last_Name"
            WidthClassName="thirds"
          />
          <InputerForm
            {...this.props}
            FieldName="GIT_935_295_Tenant_Phone"
            WidthClassName="thirds"
          />
          <InputerForm
            {...this.props}
            FieldName="GIT_935_199_Tenant_Email"
            WidthClassName="thirds"
          />
        </div>

        <div className="form_inputs">
          <InputerForm {...this.props} FieldName="GIT_170_Tenant_Notified" />
        </div>
      </>
    );
  }
}
