import { useEffect, useState } from 'react';
export const THRESHOLD = 50;
export const SCROLL_DIRECTION = {
  up: 'up',
  down: 'down',
};

export const useScrollDirection = () => {
  const [scrollDir, setScrollDir] = useState(SCROLL_DIRECTION.down);

  useEffect(() => {
    let lastScrollY = window.pageYOffset;
    let ticking = false;

    const updateScrollDir = () => {
      const scrollY = window.pageYOffset;

      if (Math.abs(scrollY - lastScrollY) < THRESHOLD) {
        ticking = false;
        return;
      }
      setScrollDir(
        scrollY > lastScrollY ? SCROLL_DIRECTION.up : SCROLL_DIRECTION.down,
      );
      lastScrollY = scrollY > 0 ? scrollY : 0;
      ticking = false;
    };

    const onScroll = () => {
      if (!ticking) {
        window.requestAnimationFrame(updateScrollDir);
        ticking = true;
      }
    };

    window.addEventListener('scroll', onScroll);

    return () => window.removeEventListener('scroll', onScroll);
  }, [scrollDir]);

  return {
    scrollUp: scrollDir === SCROLL_DIRECTION.up,
    scrollDown: scrollDir === SCROLL_DIRECTION.down,
  };
};
