import { LOCATE, CURRENCY, CURRENCY_DECIMALS } from 'configs/locate';

export const formatPrice = (value = '') =>
  new Intl.NumberFormat(LOCATE, {
    style: 'currency',
    currency: CURRENCY,
    minimumFractionDigits: CURRENCY_DECIMALS,
  }).format(value);

export const convertPhoneValue = value => {
  if (value.includes('+')) return value;
  return value ? `+${value}` : value;
};
