// @flow
import React, { memo } from 'react';
import type { Element, AbstractComponent } from 'react';
import {
  Grid,
  makeStyles,
  IconButton,
  OutlinedInput,
  InputAdornment,
  FormControl,
} from '@material-ui/core';
import { Search as SearchIcon, Clear as ClearIcon } from '@material-ui/icons';

const useStyles = makeStyles(theme => ({
  searchControl: {
    fontFamily: theme.typography.fontFamily.avenirLT,
    borderRadius: 'unset',
    height: '100%',
    '& input': {
      background: 'transparent !important',
      paddingLeft: '5px !important',
    },
  },
  startAdornment: {
    fontSize: '1rem',
    color: theme.palette.grey[500],
  },
  resetButton: {
    width: '22px',
    height: '22px',
  },
  formControl: {
    height: '100%',
  },
}));

type Props = {
  search: string,
  setSearch: Function,
};

const SearchField = ({ search, setSearch }: Props): Element<'div'> => {
  const classes = useStyles();

  return (
    <Grid item xs={12} lg={3}>
      <FormControl
        variant="outlined"
        size="small"
        fullWidth
        className={classes.formControl}
      >
        <OutlinedInput
          fullWidth
          className={classes.searchControl}
          name="search"
          placeholder="Search"
          value={search}
          onChange={event => {
            setSearch(event.target.value);
          }}
          startAdornment={
            <InputAdornment position="start">
              <SearchIcon
                stroke={1.5}
                size="1rem"
                className={classes.startAdornment}
              />
            </InputAdornment>
          }
          endAdornment={
            search ? (
              <IconButton
                type="button"
                size="small"
                className={classes.resetButton}
                onClick={() => {
                  setSearch('');
                }}
              >
                <ClearIcon stroke={1.5} size="1rem" />
              </IconButton>
            ) : (
              <></>
            )
          }
          aria-describedby="search-helper-text"
          inputProps={{
            'aria-label': 'weight',
          }}
        />
      </FormControl>
    </Grid>
  );
};

export default (memo(SearchField): AbstractComponent<Props>);
