import { makeStyles } from '@material-ui/core/styles';
import clsx from 'clsx';
import _get from 'lodash/get';
import _isEmpty from 'lodash/isEmpty';
import qs from 'query-string';
import BlockUi from 'react-block-ui';
import { Loader } from 'react-loaders';
import { toastr } from 'react-redux-toastr';
import { useHistory } from 'react-router-dom';
import { GET_FORMS } from 'graphql/queries/offers/form';
import { useQuery } from '@apollo/client';
import React, { useEffect, Fragment } from 'react';
import { Redirect, generatePath } from 'react-router-dom';
import { OFFER_STEPS, APP_PATHS } from 'shared/Consts';

const useStyles = makeStyles(theme => ({
  container: {
    minHeight: '100px',
  },
  loading: {
    border: '1px solid #E0E0E0',
    backgroundColor: theme.palette.common.white,
  },
}));

const FORM_TYPE = 'sb';

const RedirectForm = (props): Element<'div' | typeof Fragment> => {
  const classes = useStyles();
  const history = useHistory();
  const queryString = qs.parse(_get(props, ['location', 'search'], ''), {
    ignoreQueryPrefix: true,
  });
  const offerId = _get(queryString, 'offerId', '');

  if (_isEmpty(offerId)) history.push({ pathname: '/' });

  const { loading, error, data } = useQuery(GET_FORMS, {
    variables: {
      offerId,
    },
    fetchPolicy: 'cache-first',
  });

  useEffect(() => {
    const { message } = error || {};
    if (message) {
      toastr.error(message);
    }
  }, [error]);

  if (_isEmpty(data)) {
    return <></>;
  }

  const stepId = _get(data, ['getOffer', 'StepId'], 0);
  if (stepId >= OFFER_STEPS.awaitingAdvisorApproval) {
    return <Redirect to={APP_PATHS.dashboard} />;
  }

  const forms = _get(data, ['getOffer', 'Forms'], {});
  const form = !_isEmpty(forms)
    ? forms.find(form => {
        const formTypeId = _get(form, 'FormTypeId', '');
        return formTypeId === FORM_TYPE;
      })
    : '';
  const formId = _get(form, 'FormId', '');
  if (_isEmpty(formId)) {
    toastr.error('Form not found');
    return <></>;
  }

  const link = generatePath(
    '/offer?offerId=:offerId&step=:stepId&formId=:formId',
    {
      offerId,
      stepId: OFFER_STEPS.pleaseComplete,
      formId,
    },
  );

  return (
    <BlockUi
      tag="div"
      blocking={loading}
      loader={<Loader active type="ball-pulse" color="rgba(231,28,99,1)" />}
      className={clsx(classes.container, { [classes.loading]: loading })}
    >
      <Redirect to={link} />
    </BlockUi>
  );
};

export default RedirectForm;
