// @flow
import * as React from 'react';
import clsx from 'clsx';
import { makeStyles, Container, Grid } from '@material-ui/core';
import imageLogo from '../../images/logo_n.svg';
import imageLogoS from '../../images/logo_s.svg';
import style from './headerStyle';
import { APP_PATHS } from 'shared/Consts';
import { LinkItem } from './LinkItem';
import RoleSwitcherSection from './RoleSwitcherSection';
import { Link } from 'react-router-dom';

const useStyles = makeStyles(style);

type Props = {
  links: Object[],
};

export const AuthDesktopHeader = ({ links }: Props): React.Element<'div'> => {
  const classes = useStyles();
  return (
    <Container className={classes.container} maxWidth="lg">
      <Grid container alignItems="center">
        <Grid item md={1} sm={1} xs={1} className={classes.menuGrid}>
          <Link className={classes.logo} to={APP_PATHS.dashboard}>
            {process.env.REACT_APP_ENV_NAME ? (
              <div style={{ textAlign: 'center' }}>
                <img src={imageLogoS} width="70px" alt="LogoW" />
                <div className="sub-title-alert">
                  {process.env.REACT_APP_ENV_NAME}
                </div>
              </div>
            ) : (
              <img src={imageLogo} width="70px" alt="LogoW" />
            )}
          </Link>
        </Grid>
        <Grid item md={8} sm={8} xs={6} className={classes.menuGrid}>
          <div className={classes.menuItems}>
            {links.map((item, key) => (
              <LinkItem key={key} {...item} />
            ))}
          </div>
        </Grid>
        <Grid
          item
          xs={5}
          sm={3}
          md={3}
          className={clsx(classes.menuGrid, classes.actionGrid)}
        >
          <div
            className={clsx(
              classes.menuSwitcher,
              classes.menuItemLink,
              classes.menuItemText,
            )}
          >
            <RoleSwitcherSection />
          </div>
        </Grid>
      </Grid>
    </Container>
  );
};
