// @flow
import React, { Fragment, memo } from 'react';
import type { Element, AbstractComponent } from 'react';
import { makeStyles, Typography, Paper } from '@material-ui/core';

type Props = {
  instruction: string,
};

const useStyles = makeStyles(theme => ({
  container: {
    minHeight: '56px',
  },
  wrapper: {
    backgroundColor: theme.palette.grey[300],
    padding: theme.spacing(2),
    borderRadius: 'unset',
    borderLeft: '5px solid',
  },
  text: {
    fontFamily: theme.typography.fontFamily.avenirLT,
    fontSize: '0.875rem',
  },
}));

const Instruction = ({
  instruction,
}: Props): Element<'div' | typeof Fragment> => {
  const classes = useStyles();

  if (!instruction) return <></>;

  return (
    <Paper elevation={0} className={classes.wrapper}>
      <Typography className={classes.text}>{instruction}</Typography>
    </Paper>
  );
};

export default (memo(Instruction): AbstractComponent<Props>);
