import * as Consts from './Consts';
import { isPossiblePhoneNumber } from 'react-phone-number-input';
import * as InputValuesHelpers from './InputValuesHelper';
import format from 'string-template';

export const Validate = (
  inputValues,
  fieldDefinitions,
  ignoreRequiredFields = false,
) => {
  if (inputValues === undefined || fieldDefinitions === undefined) {
    throw new Error('Incorrect Parameters.');
  }

  var validationResults = [];

  for (const field of fieldDefinitions) {
    const fieldName = field.FieldName;
    const required = field.Required === true;
    const fieldType = field.Type;
    const maxAllowedValue = field.MaxValue;

    var validationRules = [];
    if (typeof field.DependsOn === 'string') {
      validationRules.push({
        field: field.DependsOn,
        value: field.DependsOnValue,
      });
    } else if (
      typeof field.DependsOn === 'object' &&
      Array.isArray(field.DependsOn)
    ) {
      validationRules = field.DependsOn;
    }

    var notMatching = false;
    for (const validationRule of validationRules) {
      const dependsOnFieldName = validationRule.field;
      if (dependsOnFieldName !== undefined) {
        const dependsOn = fieldDefinitions.find(
          item => item.FieldName === dependsOnFieldName,
        );
        if (dependsOn !== undefined) {
          if (dependsOn.Type === Consts.FIELD_TYPE_YES_NO_OPTIONS) {
            const dependsOnValue =
              validationRule.value !== undefined
                ? validationRule.value
                : Consts.OPTION_YES_VALUE;
            if (
              InputValuesHelpers.IsDependsOnMet(
                inputValues[dependsOnFieldName],
                dependsOnValue,
              ) !== true
            ) {
              notMatching = true;
              break;
            }
          } else if (dependsOn.Type === Consts.FIELD_TYPE_MULTIPLE_CHIOSES) {
            const dependsOnValue = validationRule.value;
            const dependsOnInputValue = inputValues[dependsOnFieldName];
            if (
              InputValuesHelpers.IsDependsOnMet(
                dependsOnInputValue,
                dependsOnValue,
              ) !== true
            ) {
              notMatching = true;
              break;
            }
          } else if (dependsOn.Type === Consts.FIELD_TYPE_CUSTOME_SELECT) {
            const dependsOnValue = validationRule.value;
            const dependsOnInputValue = inputValues[dependsOnFieldName];

            if (dependsOnInputValue === undefined) {
              notMatching = true;
              break;
            } else {
              if (dependsOnValue !== Consts.OPTION_ANY_VALUE) {
                if (
                  InputValuesHelpers.IsDependsOnMet(
                    dependsOnInputValue,
                    dependsOnValue,
                  ) !== true
                ) {
                  notMatching = true;
                  break;
                }
              }
            }
          } else if (dependsOn.Type === Consts.FIELD_TYPE_OPTIONS) {
            const dependsOnValue = validationRule.value;
            const dependsOnInputValue = inputValues[dependsOnFieldName];
            if (
              InputValuesHelpers.IsDependsOnMet(
                dependsOnInputValue,
                dependsOnValue,
              ) !== true
            ) {
              notMatching = true;
              break;
            }
          } else {
            const dependsOnValue = validationRule.value;
            const dependsOnInputValue = inputValues[dependsOnFieldName];
            if (
              InputValuesHelpers.IsDependsOnMet(
                dependsOnInputValue,
                dependsOnValue,
              ) !== true
            ) {
              notMatching = true;
              break;
            }
          }
        }
      }
    }

    if (notMatching) {
      continue;
    }

    const fieldPropsToReturn = {
      FieldName: field.FieldName,
      DataSection: field.DataSection,
    };

    if (required && ignoreRequiredFields === false) {
      // validate table
      if (field.Type === Consts.FIELD_TYPE_TABLE) {
        var totalRows = 0;
        if (field.Rows !== undefined) {
          if (typeof field.Rows === 'number') {
            totalRows = field.Rows;
          } else if (typeof field.Rows === 'string') {
            totalRows = parseInt(inputValues[field.Rows]);
          }
        }

        var tableFieldDefs = [];
        for (var row = 1; row <= totalRows; row++) {
          for (const element of field.Cols) {
            if (element.FieldName !== undefined) {
              var newField = {
                ...element,
                FieldName: `${element.FieldName}_${row}`,
              };

              if (element.DependsOn !== undefined) {
                var tableFieldDependsOn = [];
                if (Array.isArray(element.DependsOn)) {
                  for (const dependsOnItem of element.DependsOn) {
                    if (
                      dependsOnItem.row_field !== undefined &&
                      dependsOnItem.row_value !== undefined
                    ) {
                      tableFieldDependsOn.push({
                        field: `${dependsOnItem.row_field}_${row}`,
                        value: dependsOnItem.row_value,
                      });
                    } else if (
                      dependsOnItem.field !== undefined &&
                      dependsOnItem.value !== undefined
                    ) {
                      tableFieldDependsOn.push(dependsOnItem);
                    }
                  }

                  newField = { ...newField, DependsOn: tableFieldDependsOn };
                }
              }
              tableFieldDefs.push(newField);
            }
          }
        }

        const tableValidationResults = Validate(inputValues, tableFieldDefs);
        validationResults = validationResults.concat(
          tableValidationResults.map(validationResult => {
            return { ...validationResult, DataSection: field.DataSection };
          }),
        );
        continue;
      }

      if (inputValues[fieldName] === undefined) {
        validationResults.push({
          ...fieldPropsToReturn,
          ValidationError: Consts.VALIDATION_ERROR_REQUIRED_FIELD,
        });
        continue;
      }

      if (
        field.Type === Consts.FIELD_TYPE_SINGLE_FILE ||
        field.Type === Consts.FIELD_TYPE_MULTIPLE_FILES ||
        field.Type === Consts.FIELD_TYPE_MULTIPLE_CHIOSES
      ) {
        if (inputValues[fieldName].length === 0) {
          validationResults.push({
            ...fieldPropsToReturn,
            ValidationError: Consts.VALIDATION_ERROR_REQUIRED_FIELD,
          });
          continue;
        }
      } else if (field.Type === Consts.FIELD_TYPE_SINGLE_OPTION) {
        if (inputValues[fieldName] !== true) {
          validationResults.push({
            ...fieldPropsToReturn,
            ValidationError: Consts.VALIDATION_ERROR_REQUIRED_FIELD,
          });
          continue;
        }
      } else if (
        field.Type === Consts.FIELD_TYPE_INTEGER ||
        field.Type === Consts.DATA_TYPE_NUMBER
      ) {
        if (inputValues[fieldName] === undefined) {
          validationResults.push({
            ...fieldPropsToReturn,
            ValidationError: Consts.VALIDATION_ERROR_REQUIRED_FIELD,
          });
          continue;
        }
      } else {
        if (`${inputValues[fieldName]}`.trim() === '') {
          validationResults.push({
            ...fieldPropsToReturn,
            ValidationError: Consts.VALIDATION_ERROR_REQUIRED_FIELD,
          });
          continue;
        }
      }
    }

    const fieldValue = inputValues[fieldName];
    if (fieldType === Consts.FIELD_TYPE_EMAIL) {
      if (
        fieldValue !== '' &&
        fieldValue !== undefined &&
        Consts.EMAIL_REX.test(fieldValue) === false
      ) {
        validationResults.push({
          ...fieldPropsToReturn,
          ValidationError: Consts.VALIDATION_ERROR_INVALID_EMAIL,
        });
      }
    } else if (fieldType === Consts.FIELD_TYPE_CURRENCY) {
      if (
        fieldValue !== '' &&
        fieldValue !== undefined &&
        fieldValue !== null &&
        Consts.CURRENCY_REX.test(fieldValue) === false
      ) {
        validationResults.push({
          ...fieldPropsToReturn,
          ValidationError: Consts.VALIDATION_ERROR_CURRENCY,
        });
      } else if (
        maxAllowedValue !== undefined &&
        fieldValue > maxAllowedValue
      ) {
        validationResults.push({
          ...fieldPropsToReturn,
          ValidationError: Consts.VALIDATION_ERROR_NUMBER_TOO_LARGE,
        });
      }
    } else if (fieldType === Consts.FIELD_TYPE_INTEGER) {
      if (
        fieldValue !== '' &&
        fieldValue !== undefined &&
        Consts.INTEGER_REX.test(fieldValue) === false
      ) {
        validationResults.push({
          ...fieldPropsToReturn,
          ValidationError: Consts.VALIDATION_ERROR_INTEGER,
        });
      } else if (
        maxAllowedValue !== undefined &&
        fieldValue > maxAllowedValue
      ) {
        validationResults.push({
          ...fieldPropsToReturn,
          ValidationError: Consts.VALIDATION_ERROR_NUMBER_TOO_LARGE,
        });
      }
    } else if (fieldType === Consts.FIELD_TYPE_INTL_TEL) {
      if (
        fieldValue !== '' &&
        fieldValue !== undefined &&
        isPossiblePhoneNumber(fieldValue) === false
      ) {
        validationResults.push({
          ...fieldPropsToReturn,
          ValidationError: Consts.VALIDATION_ERROR_INTL_PHONE,
        });
      }
    } else if (
      fieldType === Consts.FIELD_TYPE_SINGLE_FILE ||
      fieldType === Consts.FIELD_TYPE_MULTIPLE_FILES
    ) {
      if (Array.isArray(fieldValue)) {
        if (fieldValue.length > Consts.ALLOWED_MAX_FILES_COUNT) {
          validationResults.push({
            ...fieldPropsToReturn,
            ValidationError: format(Consts.VALIDATION_ERROR_TOO_MANY_FILES, {
              AllowedMaxFiles: Consts.ALLOWED_MAX_FILES_COUNT,
            }),
          });
        } else {
          for (const fileItem of fieldValue) {
            if (fileItem.file !== undefined) {
              if (
                fileItem.file.size >
                Consts.ALLOWED_MAX_FILE_SIZE_MB * 1024 * 1024
              ) {
                validationResults.push({
                  ...fieldPropsToReturn,
                  ValidationError: format(
                    Consts.VALIDATION_ERROR_FILE_SIZE_OVER_LIMIT,
                    { AllowedFileSize: Consts.ALLOWED_MAX_FILE_SIZE_MB },
                  ),
                });
                break;
              } else if (
                Consts.ALLOWED_TYPE_TYPES.find(
                  allowedType => allowedType.FileType === fileItem.file.type,
                ) === undefined
              ) {
                validationResults.push({
                  ...fieldPropsToReturn,
                  ValidationError:
                    Consts.VALIDATION_ERROR_NOT_SUPPORTED_FILE_TYPE,
                });
                break;
              }
            }
          }
        }
      }
    }
  }

  console.log(validationResults);
  return validationResults;
};
