class Dashboard extends React.Component {
  constructor(props) {
    super();

    this.UpdatePropertyStatus = this.UpdatePropertyStatus.bind(this);
    this.LoadData = this.LoadData.bind(this);
    this.SaveData = this.SaveData.bind(this);
    this.SubmitData = this.SubmitData.bind(this);
    this.SwitchTab = this.SwitchTab.bind(this);
    this.ValueChangeHandler = this.ValueChangeHandler.bind(this);
    this.ValidateInputs = this.ValidateInputs.bind(this);
    this.GotoDashboard = this.GotoDashboard.bind(this);
    this.SubmitContactUs = this.SubmitContactUs.bind(this);
    this.UpdateInputValue = this.UpdateInputValue.bind(this);

    const queryString = qs.parse(props.location.search, {
      ignoreQueryPrefix: true,
    });
    const action = queryString.action;
    const propertyId = queryString.propertyId;
    const formId = queryString.formId;

    var startStep = 1;
    if (props.match !== undefined) {
      startStep =
        props.match.params.step === undefined ? 1 : props.match.params.step;
    }

    this.state = {
      propertyId: propertyId,
      SelectedTab: startStep,
      action: action,
      loading: false,
      InputValues: {},
      InputValuesChanged: {},
      ValidationResults: [],
      ResponseData: undefined,
      ReadOnly: undefined,
      PropertyData: undefined,
      PropertyStatus: undefined,
      NotFound: false,
      ViewOnly: true,
      formId,
    };
  }

  componentDidMount() {
    this.LoadData();
  }

  async LoadData() {
    var NotFound = false;
    try {
      const action = Actions.find(
        action => action.Action === this.state.action,
      );
      if (
        action === undefined ||
        (action !== undefined && action.UseGlobalDataLoad !== true)
      ) {
        return;
      }
      var dataSections = action.DataSections;
      dataSections.push(Consts.DATA_SECTION_PROPERTY_DATA);

      const fieldDefs = action.FieldDefinitions;

      this.setState({ loading: true, InputValuesChanged: {} });
      const requestConfig = await Consts.GetRequestConfig(Auth);

      const url = `${Consts.API_SELLER_PROPERTIES}/${
        this.state.propertyId
      }/datasections?include=${dataSections.join(',')}`;
      const response = await axios.get(url, requestConfig);

      let inputValues = this.state.InputValues;
      if (inputValues === undefined) {
        inputValues = {};
      }

      dataSections.forEach(dataSection => {
        const result = this.ConvertData(dataSection, fieldDefs, response.data);
        inputValues = { ...inputValues, ...result };
      });

      const ReadOnly = SellRequestUtil.GetStatus(
        action.SubmittedFlag,
        response.data.Property[Consts.DATA_SECTION_PROPERTY_STATUS],
      );
      if (action.InitInputValuesFunc !== undefined) {
        action.InitInputValuesFunc(inputValues, response.data);
      }

      this.setState({
        InputValues: inputValues,
        ReadOnly,
        ViewOnly: response.data.Property.ViewOnly,
        ResponseData: response.data.Property,
        PropertyData: response.data.Property[Consts.DATA_SECTION_PROPERTY_DATA],
        PropertyStatus:
          response.data.Property[Consts.DATA_SECTION_PROPERTY_STATUS],
      });
    } catch (error) {
      console.log(error);
      if (error.response !== undefined && error.response.status === 404) {
        NotFound = true;
        toastr.error('Load property data', 'Property does not exist');
      } else {
        toastr.error('Load property data', 'Please refresh page and try again');
      }
    } finally {
      this.setState({ loading: false, NotFound });
    }
  }

  async UpdatePropertyStatus(name) {
    this.setState({ loading: true });
    try {
      const body = {
        Name: name,
      };
      const requestConfig = await Consts.GetRequestConfig(Auth);
      await axios.put(
        `${Consts.API_SELLER_PROPERTIES}/${this.state.propertyId}/status`,
        body,
        requestConfig,
      );
      toastr.success('Submission', 'Succeeded');
      return true;
    } catch (error) {
      console.log(error);
      if (
        error.response.status === 409 &&
        error.response.data.Message !== undefined
      ) {
        toastr.error('Update status', error.response.data.Message);
      } else {
        toastr.error('Update status', 'Please refresh page and try again');
      }
      return false;
    } finally {
      this.setState({ loading: false });
    }
  }

  ValidateInputs(dataSections, fieldDefs, ignoreRequiredFields = false) {
    var validateFieldDefs = [];
    for (const dataSection of dataSections) {
      const defs = fieldDefs.filter(def => def.DataSection === dataSection);
      validateFieldDefs = validateFieldDefs.concat(defs);
    }

    const propertyDataDefs = fieldDefs.filter(
      def => def.DataSection === Consts.DATA_SECTION_PROPERTY_DATA,
    );
    validateFieldDefs = validateFieldDefs.concat(propertyDataDefs);

    const validationResults = Validator.Validate(
      this.state.InputValues,
      validateFieldDefs,
      ignoreRequiredFields,
    );
    this.setState({ ValidationResults: validationResults });
    return validationResults;
  }

  async SaveData(dataSections, ignoreRequiredFields = false) {
    try {
      const action = Actions.find(
        action => action.Action === this.state.action,
      );
      if (action === undefined) {
        return false;
      }
      const fieldDefs = action.FieldDefinitions;

      const validateResults = this.ValidateInputs(
        dataSections,
        fieldDefs,
        ignoreRequiredFields,
      );
      if (validateResults.length > 0) {
        toastr.warning('Please fill in all required fields');
        return false;
      }

      this.setState({ loading: true });

      const postData = {};
      const validateResultsToSave = this.ValidateInputs(
        dataSections,
        fieldDefs,
      );
      dataSections.forEach(section => {
        // get all validation error
        var sectionValidateResults = validateResultsToSave.filter(
          validationResult => validationResult.DataSection === section,
        );
        const sectionPostData = PostDataGenerator.GetPostData(
          this.state.InputValues,
          fieldDefs,
          section,
        );
        postData[section] = {
          ...sectionPostData,
          Errors: sectionValidateResults,
        };
      });

      const body = {
        Data: postData,
      };

      const requestConfig = await Consts.GetRequestConfig(Auth);
      const response = await axios.post(
        `${Consts.API_SELLER_PROPERTIES}/${this.state.propertyId}/datasections`,
        body,
        requestConfig,
      );
      const responseData = response.data;

      // upload files
      if (responseData.urls !== undefined) {
        const uploadUrls = responseData.urls;
        const inputValues = this.state.InputValues;
        for (const key of Object.keys(inputValues)) {
          if (Array.isArray(inputValues[key])) {
            for (const fileItem of inputValues[key]) {
              if (
                fileItem.action !== undefined &&
                fileItem.file !== undefined &&
                fileItem.size !== undefined
              ) {
                const uploadUrl = uploadUrls.find(
                  item => item.id === fileItem.id,
                );

                if (uploadUrl !== undefined) {
                  var options = {
                    headers: {
                      'Content-Type': fileItem.type,
                    },
                  };
                  try {
                    const fileToUpload = fileItem.file;
                    await axios.put(uploadUrl.url, fileToUpload, options);
                    toastr.success(
                      'Upload file',
                      `Uploaded file ${fileItem.name}`,
                    );
                  } catch (error) {
                    toastr.error(
                      'Upload file',
                      `Failed to upload file ${fileItem.name}`,
                    );
                    return false;
                  }
                }
              }
            }
          }
        }
      }

      toastr.success('Save Data', 'Succeeded');
      return true;
    } catch (error) {
      toastr.error('Failed to save data', 'Please refresh page and try again');
      return false;
    } finally {
      this.setState({ loading: false });
    }
  }

  async SubmitData(dataSections, submitFlag) {
    const action = Actions.find(action => action.Action === this.state.action);
    if (action === undefined) {
      toastr.error('Failed to save data', 'Please refresh page and try again');
      return false;
    }

    if (submitFlag === undefined) {
      submitFlag = action.SubmittedFlag;
    }

    var result = false;
    if ((await this.SaveData(dataSections)) === true) {
      if ((await this.UpdatePropertyStatus(submitFlag)) === true) {
        await this.LoadData();
        result = true;
      }
    }

    return result;
  }

  async SubmitContactUs(message, requestType) {
    var result;
    try {
      const body = {
        Message: `${
          requestType !== undefined ? requestType + '\n\n' : ''
        }${message}`,
      };
      const requestConfig = await Consts.GetRequestConfig(Auth);
      await axios.post(
        `${Consts.API_SELLER_PROPERTIES}/${this.state.propertyId}/contactus`,
        body,
        requestConfig,
      );

      toastr.success('Contact US', 'You request has been submitted.');
      result = true;
    } catch (error) {
      result = false;
      console.log(error);
      var errorMessage = 'Please refresh page and try again';
      if (error.response.status === 404) {
        if (
          error.response.data !== undefined &&
          error.response.data.Message !== undefined
        ) {
          errorMessage = error.response.data.Message;
        }
      }
      toastr.error('Failed to submit your request', errorMessage);
    }
    return result;
  }

  UpdateInputValue(fieldName, value) {
    const inputValues = this.state.InputValues;
    inputValues[fieldName] = value;
    this.setState({ InputValues: inputValues });
    this.UpdateInputValuesChanged(this.state.SelectedTab, true);
  }

  ValueChangeHandler(event, fieldName, fieldType, value) {
    const action = Actions.find(action => action.Action === this.state.action);
    const inputValues = this.state.InputValues;

    if (action.PreValueChangeFunc !== undefined) {
      action.PreValueChangeFunc(
        event,
        fieldName,
        fieldType,
        value,
        inputValues,
        action,
      );
    }

    if (fieldName !== undefined && fieldType !== undefined) {
      inputValues[fieldName] = value;
    } else {
      if (event.target.type === 'text') {
        inputValues[event.target.name] = event.target.value;
      } else if (event.target.type === 'checkbox') {
        if (event.target.dataset === undefined) {
          inputValues[event.target.name] = event.target.checked;
        } else if (event.target.dataset.type === 'multiple-choises') {
          const label = event.target.dataset.label;
          const value = event.target.dataset.value;

          var currentSelectedItems = inputValues[event.target.name];
          if (
            currentSelectedItems === undefined ||
            (typeof currentSelectedItems === 'string' &&
              currentSelectedItems === '')
          ) {
            currentSelectedItems = [];
          } else {
            currentSelectedItems = currentSelectedItems.filter(
              item =>
                item.value !== value &&
                item.label.toUpperCase() !== Consts.LABEL_NONE.toUpperCase(),
            );
          }
          if (event.target.checked) {
            // clear other choices when None is checked
            if (label.toUpperCase() === Consts.LABEL_NONE.toUpperCase()) {
              currentSelectedItems = [{ label, value }];
            } else {
              currentSelectedItems.push({ label, value });
            }
          }

          inputValues[event.target.name] = currentSelectedItems;
        } else {
          inputValues[event.target.name] = event.target.checked;
        }
      } else if (event.target.type === 'date') {
        inputValues[event.target.name] = event.target.value;
      } else {
        inputValues[event.target.name] = event.target.value;
      }
    }

    if (action.PostValueChangeFunc !== undefined) {
      action.PostValueChangeFunc(
        event,
        fieldName,
        fieldType,
        value,
        inputValues,
        action,
      );
    }

    this.setState({ InputValues: inputValues });
    this.UpdateInputValuesChanged(this.state.SelectedTab, true);
  }

  SwitchTab(tabIndex) {
    this.setState({ SelectedTab: tabIndex });
    window.scrollTo(0, 0);
  }

  GotoDashboard() {
    console.log('dd');
    this.props.history.push('/?tab=sell');
  }

  ConvertData(dataSection, fieldDefs, responseData) {
    const sectionData = responseData.Property[dataSection];
    const sectionDataResult = ResponseDataConvert.Convert(
      sectionData,
      fieldDefs,
      dataSection,
    );
    return sectionDataResult;
  }

  UpdateInputValuesChanged(step, value) {
    const inputValuesChanged = this.state.InputValuesChanged;
    inputValuesChanged[step] = value;
    this.setState({ InputValuesChanged: inputValuesChanged });
  }

  render() {
    const action = Actions.find(
      action => action.Action === this.state.action && action.Disabled !== true,
    );

    const { formId } = this.state;

    if (action === undefined) {
      if (formId) {
        return (
          <Form
            formId={formId}
            itemId={this.state.propertyId}
            saveExitAction={this.GotoDashboard}
          />
        );
      }

      return (
        <div style={{ minHeight: '70vh', marginTop: '120px' }}>
          <p className="incorrect_parameter" style={{ textAlign: 'center' }}>
            Incorrect Parameters
          </p>
        </div>
      );
    }

    var hasThankYouPage = false;
    if (
      action !== undefined &&
      action.HasThankYouPage === true &&
      action.ThankYouPageComponent !== undefined
    ) {
      hasThankYouPage = true;
    }

    const readonly = this.state.ViewOnly || this.state.ReadOnly;

    const componentProps = {
      PropertyId: this.state.propertyId,
      InputValues: this.state.InputValues,
      SelectedTab: this.state.SelectedTab,
      ValidationResults: this.state.ValidationResults,
      FieldDefs: action.FieldDefinitions,
      Action: this.state.action,
      ReadOnly: readonly,
      ResponseData: this.state.ResponseData,
      PropertyData: this.state.PropertyData,
      PropertyStatus: this.state.PropertyStatus,
      UpdatePropertyStatusHandler: this.UpdatePropertyStatus,
      LoadDataHandler: this.LoadData,
      SaveDataHandler: this.SaveData,
      SubmitDataHandler: this.SubmitData,
      SwitchTabHandler: this.SwitchTab,
      GotoDashboardHandler: this.GotoDashboard,
      ValueChangeHandler: this.ValueChangeHandler,
      ValidateInputsHandler: this.ValidateInputs,
      UpdateInputValueHandler: this.UpdateInputValue,
      SubmitContactUsHandler: this.SubmitContactUs,
      HasThankYouPage: hasThankYouPage,
      ThankYouPageComponent: action.ThankYouPageComponent,
      DataSectionAPIUrl: `${Consts.API_SELLER_PROPERTIES}/${this.state.propertyId}/datasections`,
      StartLoading: () => {
        console.log(`Start Loading ${this.state.action}`);
        this.setState({ loading: true });
      },
      EndLoading: () => {
        console.log(`End Loading ${this.state.action}`);
        this.setState({ loading: false });
      },
      NotifyError: (title, message) => {
        toastr.error(title, message);
      },
      NotifySuccess: (title, message) => {
        toastr.success(title, message);
      },
      NotifyWarning: (title, message) => {
        toastr.warning(title, message);
      },
      GetRequestConfig: async () => {
        return Consts.GetRequestConfig(Auth);
      },
    };

    return (
      <BlockUi
        tag="div"
        blocking={this.state.loading}
        loader={<Loader active type="ball-pulse" color="rgba(231,28,99,1)" />}
        style={{ minHeight: '70vh', marginTop: '120px' }}
      >
        {this.state.NotFound === true && (
          <div style={{ minHeight: '70vh', marginTop: '120px' }}>
            <p className="incorrect_parameter" style={{ textAlign: 'center' }}>
              Property Not Found
            </p>
          </div>
        )}

        {action !== undefined && this.state.ResponseData !== undefined && (
          <div className="container">
            <action.Component
              {...{
                ...componentProps,
                ActionDef: action,
                GlobalProps: componentProps,
              }}
            />
          </div>
        )}
      </BlockUi>
    );
  }
}

export default withCustomAuthenticator(Dashboard);

