// @flow
import * as React from 'react';
import { useState } from 'react';
import clsx from 'clsx';
import { makeStyles, Grid, Box, IconButton, Drawer } from '@material-ui/core';
import MenuIcon from '@material-ui/icons/Menu';
import imageLogo from '../../images/logo_n.svg';
import imageLogoS from '../../images/logo_s.svg';
import style from './headerStyle';
import { APP_PATHS } from 'shared/Consts';
import { LinkItem } from './LinkItem';
import RoleSwitcherSection from './RoleSwitcherSection';
import { Link } from 'react-router-dom';

const useStyles = makeStyles(style);

type Props = {
  links: Object[],
};

export const AuthMobileHeader = ({
  links,
}: Props): React.Element<typeof React.Fragment> => {
  const classes = useStyles();
  const [open, setOpen] = useState(false);
  return (
    <>
      <Box sx={{ py: 1.5, px: 2 }}>
        <Grid
          container
          alignItems="center"
          justifyContent="space-between"
          fullWidth
        >
          <Grid item xs={4}>
            <IconButton
              className={classes.mobileMenuButton}
              onClick={() => setOpen(true)}
            >
              <MenuIcon fontSize="large" />
            </IconButton>
          </Grid>
          <Grid item xs={4} container justifyContent="center">
            <Link to={APP_PATHS.dashboard}>
            {
              process.env.REACT_APP_ENV_NAME 
              ? 
              <div style={{textAlign: 'center'}}>
              <img src={imageLogoS} width="70px" alt="LogoW" />
              <div className='sub-title-alert'>{process.env.REACT_APP_ENV_NAME}</div>
              </div>  
              : 
              <img src={imageLogo} width="70px" alt="LogoW" />
            }
            </Link>
          </Grid>
          <Grid item xs={4} container justifyContent="flex-end">
            <div
              className={clsx(
                classes.menuItemLink,
                classes.menuItemText,
                classes.roleSwitcher,
              )}
            >
              <RoleSwitcherSection />
            </div>
          </Grid>
        </Grid>
      </Box>
      <Drawer
        open={open}
        anchor="left"
        onClose={() => setOpen(false)}
        classes={{ paper: classes.mobileMenu }}
      >
        <Box className={classes.mobileLogo}>
          <Link to={APP_PATHS.dashboard}>
          {
            process.env.REACT_APP_ENV_NAME 
            ? 
            <div style={{textAlign: 'center'}}>
            <img src={imageLogoS} width="70px" alt="LogoW" />
            <div className='sub-title-alert'>{process.env.REACT_APP_ENV_NAME}</div>
            </div>  
            : 
            <img src={imageLogo} width="70px" alt="LogoW" />
          }
          </Link>
        </Box>
        <Box className={classes.mobileItems}>
          {links.map((item, key) => (
            <LinkItem key={key} {...item} />
          ))}
        </Box>
      </Drawer>
    </>
  );
};
