// @flow
import React, { useEffect, useState } from 'react';
import type { Element, AbstractComponent } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Calendar from '../../components/Calendar';
import { Widget } from '../../components/Widget';
import { Box, Button, Collapse, Typography } from '@material-ui/core';
import { PropertyButtonGroup } from '../../components/PropertyButtonGroup';
import _get from 'lodash.get';
import { connect } from 'react-redux';
import { withErrorBoundary } from 'components/ErrorBoundary';
import { createStructuredSelector } from 'reselect';
import { setProperty } from '../../components/Calendar/redux/Actions';
import BlockUi from 'react-block-ui';
import { Loader } from 'react-loaders';
import clsx from 'clsx';

const useStyles = makeStyles(theme => ({
  widget: {
    padding: theme.spacing(2),
  },
  selectProperty: {
    padding: theme.spacing(0.5, 0),
    textTransform: 'capitalize',
  },
  container: {
    fontFamily: theme.typography.fontFamily.avenirLT,
  },
  loading: {
    backgroundColor: theme.palette.common.white,
  },
}));

type Props = {
  property: Object,
  properties: Object[],
  setProperty: Function,
  loading: boolean,
};

const CalendarWidget = ({
  loading,
  property,
  properties: allProperties,
  setProperty,
}: Props): Element<typeof Widget> => {
  const classes = useStyles();
  const [open, setOpen] = useState(true);
  const properties = allProperties.filter(
    property => property.HomeViewingCalendarProfileId,
  );
  const hasProperty = Boolean(property);
  const profileId = _get(property, 'HomeViewingCalendarProfileId', '');

  useEffect(() => {
    if (properties.length === 1) {
      setProperty(properties[0]);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [properties]);

  const onSelectProperty = (value: Object) => {
    setOpen(false);
    setProperty(value);
  };

  return (
    <Widget className={classes.widget}>
      <BlockUi
        tag="div"
        blocking={loading}
        loader={<Loader active type="ball-pulse" color="rgba(231,28,99,1)" />}
        className={clsx(classes.container, {
          [classes.loading]: loading,
        })}
      >
        <Box>
          {properties.length > 0 ? (
            <>
              <Button
                onClick={() => setOpen(!open)}
                classes={{ root: classes.selectProperty }}
              >
                <Typography variant="h5">Select a property</Typography>
              </Button>
              <Collapse in={open}>
                <PropertyButtonGroup
                  value={_get(property, 'PropertyId', '')}
                  properties={properties}
                  onSelectProperty={onSelectProperty}
                />
              </Collapse>
            </>
          ) : (
            <div>There are no properties with calendar profile</div>
          )}
        </Box>
        {hasProperty && profileId && (
          <Box sx={{ mt: 2 }}>
            <Calendar profileId={profileId} />
          </Box>
        )}
      </BlockUi>
    </Widget>
  );
};

const structuredSelector = createStructuredSelector({
  property: state => _get(state, ['calendar', 'property']),
});

const mapDispatchToProps = {
  setProperty,
};

export default (connect(
  structuredSelector,
  mapDispatchToProps,
)(withErrorBoundary(CalendarWidget)): AbstractComponent<{}>);
