import React from 'react';
import _get from 'lodash/get';
import { LOCATE, CURRENCY } from 'configs/locate';

export const renderPrice = prop => {
  const value = _get(prop, 'value', '');
  return `${
    value
      ? new Intl.NumberFormat(LOCATE, {
          style: 'currency',
          currency: CURRENCY,
        }).format(value)
      : '-'
  }`;
};

export const renderItem = ({ value }) => <span>{value || '-'}</span>;

export const descendingComparator = (a, b, orderBy) => {
  const orderByA = _get(a, orderBy) || '';
  const orderByB = _get(b, orderBy) || '';

  if (typeof orderByA === 'string' && typeof orderByB === 'string') {
    return orderByB.localeCompare(orderByA);
  }

  if (orderByB < orderByA) {
    return -1;
  }
  if (orderByB > orderByA) {
    return 1;
  }
  return 0;
};

export const getComparator = (order, orderBy) => {
  const direction = order === 'desc' ? 1 : -1;
  return (a, b) => descendingComparator(a, b, orderBy) * direction;
};

export const stableSort = (array, comparator) => {
  const stabilizedThis = array.map((value, index) => ({ value, index }));
  stabilizedThis.sort((a, b) => {
    const order = comparator(a.value, b.value);
    if (order !== 0) return order;
    return a.index - b.index;
  });
  return stabilizedThis.map(el => el.value);
};