// @flow
import type { Element } from 'react';
import {
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableRow,
  IconButton,
  makeStyles,
} from '@material-ui/core';
import React from 'react';
import { useOrder } from '../../../hooks';
import VisibilityIcon from '@material-ui/icons/Visibility';
import { Header } from 'components/ListTable';
import { getOfferInfo, headerCells } from './helpers';
import { getComparator, stableSort } from '../../../helpers/Table';

const useStyles = makeStyles(theme => ({
  row: {
    ...theme.typography.h5,
    color: theme.palette.text.secondaryDark,
    '&&:hover ,&.Mui-selected': {
      backgroundColor: theme.palette.background.green,
    },
  },
  status: {
    width: '15%'
  }
}));

type Props = {
  offers: Object[],
  onViewOffer: Function,
};

export const OffersTable = ({
  offers,
  onViewOffer,
}: Props): Element<typeof TableContainer> => {
  const classes = useStyles();
  const { order, orderBy, handleRequestSort } = useOrder('OfferNo');

  const sortedOffers = stableSort(offers, getComparator(order, orderBy));

  return (
    <TableContainer>
      <Table aria-labelledby="tableTitle" size="small">
        <Header
          cells={headerCells}
          order={order}
          orderBy={orderBy}
          onRequestSort={handleRequestSort}
          rowCount={offers.length}
        />
        <TableBody>
          {sortedOffers.map((offer, index) => {
            const {
              offerId,
              offerNo,
              buyerName,
              price,
              funding,
              downPayment,
              inspectionContingency,
              saleOfHomeContingency,
              financeContingency,
              appraisalContingency,
              closingPeriod,
              status,
            } = getOfferInfo(offer);
            return (
              <TableRow
                hover
                role="checkbox"
                tabIndex={-1}
                key={offerId}
                classes={{
                  root: classes.row,
                }}
              >
                <TableCell padding="none" align="center">
                  {offerNo}
                </TableCell>
                <TableCell padding="none" align="center">
                  {buyerName}
                </TableCell>
                <TableCell align="center" padding="none">
                  {price}
                </TableCell>
                <TableCell align="center" padding="none">
                  {funding}
                </TableCell>
                <TableCell padding="none" align="center">
                  {downPayment}
                </TableCell>
                <TableCell align="center" padding="none">
                  {financeContingency}
                </TableCell>
                <TableCell align="center" padding="none">
                  {inspectionContingency}
                </TableCell>
                <TableCell align="center" padding="none">
                  {appraisalContingency}
                </TableCell>
                <TableCell align="center" padding="none">
                  {saleOfHomeContingency}
                </TableCell>
                <TableCell padding="none" align="center">
                  {closingPeriod}
                </TableCell>
                <TableCell align="center" padding="none" className={classes.status}>
                  {status}
                </TableCell>
                <TableCell align="center" sx={{ pr: 3 }} padding="none">
                  <IconButton color="primary" onClick={onViewOffer(offer)}>
                    <VisibilityIcon sx={{ fontSize: '1.3rem' }} />
                  </IconButton>
                </TableCell>
              </TableRow>
            );
          })}
        </TableBody>
      </Table>
    </TableContainer>
  );
};
