export default function CalcClosingCosts(
  listPrice,
  totalLoanInput,
  propertyCity,
) {
  if (listPrice === undefined || propertyCity === undefined) {
    return undefined;
  }

  var errors = [];

  const totalLoan = totalLoanInput === undefined ? 0 : parseInt(totalLoanInput);

  var listFee = 0;
  if (listPrice < 1000000) {
    listFee = 10000;
  } else if (listPrice < 2000000) {
    listFee = 15000;
  } else {
    listFee = 20000;
  }

  const incentiveCredit = 5000;
  const buyerAgentFeeRate = 0.025;
  const escrowFee = 1000;
  const naturalHazardReport = 125;
  const regulationCompliance = 175;
  const surpassSavePer = 0.06;

  const homeWarranty = 550;
  const countyTransferTaxRate = 0.0011;
  const cityTaxRate = 0.0045;
  const hoaDocuments = 350;
  const hoaTransfer = 500;
  const privateTransferFee = 0;

  const buyerAgentFee = listPrice * buyerAgentFeeRate;
  const countyTransferTax = Math.round(listPrice * countyTransferTaxRate);

  const cityTax = Math.round(listPrice * cityTaxRate);

  // var CityTaxRate;
  // var CityTax = 0;
  // const transferTaxItem = TransferTax.find(item => item.City.toUpperCase() === propertyCity.toUpperCase());
  // if (transferTaxItem !== undefined) {
  //   CityTaxRate = transferTaxItem.CityTaxRate;
  //   // countyTransferTaxRate = transferTaxItem.CountyTransferTaxRate;
  //   CityTax = Math.round(listPrice * CityTaxRate);
  //   // countyTransferTax = Math.round(listPrice * countyTransferTaxRate);
  // } else {
  //   errors.push(`Missing transfer tax setting for city ${propertyCity}`);
  // }

  var titleFee;
  const titleFeeItems = TitleFees.filter(
    item => item.ListPrice <= listPrice,
  ).sort((item1, item2) => item2.ListPrice - item1.ListPrice);
  if (titleFeeItems.length > 0) {
    titleFee = titleFeeItems[0].TitleFee;
  } else {
    errors.push(`Missing title fee setting for ${propertyCity}`);
  }

  const surpassNet =
    listPrice -
    (totalLoan +
      listFee +
      incentiveCredit +
      countyTransferTax +
      cityTax +
      escrowFee +
      titleFee +
      naturalHazardReport +
      regulationCompliance +
      homeWarranty);
  const surpassSave =
    listPrice * surpassSavePer + 3000 - listFee - incentiveCredit;
  const advantageNet =
    listPrice -
    (totalLoan +
      listFee +
      buyerAgentFee +
      countyTransferTax +
      cityTax +
      escrowFee +
      titleFee +
      naturalHazardReport +
      regulationCompliance +
      homeWarranty +
      hoaDocuments +
      hoaTransfer);
  const advantageSave =
    listPrice * surpassSavePer + 3000 - listFee - buyerAgentFee;

  const estimatedClosingCosts =
    countyTransferTax +
    cityTax +
    escrowFee +
    titleFee +
    naturalHazardReport +
    regulationCompliance +
    homeWarranty +
    hoaDocuments +
    hoaTransfer +
    privateTransferFee;

  const result = {
    BrokerFees: {
      ListFee: listFee,
      IncentiveCredit: incentiveCredit,
      BuyerAgentFee: buyerAgentFee,
      TotalLoan: totalLoan,
    },
    Taxes: {
      CityTaxRate: cityTaxRate,
      CityTax: cityTax,
      CountyTransferTaxRate: countyTransferTaxRate,
      CountyTransferTax: countyTransferTax,
    },
    ClosingCosts: {
      EscrowFee: escrowFee,
      TitleFee: titleFee,
    },
    RegulatoryRequirements: {
      NaturalHazardReport: naturalHazardReport,
      RegulationCompliance: regulationCompliance,
    },
    Other: {
      HomeWarranty: homeWarranty,
      HoaDocuments: hoaDocuments,
      HoaTransfer: hoaTransfer,
      PrivateTransferFee: privateTransferFee,
    },
    SurpassNet: surpassNet,
    SurpassSave: surpassSave,
    AdvantageNet: advantageNet,
    AdvantageSave: advantageSave,
    EstimatedClosingCosts: estimatedClosingCosts,
    Errors: errors,
  };

  return result;
}

