// @flow
import React, { useEffect, useState, Fragment, memo } from 'react';
import type { Element, AbstractComponent } from 'react';
import { Grid, makeStyles, Typography } from '@material-ui/core';
import {
  getDocumentsData,
  NONE_FILTER,
  searchFilterDocuments,
} from 'components/Documents/helpers';
import { DocumentsTable } from 'components/Documents';
import _isEmpty from 'lodash/isEmpty';
import _get from 'lodash/get';
import {
  ScopeSelector,
  SearchField,
  DocTypesSelector,
  ResetButton,
} from './Filters';
import { Widget } from 'components/Widget';
import DownloadButton from './DownloadButton';
import { useSelectRow, useDownloadDocs } from '../../hooks';
import { toastr } from 'react-redux-toastr';
import qs from 'query-string';
import { useLocation } from 'react-router';

const useStyles = makeStyles(theme => ({
  notFound: {
    padding: '1rem',
  },
  filtersWrapper: {
    padding: '1rem',
    width: '100%',
    height: '100%',
  },
  widget: {
    padding: 0,
  },
}));

type Props = {
  data: Object,
  isSeller: boolean,
  docTypes: Object[],
};

const Documents = (props: Props): Element<'div' | typeof Fragment> => {
  const { data, isSeller, docTypes } = props;
  const location = useLocation();

  const classes = useStyles();
  const [scopes, setScopes] = useState({});
  const [selectedScope, setSelectedScope] = useState('');
  const [search, setSearch] = useState('');
  const [selectedDocTypes, setSelectedDocTypes] = useState([]);
  const queryString = qs.parse(_get(location, ['search'], ''), {
    ignoreQueryPrefix: true,
  });
  const scopeId = _get(queryString, ['scopeId'], '');

  const { selected, isSelected, handleSelect, handleSelectAll, clearSelected } =
    useSelectRow('ObjectKey');
  const { doDownloadDocs, isDownloading, error } = useDownloadDocs();

  useEffect(() => {
    const validData = getDocumentsData({ data, isSeller }) || {};
    const validScopes = Object.keys(validData) || [];
    const defaultSelectedScopes =
      scopeId || (validScopes.length === 1 ? validScopes[0] : NONE_FILTER);
    setScopes(validData);
    setSelectedScope(defaultSelectedScopes);
  }, [data, isSeller, scopeId]);

  useEffect(() => {
    clearSelected();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [selectedScope]);

  useEffect(() => {
    const { message } = error || {};
    if (message) {
      toastr.error('Something went wrong', message);
    }
  }, [error]);

  const getDocs = () => {
    if (selectedScope === NONE_FILTER) return [];

    const rawDocs = _get(scopes, [selectedScope, 'Docs'], []);
    if (_isEmpty(rawDocs)) return [];
    return searchFilterDocuments(rawDocs, {
      search,
      docTypes: selectedDocTypes,
    });
  };

  const onResetFilters = () => {
    setSearch('');
    setSelectedDocTypes([]);
  };

  const handleMultipleDownload = () => {
    if (_isEmpty(selected)) return;

    doDownloadDocs(selected);
  };

  const documents = getDocs();

  return (
    <>
      {!_isEmpty(scopes) && (
        <Widget className={classes.widget}>
          <Grid
            container
            direction="row"
            justifyContent="flex-start"
            alignItems="stretch"
            spacing={1}
            className={classes.filtersWrapper}
          >
            <ScopeSelector
              scopes={scopes}
              selectedScope={selectedScope}
              setSelectedScope={setSelectedScope}
              isSeller={isSeller}
            />
            <SearchField search={search} setSearch={setSearch} />
            <DocTypesSelector
              docTypes={docTypes}
              selectedDocTypes={selectedDocTypes}
              setSelectedDocTypes={setSelectedDocTypes}
            />
            <ResetButton onResetFilters={onResetFilters} />
            {!_isEmpty(selected) && (
              <DownloadButton
                onDownload={handleMultipleDownload}
                isDownloading={isDownloading}
              />
            )}
          </Grid>
        </Widget>
      )}
      <Widget className={classes.widget}>
        {!_isEmpty(documents) && (
          <DocumentsTable
            documents={documents}
            doDownloadDocs={doDownloadDocs}
            isDownloading={isDownloading}
            selected={selected}
            isSelected={isSelected}
            handleSelect={handleSelect}
            handleSelectAll={handleSelectAll}
          />
        )}
        {_isEmpty(documents) && (
          <Grid container className={classes.notFound}>
            <Typography variant="h3">No document found</Typography>
          </Grid>
        )}
      </Widget>
    </>
  );
};

export default (memo(Documents): AbstractComponent<Props>);
