import 'react-app-polyfill/ie9';
import React from 'react';
import ReactDOM from 'react-dom';
import { Route, HashRouter as Router, Switch } from 'react-router-dom';
import {
  APP_PATHS,
  SENTRY_ENV,
  SENTRY_DSN,
  ENABLE_CALENDAR,
} from './shared/Consts';
import Dashboard from './containers/NewDashboard/Dashboard';
import PropertyDashboard from './containers/SellerProperty/Dashboard';
import OfferStep from './containers/OfferStep';
import PropertyList from './containers/Property/List';
import * as serviceWorker from './serviceWorker';
import { Provider } from 'react-redux';
import ReduxToastr from 'react-redux-toastr';
import store from './reducer';
import * as Sentry from '@sentry/react';
import { ThemeProvider } from '@material-ui/core/styles';
import { theme } from 'utils/Theme';
import { ApolloProvider } from '@apollo/client';
import { client } from 'dataProvider/client';
import { UserProvider } from 'contexts/UserContext';
import { OfferPage } from './containers/OfferPage';
import CalendarPage from './containers/CalendarPage';
import DocumentsPage from './containers/DocumentsPage';
import OfferDetailsPage from './containers/OfferDetailsPage';
import SellerCalendarPage from './containers/SellerCalendarPage';
import Redirect from 'components/Redirect';
import './css/common.css';
import './css/nilehome.css';
import 'react-redux-toastr/lib/css/react-redux-toastr.min.css';
import 'react-block-ui/style.css';
import 'loaders.css/loaders.min.css';

Sentry.init({ dsn: SENTRY_DSN, environment: SENTRY_ENV });

const WithProvider = () => (
  <ApolloProvider client={client}>
    <Provider store={store}>
      <div>
        <ReduxToastr
          timeOut={4000}
          newestOnTop={false}
          preventDuplicates
          position="top-right"
          getState={state => state.toastr} // This is the default
          transitionIn="fadeIn"
          transitionOut="fadeOut"
          progressBar={false}
          closeOnToastrClick
        />
      </div>
      <ThemeProvider theme={theme}>
        <UserProvider>
          <Router>
            <Switch>
              <Route exact path={APP_PATHS.dashboard} component={Dashboard} />
              <Route exact path={APP_PATHS.offer} component={OfferStep} />
              <Route
                exact
                path={APP_PATHS.property}
                component={PropertyDashboard}
              />
              <Route
                exact
                path={APP_PATHS.properties}
                component={PropertyList}
              />
              <Route path={APP_PATHS.offers} component={OfferPage} />
              <Route path={APP_PATHS.redirect} component={Redirect} />
              {ENABLE_CALENDAR && (
                <Route
                  path={`${APP_PATHS.calendar}/:profileId`}
                  component={CalendarPage}
                />
              )}
              {ENABLE_CALENDAR && (
                <Route
                  path={APP_PATHS.sellerCalendar}
                  component={SellerCalendarPage}
                />
              )}
              <Route
                exact
                path={APP_PATHS.documents}
                component={DocumentsPage}
              />
              <Route
                exact
                path={APP_PATHS.offerDetails}
                component={OfferDetailsPage}
              />
            </Switch>
          </Router>
        </UserProvider>
      </ThemeProvider>
    </Provider>
  </ApolloProvider>
);
ReactDOM.render(<WithProvider />, document.getElementById('root'));
serviceWorker.unregister();
