// @flow
import React, { useContext, useEffect } from 'react';
import type { Element } from 'react';
import UserContext from 'contexts/UserContext';
import { Grid, makeStyles } from '@material-ui/core';
import { PageWrapper } from 'components/PageWrapper';
import { Widget } from 'components/Widget';
import BlockUi from 'react-block-ui';
import { Loader } from 'react-loaders';
import clsx from 'clsx';
import withCustomAuthenticator from '../Authenticator/withCustomAuthenticator';
import { useQuery } from '@apollo/client';
import {
  GET_SELLER_DOCUMENTS,
  GET_BUYER_DOCUMENTS,
  GET_DOC_TYPES,
} from 'graphql/queries/documents';
import { toastr } from 'react-redux-toastr';
import { Documents } from 'components/Documents';
import _get from 'lodash.get';

const useStyles = makeStyles(theme => ({
  container: {
    minHeight: '56px',
  },
  widget: {
    padding: 0,
  },
  notFound: {
    padding: '1rem',
  },
}));

const DocumentsPage = (): Element<'div'> => {
  const classes = useStyles();
  const { IsSeller: isSeller } = useContext(UserContext);

  const {
    loading: fetchLoading,
    data,
    errors: fetchError,
  } = useQuery(isSeller ? GET_SELLER_DOCUMENTS : GET_BUYER_DOCUMENTS, {
    errorPolicy: 'all',
    skip: isSeller === undefined,
  });

  const {
    loading: fetchDocTypes,
    data: docTypesData,
    errors: docTypesError,
  } = useQuery(GET_DOC_TYPES, {
    errorPolicy: 'all',
    skip: isSeller === undefined,
  });

  useEffect(() => {
    const { message } = fetchError || {};
    const { messageOocTypes } = docTypesError || {};
    if (message || messageOocTypes) {
      toastr.error('Something went wrong', message || messageOocTypes);
    }
  }, [fetchError, docTypesError]);

  const loading = isSeller === undefined || fetchLoading || fetchDocTypes;
  const docTypes = _get(docTypesData, 'docs_GetDocTypes', []);

  return (
    <PageWrapper>
      <Grid container justifyContent="center">
        <Grid item xs={12}>
          <Widget className={classes.widget}>
            <BlockUi
              tag="div"
              blocking={loading}
              loader={
                <Loader active type="ball-pulse" color="rgba(231,28,99,1)" />
              }
              className={clsx(loading && classes.container)}
            >
              {!loading && (
                <Documents
                  data={data}
                  isSeller={isSeller}
                  fetchLoading={loading}
                  docTypes={docTypes}
                />
              )}
            </BlockUi>
          </Widget>
        </Grid>
      </Grid>
    </PageWrapper>
  );
};

export default (withCustomAuthenticator(DocumentsPage): Element<'div'>);
