// @flow
import React, { memo } from 'react';
import type { AbstractComponent } from 'react';
import _ from 'lodash';
import {
  TextField,
  Grid,
  makeStyles,
  FormControl,
  Typography,
} from '@material-ui/core';
import Autocomplete from '@material-ui/lab/Autocomplete';
import { hideIfInvalidProps } from 'utils/RenderComponent';
import _isEmpty from 'lodash/isEmpty';

type Props = {
  docTypes: Object[],
  selectedDocTypes: string[],
  setSelectedDocTypes: Function,
};

const useStyles = makeStyles(theme => ({
  formControl: {
    height: '100%',
  },
  autocomplete: {
    borderRadius: 'unset',
  },
  option: {
    fontFamily: theme.typography.fontFamily.avenirLT,
    fontSize: '0.875rem',
  },
}));

const checkProps = ({ docTypes }) => docTypes && !_isEmpty(docTypes);

const DoctypesSelector = ({
  docTypes,
  selectedDocTypes,
  setSelectedDocTypes,
}: Props) => {
  const classes = useStyles();

  return (
    <Grid item xs={12} lg={4}>
      <FormControl
        variant="outlined"
        size="small"
        fullWidth
        className={classes.formControl}
      >
        <Autocomplete
          classes={{ inputRoot: classes.autocomplete }}
          value={selectedDocTypes}
          onChange={(event, newValues) => {
            setSelectedDocTypes(_.uniqBy(newValues, 'id'));
          }}
          size="small"
          fullWidth
          multiple
          limitTags={1}
          options={docTypes}
          getOptionLabel={tag => _.get(tag, 'name', '')}
          renderOption={tag => (
            <Typography className={classes.option}>
              {_.get(tag, 'name', '')}
            </Typography>
          )}
          defaultValue={[]}
          renderInput={params => (
            <TextField
              {...params}
              label="Type"
              variant="outlined"
              multiline={false}
              className={classes.option}
            />
          )}
        />
      </FormControl>
    </Grid>
  );
};

export default (hideIfInvalidProps(checkProps)(
  memo(DoctypesSelector),
): AbstractComponent<Props>);
