// @flow
import * as React from 'react';
import { useEffect } from 'react';
import Engine from 'components/FormBuilder/EngineLoadable';
import _isEmpty from 'lodash/isEmpty';
import { toastr } from 'react-redux-toastr';
import { SURVEYS_ERROR_MESSAGE } from './Messages';
import _get from 'lodash/get';
import { getFormMode } from 'helpers/SurveyJS';
import BlockUi from 'react-block-ui';
import { useQuery } from '@apollo/client';
import { GET_FORM_TEMPLATE_AND_DATA } from 'graphql/queries/forms';

type Props = {|
  onCompleteHandler?: Function,
  onSaveExitHandler?: Function,
  onPageChangedHandler?: Function,
  onFilesUploadHandler?: Function,
  itemId: String,
  formId: String,
  onAfterRenderSurveyHandler?: Function,
  isProcessing?: boolean,
|};

export const FormBuilder = (
  props: Props,
): React.Element<typeof React.Fragment> => {
  const {
    formId,
    itemId,
    onCompleteHandler,
    onSaveExitHandler,
    onPageChangedHandler,
    onFilesUploadHandler,
    onAfterRenderSurveyHandler,
    isProcessing,
  } = props;

  const {
    data,
    error,
    loading: isFetchingForm,
  } = useQuery(GET_FORM_TEMPLATE_AND_DATA, {
    variables: {
      FormId: formId,
      ItemId: itemId,
    },
  });

  useEffect(() => {
    if (error) {
      toastr.error(SURVEYS_ERROR_MESSAGE.COMMON);
    }
  }, [error]);

  const formData = _get(data, 'formdata_GetFormData', {});
  const form = _get(formData, 'Form', {});
  const formType = _get(form, ['FormTypeId'], '');
  const template = _get(form, ['Template'], '');
  const formSavedData = _get(formData, ['FormData', 'DataValue'], '');
  const mode = getFormMode(_get(formData, ['FormData', 'DataStatus'], ''));
  const formProperty = _get(formData, 'FormProperty', '');

  return (
    <BlockUi tag="div" blocking={isFetchingForm || isProcessing}>
      {!_isEmpty(template) && !isFetchingForm && (
        <Engine
          formType={formType}
          formProperty={formProperty}
          mode={mode}
          formTemplate={template}
          onCompleteHandler={onCompleteHandler}
          onSaveExitHandler={onSaveExitHandler}
          onPageChangedHandler={onPageChangedHandler}
          onFilesUploadHandler={onFilesUploadHandler}
          formData={formSavedData}
          onAfterRenderSurveyHandler={onAfterRenderSurveyHandler}
        />
      )}
    </BlockUi>
  );
};

FormBuilder.defaultProps = {
  onSaveExitHandler: () => {},
  onPageChangedHandler: () => {},
  onCompleteHandler: () => {},
};

export default FormBuilder;
