import { gql } from '@apollo/client';

const SAVE_FORM_DATA = gql`
  mutation saveFormData(
    $FormData: AWSJSON!
    $FormId: String!
    $ItemId: String!
  ) {
    formdata_SaveFormData(
      FormData: $FormData
      FormId: $FormId
      ItemId: $ItemId
    ) {
      urls {
        id
        url
      }
    }
  }
`;

const SAVE_SUBMIT_FORM_DATA = gql`
  mutation submitFormData(
    $FormData: AWSJSON!
    $FormId: String!
    $ItemId: String!
  ) {
    formdata_SubmitFormData(
      FormData: $FormData
      FormId: $FormId
      ItemId: $ItemId
    ) {
      urls {
        id
        url
      }
    }
  }
`;

export { SAVE_FORM_DATA, SAVE_SUBMIT_FORM_DATA };
