import React from 'react';
import * as Sentry from '@sentry/react';
import './Style.css';

export const withErrorBoundary = ChildComponent => {
  const Wrapped = props => (
    <Sentry.ErrorBoundary
      fallback={
        <div className="error-boundary-block">
          <button
            className="save_btn animated_fastest"
            type="button"
            onClick={() => {
              Sentry.showReportDialog();
            }}
          >
            Report feedback
          </button>
        </div>
      }
      showDialog
    >
      <ChildComponent {...props} />
    </Sentry.ErrorBoundary>
  );

  return Wrapped;
};
