// @flow
import React from 'react';
import type { Element } from 'react';
import { ToggleButton, ToggleButtonGroup } from '@material-ui/lab';
import _get from 'lodash.get';
import { makeStyles } from '@material-ui/core';

const useStyles = makeStyles(theme => ({
  group: {
    width: '100%',
    flexWrap: 'wrap',
  },
  button: {
    flex: '1 0 16%',
    maxWidth: '16%',
    color: theme.palette.primary.dark,
    padding: theme.spacing(1),
    borderRadius: '4px',
    marginRight: theme.spacing(1),
    marginTop: theme.spacing(1),
    [theme.breakpoints.down('md')]: {
      flex: '1 0 25%',
      maxWidth: 'calc(25% - 8px)',
    },
    [theme.breakpoints.down('xs')]: {
      flex: '1 0 50%',
      maxWidth: 'calc(50% - 8px)',
    },
    '&&': {
      borderColor: theme.palette.primary.main,
      borderWidth: '1px',
      borderRadius: '0.25rem',
    },
    '&:focus, &:hover': {
      '&&': {
        borderColor: theme.palette.primary.main,
      },
    },
  },
  label: {
    ...theme.typography.subtitle1,
    textAlign: 'left',
  },
  selected: {
    '&&, &&:hover': {
      borderColor: theme.palette.primary.main,
      backgroundColor: theme.palette.primary.main,
      color: theme.palette.common.white,
    },
  },
}));

type Props = {
  value: string,
  properties: Object[],
  onSelectProperty?: Function,
};

export const PropertyButtonGroup = ({
  value,
  onSelectProperty = () => {},
  properties,
}: Props): Element<typeof ToggleButtonGroup> => {
  const classes = useStyles();
  const onChange = (event, newId) => {
    if (!newId) return;
    const newProperty = properties.find(({ PropertyId: id }) => id === newId);
    onSelectProperty(newProperty);
  };

  return (
    <ToggleButtonGroup
      value={value}
      onChange={onChange}
      className={classes.group}
      exclusive
    >
      {properties.map(property => (
        <ToggleButton
          key={property.PropertyId}
          value={property.PropertyId}
          classes={{
            root: classes.button,
            label: classes.label,
            selected: classes.selected,
          }}
        >
          {_get(property, 'PropertyData.FullAddress')}
        </ToggleButton>
      ))}
    </ToggleButtonGroup>
  );
};
