// @flow
import React, { memo } from 'react';
import type { Element, AbstractComponent } from 'react';
import { makeStyles, Grid, Typography } from '@material-ui/core';
import type { Offer as TOffer } from 'types/Offer';
import { hideIfInvalidProps } from 'utils/RenderComponent';
import _isEmpty from 'lodash/isEmpty';
import { Widget } from 'components/Widget';
import {
  getDetailFields,
  getFurnitureField,
  getFullAddressField,
} from './helpers';

type Props = {
  offer: TOffer,
  isSeller: Boolean,
};

const useStyles = makeStyles(theme => ({
  field: {
    marginBottom: theme.spacing(1.5),
  },
  fieldLabel: {
    marginBottom: theme.spacing(1),
    color: theme.palette.primary.gray,
    textTransform: 'uppercase',
  },
  fieldValue: {
    color: theme.palette.text.secondaryDark,
  },
}));

const checkProps = ({ offer }) => !_isEmpty(offer);

const Details = ({ offer, isSeller }: Props): Element<'div'> => {
  const classes = useStyles();
  const fields = getDetailFields(offer, isSeller);
  const funitures = getFurnitureField(offer);
  const address = getFullAddressField(offer);

  return (
    <Widget title="Offer Details">
      <Grid container>
        <Grid item xs={12} className={classes.field}>
          <Typography variant="h6" className={classes.fieldLabel}>
            {address.label}
          </Typography>
          <Typography variant="h5" className={classes.fieldValue}>
            {address.value}
          </Typography>
        </Grid>
        {fields.map(({ label, value }) => (
          <Grid item xs={6} key={label} className={classes.field}>
            <Typography variant="h6" className={classes.fieldLabel}>
              {label}
            </Typography>
            <Typography variant="h5" className={classes.fieldValue}>
              {value}
            </Typography>
          </Grid>
        ))}
        <Grid item xs={12}>
          <Typography variant="h6" className={classes.fieldLabel}>
            {funitures.label}
          </Typography>
          <Typography variant="h5" className={classes.fieldValue}>
            {funitures.value}
          </Typography>
        </Grid>
      </Grid>
    </Widget>
  );
};

export default (hideIfInvalidProps(checkProps)(
  memo(Details),
): AbstractComponent<Props>);
