import React from 'react';
import FooterSaveSubmit from './FooterSaveSubmit';
import * as InputValuesHelper from '../../../shared/InputValuesHelper';
import * as Consts from '../../../shared/Consts';

export default class VestingCompletedByActions extends React.Component {
  render() {
    if (this.props.VestingSuspended) {
      return null;
    }

    if (this.props.NeedCompleteBy !== true) {
      return null;
    }

    const { InputValues } = this.props;

    return (
      <>
        {InputValuesHelper.GetInputValue(
          InputValues,
          'Vesting_Property_Data_Correct',
        ) === Consts.OPTION_YES_VALUE &&
          (InputValues.Vesting_Form_Completed_By === undefined ||
            InputValues.Vesting_Form_Completed_By === '_not_listed_') && (
            <FooterSaveSubmit
              SaveHandler={async () => {
                if (
                  (await this.props.SaveDataHandler(
                    [Consts.DATA_SECTION_PROPERTY_VESTING],
                    true,
                  )) === true
                ) {
                  await this.props.GotoDashboardHandler();
                }
              }}
              PreviousStepHandler={() => {
                this.props.GoToStepHandler(Consts.ListingWelcome);
              }}
              SubmitHandler={async () => {
                await this.props.SubmitDataHandler(
                  [Consts.DATA_SECTION_PROPERTY_VESTING],
                  Consts.STATUS_VESTING_SUSPENDED,
                );
              }}
            />
          )}
      </>
    );
  }
}
