// @flow
import type { Element } from 'react';
import React from 'react';
import _get from 'lodash/get';
import { Button, Grid, makeStyles } from '@material-ui/core';
import { STEPS_AWAITING_SELLER_ACTION } from '../../../shared/Consts';
import { Fragment } from 'react';
import { instructionContent } from './helpers';

const useStyles = makeStyles(theme => ({
  wrapper: {
    justifyContent: 'flex-end',
  },
  acceptButton: {
    borderRadius: 0,
    padding: '0.5625rem 2rem',
    color: theme.palette.common.white,
    borderWidth: '2px',
  },
  declineButton: {
    borderRadius: 0,
    padding: '0.5625rem 2rem',
    color: theme.palette.error.dark,
  },
}));

type Props = {
  offer: Object,
  onAccept: Function,
  onDecline: Function,
  disabled: boolean,
};

export const ActionButtons = ({
  offer,
  onAccept,
  onDecline,
  disabled,
}: Props): Element<typeof Grid | typeof Fragment> => {
  const classes = useStyles();
  const stepId = _get(offer, 'StepId', 0);
  const offerId = _get(offer, 'OfferId', '');

  const isSellerReviewOffer = STEPS_AWAITING_SELLER_ACTION.includes(stepId);

  if (!isSellerReviewOffer) return <></>;

  return (
    <Grid container alignItems="center" className={classes.wrapper}>
      <Grid item>
        <Button
          className={classes.declineButton}
          onClick={() => onDecline(offerId)}
          disabled={disabled}
        >
          {_get(instructionContent, `${stepId}.reject`)}
        </Button>
        <Button
          variant="contained"
          color="primary"
          disableElevation
          className={classes.acceptButton}
          onClick={() => onAccept(offerId, stepId)}
          disabled={disabled}
        >
          {_get(instructionContent, `${stepId}.accept`)}
        </Button>
      </Grid>
    </Grid>
  );
};
