import React from 'react';
import InputerForm from '../../Shared/InputerForm';
import * as Consts from '../../../shared/Consts';
import uuidv4 from 'uuid/v4';

export default class VestingConfirmPropertyData extends React.Component {
  render() {
    const { InputValues, PropertyData } = this.props;

    var propertyAddress, vestingType;
    var ownerNames = [];
    if (PropertyData !== undefined) {
      propertyAddress = Consts.ConvertDTPropertyAddress(PropertyData);
      vestingType = PropertyData.VestingTypeUsed;
      ownerNames = PropertyData.OwnerNames.split('/');
    }

    return (
      <>
        <div className="pro_tab">
          <div className="tab1">Property Information</div>
        </div>
        <div className="b_texts">
          We have found existing records of your property and have automatically
          populated the Title form with this data. As with the Property
          Information section, this information comes from public records, so be
          sure to review it and make sure it looks right to you.
        </div>
        <div className="form_inputs">
          <div className="property_data_row">
            <div className="t_texts property_data_title">Property Address:</div>
            <div className="t_texts property_data_item">{propertyAddress}</div>
          </div>
          <div className="property_data_row">
            <div className="t_texts property_data_title">Vesting Type:</div>
            <div className="t_texts property_data_item">{vestingType}</div>
          </div>

          {ownerNames.map((ownerName, index) => {
            return (
              <div className="property_data_row" key={uuidv4()}>
                <div className="t_texts property_data_title">
                  Owner {index + 1}:
                </div>
                <div className="t_texts property_data_item">
                  {ownerName.trim()}
                </div>
              </div>
            );
          })}
        </div>
        <div className="form_inputs">
          <InputerForm
            {...this.props}
            FieldName="Vesting_Property_Data_Correct"
          />
          <InputerForm
            {...this.props}
            FieldName="Vesting_Property_Address_Override"
          />
        </div>

        {InputValues.Vesting_Property_Data_Correct === Consts.OPTION_NO_VALUE &&
          this.props.VestingSuspended === true && (
            <div className="b_texts" style={{ color: '#000' }}>
              <div>A NILE advisor will be in touch shortly.</div>
            </div>
          )}
      </>
    );
  }
}
