import React from 'react';
import * as Utils from './Utils';
import * as Consts from '../../shared/Consts';
import NumberFormat from 'react-number-format';

export default function TextInputer(props) {
  const {
    FieldName,
    Required,
    FieldDef,
    MaxLength,
    ValueChangeHandler,
    ValidationResults,
    InputValues,
    Periphery,
  } = props;

  // const Type = FieldDef.Type;

  let inputType = FieldDef.Type;
  if (
    FieldDef.Type === Consts.FIELD_TYPE_NUMBER ||
    FieldDef.Type === Consts.FIELD_TYPE_CURRENCY ||
    FieldDef.Type === Consts.FIELD_TYPE_INTEGER
  ) {
    inputType = 'number';
  }

  let attributes = {
    name: FieldName,
    key: FieldName,
    type: inputType === undefined ? 'text' : inputType,
  };

  if (MaxLength !== undefined) {
    attributes = { ...attributes, maxLength: MaxLength };
  }

  let value = '';
  if (InputValues !== undefined && InputValues[FieldName] !== undefined) {
    value = InputValues[FieldName];
  }

  var style = {};
  if (
    FieldDef.Type === Consts.FIELD_TYPE_DATE ||
    FieldDef.Type === Consts.FIELD_TYPE_NUMBER ||
    FieldDef.Type === Consts.FIELD_TYPE_CURRENCY ||
    FieldDef.Type === Consts.FIELD_TYPE_INTEGER
  ) {
    style = { display: 'block' };
  }

  var inputer = (
    <>
      <input
        className={`text_inputer ${Utils.GetClassName(
          ValidationResults,
          FieldName,
          Required,
        )}`}
        onChange={ValueChangeHandler}
        value={value}
        style={style}
        {...attributes}
        disabled={props.ReadOnly === true ? 'disabled' : ''}
      />
    </>
  );
  if (
    FieldDef.Type === Consts.FIELD_TYPE_CURRENCY ||
    FieldDef.Type === Consts.FIELD_TYPE_NUMBER ||
    FieldDef.Type === Consts.FIELD_TYPE_CURRENCY
  ) {
    inputer = (
      <>
        <NumberFormat
          thousandSeparator
          isNumericString
          prefix={FieldDef.Type === Consts.FIELD_TYPE_CURRENCY ? '$' : ''}
          decimalScale={FieldDef.Type === Consts.FIELD_TYPE_CURRENCY ? 2 : 0}
          allowNegative={false}
          className={`text_inputer ${Utils.GetClassName(
            ValidationResults,
            FieldName,
            Required,
          )}`}
          onValueChange={values => {
            const { value } = values;
            ValueChangeHandler(this, FieldName, FieldDef.Type, value);
          }}
          value={value}
          style={style}
          disabled={props.ReadOnly === true ? 'disabled' : ''}
          maxLength="14"
        />
      </>
    );
  }

  return (
    <>
      {Periphery.Title}
      {Periphery.SubTitle}
      {Periphery.TitleComponent}
      {inputer}
      {Periphery.Note}
      {Periphery.SubNote}
      {Periphery.NoteComponent}
      {Utils.GetValidationResult(ValidationResults, FieldName)}
    </>
  );
}
