import React from 'react';
import imageArrowW from '../../../images/arrow_w.svg';

export default class FooterMiddleStep extends React.Component {
  render() {
    const {
      PreviousButtonText,
      SaveButtonText,
      NextButtonText,
      SaveHandler,
      SaveNextHandler,
      PreviousStepHandler,
      SaveNextDisabled,
      HideSaveButton,
    } = this.props;

    return (
      <>
        <div className="prev">
          <div
            className="prev_btn animated_fastest"
            onClick={PreviousStepHandler}
          >
            {PreviousButtonText !== undefined ? PreviousButtonText : 'Previous'}
          </div>
        </div>
        <div className="nexts">
          {HideSaveButton !== true && (
            <div className="save_btn animated_fastest" onClick={SaveHandler}>
              {SaveButtonText !== undefined ? SaveButtonText : 'Save & Exit'}
            </div>
          )}
          {SaveNextDisabled !== true && (
            <div
              className="next_btn animated_fastest"
              onClick={SaveNextHandler}
            >
              {NextButtonText !== undefined ? NextButtonText : 'Next'}
              <div className="lm_arrow">
                <img src={imageArrowW} width="100%" alt="Arrow W" />
              </div>
            </div>
          )}

          {SaveNextDisabled === true && (
            <div className="next_btn btn_disabled animated_fastest">
              {NextButtonText !== undefined ? NextButtonText : 'Next'}
              <div className="lm_arrow">
                <img src={imageArrowW} width="100%" alt="Arrow W" />
              </div>
            </div>
          )}
        </div>
        <div className="clearer" />
      </>
    );
  }
}
