import React from 'react';
import * as Consts from '../../../shared/Consts';
import imageMarker from '../../../images/marker.svg';
import FooterMiddleStep from './FooterMiddleStep';
import FooterMiddleStepReadOnly from './FooterMiddleStepReadOnly';
import InputerForm from '../../Shared/InputerForm';
import * as FieldDef from '../../../shared/FieldDefinition/PropertyListing';
import ClosingCostsCalc from '../../../shared/ClosingCostsCalc';
import numeral from 'numeral';

export default class ListingVerification extends React.Component {
  render() {
    const { PropertyData, InputValues } = this.props;

    if (this.props.SelectedTab !== Consts.ListingVerification) {
      return null;
    }

    const FieldProps = {
      ...this.props,
      FieldDefs: FieldDef.FieldDefinitions,
    };

    const primaryPhone = InputValues.Listing_LMH_698_354_Primary_Phone;
    const primaryEmail = InputValues.Listing_LMH_698_362_Email_Address;
    const listPrice = InputValues.Listing_LMF_432_List_Price;
    var mailingAddress = 'Same as the property address';
    if (
      InputValues.Listing_LMH_385_MailingAddressSameAsPropertyAddress ===
      Consts.OPTION_NO_VALUE
    ) {
      mailingAddress = `${InputValues.Listing_LMH_257_612_Street_Address}, `;
      mailingAddress += `${InputValues.Listing_LMH_257_992_City}, `;
      mailingAddress += `${InputValues.Listing_LMH_257_992_State} ${InputValues.Listing_LMH_257_677_Postal_Code}`;
    }

    var streetAddress;
    var city;
    var state;
    var zip;
    // var vestingType;
    var ownerNames = [];

    if (PropertyData !== undefined) {
      zip = '';
      if (PropertyData.Zip9 !== null && PropertyData.Zip9 !== undefined) {
        const zipSections = PropertyData.Zip9.split('-');
        if (zipSections.length > 0) {
          zip = zipSections[0];
        }
      }
      streetAddress = PropertyData.StreetAddress;
      city = PropertyData.City;
      state = PropertyData.State;

      // vestingType = PropertyData.VestingTypeUsed;
      ownerNames = PropertyData.OwnerNames.split('/');
    }

    const closingCosts = ClosingCostsCalc(
      listPrice,
      InputValues.Listing_LMF_088_Total_Loans,
      city,
    );

    var ListFee = '-';
    var EstimatedClosingCosts = '-';

    if (closingCosts !== undefined) {
      ListFee = closingCosts.BrokerFees.ListFee;
      EstimatedClosingCosts = closingCosts.EstimatedClosingCosts;
    }

    return (
      <>
        <div id="step4" className="main_form_info steppers">
          <div className="title2">
            <div className="number_title">04</div> Confirmation
          </div>
          {/* <div className='b_texts' style={{maxWidth: 'none'}}>Please review your information below. Once you are done reviewing, please submit your information</div> */}
          <div className="listing_confirm_container">
            <div className="listing_confirm_left_panel">
              <div className="b_texts">
                Please confirm that the data below is correct for your property
                listing.
              </div>
              <div
                className="address animated_fastest"
                style={{
                  marginBottom: '20px',
                  position: 'sticky -webkit-sticky',
                  top: '0',
                }}
              >
                {ownerNames.map((ownerName, index) => {
                  return <div key={index}>{ownerName.trim()}</div>;
                })}
                <div className="add_2">{streetAddress}</div>
                <div className="add_2">
                  <div className="marker">
                    <img src={imageMarker} width="100%" alt="Marker" />
                  </div>
                  <span>
                    {city}, {state} {zip}
                  </span>
                </div>
              </div>
              <div className="listing_confirm_row">
                <div className="t_texts listing_confirm_title">
                  Your Listing Price:{' '}
                </div>
                <div
                  className="t_texts listing_confirm_item"
                  style={{ textAlign: 'right' }}
                >
                  {numeral(listPrice).format('$0,0')}
                </div>
              </div>
              <div className="listing_confirm_row">
                <div className="t_texts listing_confirm_title">
                  Nile List Fee:{' '}
                </div>
                <div
                  className="t_texts listing_confirm_item"
                  style={{ textAlign: 'right' }}
                >
                  {numeral(ListFee).format('$0,0')}
                </div>
              </div>
              <div className="listing_confirm_row">
                <div className="t_texts listing_confirm_title">
                  Estimated Closing Costs *:{' '}
                </div>
                <div
                  className="t_texts listing_confirm_item"
                  style={{ textAlign: 'right' }}
                >
                  {numeral(EstimatedClosingCosts).format('$0,0')}
                </div>
              </div>
              <div className="listing_confirm_row">
                <div className="t_texts listing_confirm_title">
                  Listing Period:{' '}
                </div>
                <div
                  className="t_texts listing_confirm_item"
                  style={{ textAlign: 'right' }}
                >
                  90 Days
                </div>
              </div>
              <div>
                * Closing cost subject to change based on buyer and any HOA
                costs.
              </div>
            </div>
            <div className="listing_confirm_right_panel">
              <div className="b_texts">
                Please confirm that the data below is correct for your personal
                information.
              </div>
              <div className="listing_confirm_row">
                <div className="t_texts listing_confirm_title">
                  Primary Phone:{' '}
                </div>
                <div className="t_texts listing_confirm_item">
                  {primaryPhone}
                </div>
              </div>
              <div className="listing_confirm_row">
                <div className="t_texts listing_confirm_title">
                  Email Address:{' '}
                </div>
                <div className="t_texts listing_confirm_item">
                  {primaryEmail}
                </div>
              </div>
              <div className="listing_confirm_row">
                <div className="t_texts listing_confirm_title">
                  Mailing Address:{' '}
                </div>
                <div className="t_texts listing_confirm_item">
                  {mailingAddress}
                </div>
              </div>
            </div>
          </div>

          <div className="reveals animated_fastest">
            <div className="pro_tab alternatess">
              <div className="tab1">Authorization</div>
            </div>
            <div className="form_inputs" style={{ marginBottom: '50px' }}>
              <div className="required required-options">
                <InputerForm
                  {...FieldProps}
                  FieldName="Agreed_Nile_Listing_Agreement"
                />
              </div>
              <div className="required required-options">
                <InputerForm
                  {...FieldProps}
                  FieldName="Agreed_Disclosure_Regarding_Real_Estate_Agency_Relationship"
                />
              </div>
              <div className="required required-options">
                <InputerForm
                  {...FieldProps}
                  FieldName="Agreed_NILE_Seller_Advisory"
                />
              </div>
            </div>
          </div>

          {this.props.ReadOnly !== true && (
            <>
              <FooterMiddleStep
                NextButtonText="Submit"
                SaveHandler={async () => {
                  if (
                    (await this.props.SaveDataHandler(DataSections, true)) ===
                    true
                  ) {
                    await this.props.GotoDashboardHandler();
                  }
                }}
                PreviousStepHandler={() => {
                  this.props.SwitchTabHandler(this.props.SelectedTab - 1);
                }}
                SaveNextHandler={async () => {
                  if (
                    (await this.props.SubmitDataHandler(
                      DataSections,
                      Consts.STATUS_LISTING_SUBMITTED,
                    )) === true
                  ) {
                    this.props.SwitchTabHandler(this.props.SelectedTab + 1);
                  }
                }}
              />
            </>
          )}

          {this.props.ReadOnly === true && (
            <FooterMiddleStepReadOnly {...this.props} />
          )}
        </div>
      </>
    );
  }
}

const DataSections = [Consts.DATA_SECTION_PROPERTY_LMA];
