import React from 'react';
import * as FieldDef from '../../../shared/FieldDefinition/PropertyListing';
import * as InputHelper from '../../Shared/InputHelper';
import * as InputValuesHelper from '../../../shared/InputValuesHelper';
import * as Consts from '../../../shared/Consts';

export default class VestingVerification extends React.Component {
  render() {
    if (this.props.NeedVerification !== true) {
      return null;
    }

    if (
      this.props.InputValues.Vesting_Property_Data_Correct !==
      Consts.OPTION_YES_VALUE
    ) {
      return null;
    }

    const { InputValues, VestingType } = this.props;

    const FieldProps = {
      ...this.props,
      FieldDefs: FieldDef.FieldDefinitions,
    };

    return (
      <>
        {InputValuesHelper.GetInputValue(
          InputValues,
          'Vesting_Property_Data_Correct',
        ) === Consts.OPTION_YES_VALUE &&
          VestingType.toUpperCase() === 'LLC' && (
            <div className="form_inputs">
              <div className="inputer_form halfs">
                {InputHelper.GenerateInput({
                  ...FieldProps,
                  FieldName: 'Vesting_Verified_LLC',
                })}
              </div>
            </div>
          )}

        {InputValuesHelper.GetInputValue(
          InputValues,
          'Vesting_Property_Data_Correct',
        ) === Consts.OPTION_YES_VALUE &&
          VestingType.toUpperCase() === 'Corporation'.toUpperCase() && (
            <div className="form_inputs">
              <div className="inputer_form halfs">
                {InputHelper.GenerateInput({
                  ...FieldProps,
                  FieldName: 'Vesting_Verified_Corporation',
                })}
              </div>
            </div>
          )}

        {InputValuesHelper.GetInputValue(
          InputValues,
          'Vesting_Property_Data_Correct',
        ) === Consts.OPTION_YES_VALUE &&
          VestingType.toUpperCase() === 'Partnership'.toUpperCase() && (
            <div className="form_inputs">
              <div className="inputer_form halfs">
                {InputHelper.GenerateInput({
                  ...FieldProps,
                  FieldName: 'Vesting_Verified_Partnership',
                })}
              </div>
            </div>
          )}

        {InputValuesHelper.GetInputValue(
          InputValues,
          'Vesting_Property_Data_Correct',
        ) === Consts.OPTION_YES_VALUE &&
          VestingType.toUpperCase() === 'Trust'.toUpperCase() && (
            <div className="form_inputs">
              <div className="inputer_form halfs">
                {InputHelper.GenerateInput({
                  ...FieldProps,
                  FieldName: 'Vesting_Verified_Trustee',
                })}
              </div>
            </div>
          )}

        {(InputValuesHelper.GetInputValue(
          InputValues,
          'Vesting_Verified_LLC',
        ) === Consts.OPTION_YES_VALUE ||
          InputValuesHelper.GetInputValue(
            InputValues,
            'Vesting_Verified_Corporation',
          ) === Consts.OPTION_YES_VALUE ||
          InputValuesHelper.GetInputValue(
            InputValues,
            'Vesting_Verified_Partnership',
          ) === Consts.OPTION_YES_VALUE ||
          InputValuesHelper.GetInputValue(
            InputValues,
            'Vesting_Verified_Trustee',
          ) === Consts.OPTION_YES_VALUE) && (
          <>
            {VestingType.toUpperCase() === 'Trust'.toUpperCase() && (
              <div className="form_inputs">
                <div className="inputer_form thirds">
                  {InputHelper.GenerateInput({
                    ...FieldProps,
                    FieldName: 'Vesting_Trustee_Contact_Person_First_Name',
                  })}
                </div>
                <div className="inputer_form thirds">
                  {InputHelper.GenerateInput({
                    ...FieldProps,
                    FieldName: 'Vesting_Trustee_Contact_Person_Last_Name',
                  })}
                </div>
                <div>
                  <div className="inputer_form halfs">
                    {InputHelper.GenerateInput({
                      ...FieldProps,
                      FieldName: 'Vesting_Trustee_Type',
                    })}
                  </div>
                </div>
              </div>
            )}
            {VestingType.toUpperCase() === 'LLC'.toUpperCase() && (
              <div className="form_inputs">
                <div className="inputer_form thirds">
                  {InputHelper.GenerateInput({
                    ...FieldProps,
                    FieldName: 'Vesting_LLC_Contact_Person_First_Name',
                  })}
                </div>
                <div className="inputer_form thirds">
                  {InputHelper.GenerateInput({
                    ...FieldProps,
                    FieldName: 'Vesting_LLC_Contact_Person_Last_Name',
                  })}
                </div>
                <div>
                  <div className="inputer_form halfs">
                    {InputHelper.GenerateInput({
                      ...FieldProps,
                      FieldName: 'Vesting_LLC_Title',
                    })}
                  </div>
                </div>
              </div>
            )}
            {VestingType.toUpperCase() === 'Corporation'.toUpperCase() && (
              <div className="form_inputs">
                <div className="inputer_form thirds">
                  {InputHelper.GenerateInput({
                    ...FieldProps,
                    FieldName: 'Vesting_Corporation_Contact_Person_First_Name',
                  })}
                </div>
                <div className="inputer_form thirds">
                  {InputHelper.GenerateInput({
                    ...FieldProps,
                    FieldName: 'Vesting_Corporation_Contact_Person_Last_Name',
                  })}
                </div>
                <div>
                  <div className="inputer_form halfs">
                    {InputHelper.GenerateInput({
                      ...FieldProps,
                      FieldName: 'Vesting_Corporation_Title',
                    })}
                  </div>
                </div>
              </div>
            )}
          </>
        )}
        {this.props.VestingSuspended === true &&
          (InputValuesHelper.GetInputValue(
            InputValues,
            'Vesting_Verified_LLC',
          ) === Consts.OPTION_NO_VALUE ||
            InputValuesHelper.GetInputValue(
              InputValues,
              'Vesting_Verified_Corporation',
            ) === Consts.OPTION_NO_VALUE ||
            InputValuesHelper.GetInputValue(
              InputValues,
              'Vesting_Verified_Partnership',
            ) === Consts.OPTION_NO_VALUE ||
            InputValuesHelper.GetInputValue(
              InputValues,
              'Vesting_Verified_Trustee',
            ) === Consts.OPTION_NO_VALUE) && (
            <div className="b_texts" style={{ color: '#000' }}>
              <div>
                Based on the data, we will need to have an Advisor reach out to
                go over the listing.
              </div>
            </div>
          )}
      </>
    );
  }
}
