// @flow
import * as React from 'react';
import { Spinner } from 'react-bootstrap';

type Props = {
  textLoading: string,
};

const Loader = (props: Props): React.Element<typeof Spinner> => {
  const { textLoading } = props;
  return (
    <Spinner animation="border" role="status">
      <span className="sr-only">{textLoading}</span>
    </Spinner>
  );
};

Loader.defaultProps = {
  textLoading: 'Loading...',
};

export default Loader;
