import React from 'react';
import * as Utils from './Utils';

export default class SingleOption extends React.Component {
  render() {
    const {
      FieldName,
      Label,
      AfterLabelLink,
      ValueChangeHandler,
      ValidationResults,
      InputValues,
      Periphery,
    } = this.props;

    let checked = '';
    if (InputValues !== undefined && InputValues[FieldName] !== undefined) {
      const fieldValue = InputValues[FieldName];
      checked = fieldValue === true ? 'checked' : '';
    }

    return (
      <>
        <span className="form-multiple-choise-wrapper__item">
          <input
            type="checkbox"
            id={'checkbox_' + FieldName}
            name={FieldName}
            onChange={event => {
              ValueChangeHandler(this, FieldName, this, event.target.checked);
            }}
            checked={checked}
            disabled={this.props.ReadOnly === true ? 'disabled' : ''}
          />
          {Periphery.Title !== undefined && (
            <label
              className="t_texts"
              style={{
                color: '#111',
                fontSize: '15px',
                paddingLeft: '25px',
                paddingRight: '5px',
                marginRight: '0px',
                marginBottom: '0px',
              }}
              htmlFor={'checkbox_' + FieldName}
            >
              {Periphery.Title}
            </label>
          )}
          {Label !== undefined && (
            <label
              className="t_texts"
              style={{
                color: '#111',
                fontSize: '15px',
                paddingLeft: '25px',
                paddingRight: '5px',
                marginRight: '0px',
                marginBottom: '0px',
              }}
              htmlFor={'checkbox_' + FieldName}
            >
              {Label}
            </label>
          )}
          {AfterLabelLink}
          {Utils.GetValidationResult(ValidationResults, FieldName)}
        </span>
      </>
    );
  }
}
