import React from 'react';
import FooterFirstStep from './FooterFirstStep';
import * as Consts from '../../../shared/Consts';
import { ContactUsForm } from '../../ContactUs/ContactUsForm';

export default class Welcome extends React.Component {
  render() {
    if (this.props.SelectedTab !== Consts.ListingWelcome) {
      return null;
    }

    return (
      <div className="steppers">
        <div className="step_title">Welcome to NILE.</div>
        <div className="step_texts">
          We created this website to streamline the home-selling process. Simply
          fill out all of the modules visible on your Seller Dashboard to move
          forward.
          <div>&nbsp;</div>
          <div>
            You can save your progress at any time and, if you have any
            questions, do not hesitate to{' '}
            <ContactUsForm handleSubmit={this.props.SubmitContactUsHandler}>
              <span className="blue_text">reach out </span>
            </ContactUsForm>
            to us at{' '}
            <a href="mailto:hello@nilehome.com" className="blue_text">
              hello@nilehome.com
            </a>{' '}
            or to your represented NILE Advisor.
          </div>
        </div>

        <FooterFirstStep
          // SaveHandler={() => { this.props.SaveHandler(); }}
          SaveNextHandler={() => {
            this.props.SwitchTabHandler(this.props.SelectedTab + 1);
          }}
        />
      </div>
    );
  }
}
