// @flow
import type { Element } from 'react';
import {
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableRow,
  IconButton,
  makeStyles,
  Checkbox,
} from '@material-ui/core';
import React from 'react';
import { useOrder } from '../../hooks';
import GetAppIcon from '@material-ui/icons/GetApp';
import { Header } from 'components/ListTable';
import { headerCells } from './helpers';
import { getComparator, stableSort } from '../../helpers/Table';
import { formatDate } from 'helpers/Datetime';

const useStyles = makeStyles(theme => ({
  row: {
    ...theme.typography.h5,
    color: theme.palette.text.secondaryDark,
    '&&:hover ,&.Mui-selected': {
      backgroundColor: theme.palette.background.green,
    },
  },
}));

type Props = {
  documents: Object[],
  doDownloadDocs: Function,
  isDownloading: Boolean,
  selected: string[],
  isSelected: Function,
  handleSelect: Function,
  handleSelectAll: Function,
};

const DocumentsTable = ({
  documents,
  doDownloadDocs,
  isDownloading,
  selected,
  isSelected,
  handleSelect,
  handleSelectAll,
}: Props): Element<typeof TableContainer> => {
  const classes = useStyles();
  const { order, orderBy, handleRequestSort } = useOrder('EventTime', 'desc');

  const handleDownloadDocs = (objectKeys: Array<string>) => {
    doDownloadDocs(objectKeys);
  };

  const sortedDocuments = stableSort(documents, getComparator(order, orderBy));

  const handleSelectAllClick = event => {
    handleSelectAll(event.target.checked, sortedDocuments);
  };

  return (
    <TableContainer>
      <Table aria-labelledby="tableTitle" size="small">
        <Header
          cells={headerCells}
          order={order}
          orderBy={orderBy}
          onRequestSort={handleRequestSort}
          rowCount={documents.length}
          numSelected={selected.length}
          onSelectAllClick={handleSelectAllClick}
          align="left"
        />
        <TableBody>
          {sortedDocuments.map((document, index) => {
            const isItemSelected = isSelected(document.ObjectKey);
            const labelId = `enhanced-table-checkbox-${index}`;
            return (
              <TableRow
                hover
                tabIndex={-1}
                key={document.ObjectKey}
                onClick={() => handleSelect(document.ObjectKey)}
                role="checkbox"
                classes={{
                  root: classes.row,
                }}
                aria-checked={isItemSelected}
                selected={isItemSelected}
              >
                <TableCell padding="checkbox">
                  <Checkbox
                    color="primary"
                    checked={isItemSelected}
                    inputProps={{
                      'aria-labelledby': labelId,
                    }}
                  />
                </TableCell>
                <TableCell align="left">{document.File?.name}</TableCell>
                <TableCell align="left">{document.DocTypeName}</TableCell>
                <TableCell align="left">
                  {formatDate(document.EventTime)}
                </TableCell>
                <TableCell align="left" sx={{ pr: 3 }}>
                  <IconButton
                    color="primary"
                    onClick={() => {
                      handleDownloadDocs([document.ObjectKey]);
                    }}
                    disabled={isDownloading}
                  >
                    <GetAppIcon sx={{ fontSize: '1.3rem' }} />
                  </IconButton>
                </TableCell>
              </TableRow>
            );
          })}
        </TableBody>
      </Table>
    </TableContainer>
  );
};

export default DocumentsTable;
