import React from 'react';
import FooterMiddleStep from './FooterMiddleStep';
import FooterMiddleStepReadOnly from './FooterMiddleStepReadOnly';
import * as FieldDef from '../../../shared/FieldDefinition/PropertyListing';
import * as InputHelper from '../../Shared/InputHelper';
import * as Consts from '../../../shared/Consts';
import ClosingCostsCalc from '../../../shared/ClosingCostsCalc';
import Slider from 'rc-slider';
import '../../../css/rc-slider.css';
import numeral from 'numeral';
import { ContactUsForm } from '../../ContactUs/ContactUsForm';

export default class ListingPrice extends React.Component {
  constructor() {
    super();
    this.state = {
      listPrice: undefined,
      listPriceRange: undefined,
      propertyState: undefined,
      propertyCity: undefined,
    };
  }

  componentDidMount() {
    const { ResponseData, InputValues } = this.props;
    var listPriceRange;
    var listPrice = this.state.listPrice;

    if (
      ResponseData !== undefined &&
      ResponseData[Consts.DATA_SECTION_PROPERTY_LIST_PRICE_RANGE] !== undefined
    ) {
      listPriceRange =
        ResponseData[Consts.DATA_SECTION_PROPERTY_LIST_PRICE_RANGE];
      const minRounded = parseInt(Math.ceil(listPriceRange.Min / 1000)) * 1000;
      const maxRounded = parseInt(Math.floor(listPriceRange.Max / 1000)) * 1000;
      const defaultValue =
        parseInt(Math.floor((listPriceRange.Min + listPriceRange.Max) / 2000)) *
        1000;
      listPriceRange = {
        ...listPriceRange,
        MinRounded: minRounded,
        MaxRounded: maxRounded,
        DefaultValue: defaultValue,
      };
    }

    var propertyState, propertyCounty, propertyCity;
    if (
      ResponseData !== undefined &&
      ResponseData[Consts.DATA_SECTION_PROPERTY_DATA] !== undefined
    ) {
      const propertyData = ResponseData[Consts.DATA_SECTION_PROPERTY_DATA];
      propertyState = propertyData.State;
      propertyCounty = propertyData.County;
      propertyCity = propertyData.City;
    }

    if (InputValues.Listing_LMF_432_List_Price !== undefined) {
      listPrice = InputValues.Listing_LMF_432_List_Price;
      if (
        listPriceRange !== undefined &&
        listPrice < listPriceRange.MinRounded
      ) {
        listPrice = undefined;
        this.props.UpdateInputValueHandler(
          'Listing_LMF_432_List_Price',
          undefined,
        );
      } else if (
        listPriceRange !== undefined &&
        listPrice > listPriceRange.MaxRounded
      ) {
        listPrice = undefined;
        this.props.UpdateInputValueHandler(
          'Listing_LMF_432_List_Price',
          undefined,
        );
      }
    } else {
      listPrice =
        listPriceRange !== undefined ? listPriceRange.DefaultValue : undefined;
      this.props.UpdateInputValueHandler(
        'Listing_LMF_432_List_Price',
        listPrice,
      );
    }

    this.setState({
      listPriceRange,
      listPrice,
      propertyState,
      propertyCounty,
      propertyCity,
    });
  }

  render() {
    const { ValidationResults } = this.props;

    const FieldProps = {
      ...this.props,
      FieldDefs: FieldDef.FieldDefinitions,
    };

    if (this.props.SelectedTab !== Consts.ListingPrice) {
      return null;
    }

    const listPrice = this.state.listPrice;
    const listPriceRange = this.state.listPriceRange;

    var listPriceError;
    if (ValidationResults !== undefined) {
      listPriceError = ValidationResults.find(
        item => item.FieldName === 'Listing_LMF_432_List_Price',
      );
    }

    var Errors = [];
    try {
      const closingCosts = ClosingCostsCalc(
        this.state.listPrice,
        this.props.InputValues.Listing_LMF_088_Total_Loans,
        this.state.propertyCity,
      );

      var ListFee = '-';
      var TotalLoan = '-';
      var IncentiveCredit = '-';
      var BuyerAgentFee = '-';

      // var CityTaxRate = '-';
      var CityTax = '-';
      // var CountyTransferTaxRate = '-';
      var CountyTransferTax = '-';

      var EscrowFee = '-';
      var TitleFee = '-';

      var NaturalHazardReport = '-';
      var RegulationCompliance = '-';

      var HomeWarranty = '-';
      var HoaDocuments = '-';
      var HoaTransfer = '-';

      var SurpassNet = '-';
      var SurpassSave = '-';
      var AdvantageNet = '-';
      var AdvantageSave = '-';

      if (closingCosts !== undefined) {
        ListFee = closingCosts.BrokerFees.ListFee;
        IncentiveCredit = closingCosts.BrokerFees.IncentiveCredit;
        BuyerAgentFee = closingCosts.BrokerFees.BuyerAgentFee;
        TotalLoan = closingCosts.BrokerFees.TotalLoan;

        // CityTaxRate = closingCosts.Taxes.CityTaxRate;
        CityTax = closingCosts.Taxes.CityTax;
        // CountyTransferTaxRate = closingCosts.Taxes.CountyTransferTaxRate;
        CountyTransferTax = closingCosts.Taxes.CountyTransferTax;

        EscrowFee = closingCosts.ClosingCosts.EscrowFee;
        TitleFee = closingCosts.ClosingCosts.TitleFee;

        NaturalHazardReport =
          closingCosts.RegulatoryRequirements.NaturalHazardReport;
        RegulationCompliance =
          closingCosts.RegulatoryRequirements.RegulationCompliance;

        HomeWarranty = closingCosts.Other.HomeWarranty;

        HoaDocuments = closingCosts.Other.HoaDocuments;
        HoaTransfer = closingCosts.Other.HoaTransfer;

        SurpassNet = closingCosts.SurpassNet;
        SurpassSave = closingCosts.SurpassSave;
        AdvantageNet = closingCosts.AdvantageNet;
        AdvantageSave = closingCosts.AdvantageSave;

        Errors = closingCosts.Errors;
      }
    } catch (error) {
      Errors.push(error);
    }

    return (
      <>
        <div id="step3" className="main_form_info steppers">
          <div className="title2">
            <div className="number_title">03</div> Listing
          </div>
          <div className="b_texts">
            {InputHelper.GetFieldTitle({
              ...FieldProps,
              FieldName: 'Listing_LMF_811_Label',
            })}
          </div>
          <div className="pro_tab">
            <div className="tab1">Listing Financials</div>
            <div className="tab_divider desktop" />
            <div className="tab2 desktop">
              We use human intelligence and machine learning to determine value.
            </div>
          </div>
          <div
            className="form_inputs greyer mobile"
            style={{ marginLeft: '0', marginBottom: '20px' }}
          >
            <span style={{ color: '#111' }}>Note:</span> We use human
            intelligence and machine learning to determine value.
          </div>
          <div className="form_inputs">
            <div className="t_texts">Choose your listing price</div>
            {listPriceError !== undefined && (
              <>
                <div style={{ color: 'red' }}>
                  {listPriceError.ValidationError}
                </div>
              </>
            )}
            <div className="container list_price_container">
              {listPriceRange !== undefined && (
                <>
                  <Slider
                    disabled={this.props.ReadOnly}
                    step={1000}
                    min={listPriceRange.MinRounded}
                    max={listPriceRange.MaxRounded}
                    defaultValue={listPriceRange.DefaultValue}
                    value={listPrice}
                    onChange={value => {
                      this.setState({ listPrice: value });
                    }}
                    onAfterChange={value => {
                      this.props.UpdateInputValueHandler(
                        'Listing_LMF_432_List_Price',
                        value,
                      );
                    }}
                  />
                  <div className="list_price_slider_tooltips">
                    <div className="list_price_slider_tooltips_item">
                      {listPriceRange.MinFormatted}
                    </div>
                    <div className="list_price_slider_tooltips_item list_price_selected_price">
                      {numeral(listPrice).format('$0,0')}
                    </div>
                    <div className="list_price_slider_tooltips_item">
                      {listPriceRange.MaxFormatted}
                    </div>
                  </div>
                </>
              )}
            </div>

            {this.props.ReadOnly !== true && (
              <div className="t_texts">
                If you want to change the range of your sale price, please click{' '}
                <ContactUsForm
                  handleSubmit={this.props.SubmitContactUsHandler}
                  requestType="List price change request"
                >
                  <span className="blue_text">here</span>
                </ContactUsForm>{' '}
                and send us a request.
              </div>
            )}
          </div>

          <div className="pro_tab alternatess">
            <div className="tab1">Loan Payoff</div>
          </div>

          <div className="form_inputs">
            <div className="inputer_form halfs">
              {InputHelper.GenerateInput({
                ...FieldProps,
                FieldName: 'Listing_LMF_088_Total_Loans',
              })}
            </div>
          </div>

          <div className="pro_tab alternatess">
            <div className="tab1">Transaction Costs</div>
            <div className="tab_divider desktop" />
            <div className="tab2 desktop">
              Breakdown of Selling and Closing Costs
            </div>
          </div>
          <div
            className="form_inputs greyer mobile"
            style={{ marginLeft: '0', marginBottom: '20px' }}
          >
            <span style={{ color: '#111' }}>Note:</span> Estimated Closing
            Costs.
          </div>

          <div className="closing_costs_container">
            <div className="closing_costs_left_panel">
              <div className="form_title2 closing_costs_title2">
                Listing Price and Payoff
              </div>

              <div className="closing_costs_row">
                <div className="closing_costs_title">Your Listing Price: </div>
                <div className="closing_costs_item">
                  {numeral(listPrice).format('$0,0')}
                </div>
              </div>

              <div className="closing_costs_row">
                <div className="closing_costs_title">Loan Payoff: </div>
                <div className="closing_costs_item">
                  {numeral(TotalLoan).format('$0,0')}
                </div>
              </div>

              <div className="form_title2 closing_costs_title2">
                Listing Fee
              </div>

              <div className="closing_costs_row">
                <div className="closing_costs_title">Nile Listing Fee: </div>
                <div className="closing_costs_item">
                  {numeral(ListFee).format('$0,0')}
                </div>
              </div>

              <div className="form_title2 closing_costs_title2">
                Buy Side Charges
              </div>
              <div className="closing_costs_row">
                <div className="closing_costs_title">Incentive Credit: </div>
                <div className="closing_costs_item">
                  {numeral(IncentiveCredit).format('$0,0')}
                </div>
              </div>

              <div className="closing_costs_row">
                <div className="closing_costs_title">OR</div>
              </div>

              <div className="closing_costs_row">
                <div className="closing_costs_title">Buyer Agent Charge</div>
                <div className="closing_costs_item">
                  {numeral(BuyerAgentFee).format('$0,0')}
                </div>
              </div>

              <div className="closing_costs_row">
                <div
                  className="closing_costs_title"
                  style={{ flexBasis: '100%' }}
                >
                  Remember - Buyer Agent Charges only apply if you accept an
                  offer where the buyer has an outside agent. If NILE sells your
                  home direclty, only the Buyer Incentive Fee Applies
                </div>
              </div>

              <div className="form_title2 closing_costs_title2">Taxes</div>
              <div className="closing_costs_row">
                <div className="closing_costs_title">County Transfer Tax</div>
                <div className="closing_costs_item">
                  {numeral(CountyTransferTax).format('$0,0')}
                </div>
              </div>
              <div className="closing_costs_row">
                <div className="closing_costs_title">Estimated City Tax</div>
                <div className="closing_costs_item">
                  {numeral(CityTax).format('$0,0')}
                </div>
              </div>
              <div className="form_title2 closing_costs_title2">
                Closing Costs
              </div>
              <div className="closing_costs_row">
                <div className="closing_costs_title">Escrow Fee</div>
                <div className="closing_costs_item">
                  {numeral(EscrowFee).format('$0,0')}
                </div>
              </div>
              <div className="closing_costs_row">
                <div className="closing_costs_title">Title Fee</div>
                <div className="closing_costs_item">
                  {numeral(TitleFee).format('$0,0')}
                </div>
              </div>
            </div>

            <div className="closing_costs_right_panel">
              <div className="form_title2 closing_costs_title2">
                Regulatory Requirements
              </div>

              <div className="closing_costs_row">
                <div className="closing_costs_title">
                  Natural Hazard Report:
                </div>
                <div className="closing_costs_item">
                  {numeral(NaturalHazardReport).format('$0,0')}
                </div>
              </div>

              <div className="closing_costs_row">
                <div className="closing_costs_title">
                  Regulatory Compliance Certification
                </div>
                <div className="closing_costs_item">
                  {numeral(RegulationCompliance).format('$0,0')}
                </div>
              </div>

              <div className="form_title2 closing_costs_title2">Other</div>

              <div className="closing_costs_row">
                <div className="closing_costs_title">
                  Buyer Post Sale Warranty:
                </div>
                <div className="closing_costs_item">
                  {numeral(HomeWarranty).format('$0,0')}
                </div>
              </div>

              <div className="closing_costs_row">
                <div className="closing_costs_title">
                  HOA Documents (If applicable):
                </div>
                <div className="closing_costs_item">
                  {numeral(HoaDocuments).format('$0,0')}
                </div>
              </div>

              <div className="closing_costs_row">
                <div className="closing_costs_title">
                  HOA Transfer Fee (If applicable):
                </div>
                <div className="closing_costs_item">
                  {numeral(HoaTransfer).format('$0,0')}
                </div>
              </div>

              <div className="closing_costs_row">
                <div className="closing_costs_title">
                  Private Transfer Fee (If applicable)
                </div>
                <div className="closing_costs_item">TBD</div>
              </div>
            </div>
          </div>

          <div className="closing_costs_container closing_costs_box">
            <div className="closing_costs_left_panel">
              <div className="form_title2" style={{ fontSize: '1.3rem' }}>
                Your Proceeds
              </div>
              <div className="closing_costs_row">
                <div className="closing_costs_title">
                  Home Purchased through NILE Buyer
                </div>
              </div>
              <div className="closing_costs_row">
                <div className="closing_costs_title">You Net:</div>
                <div className="closing_costs_item">
                  {numeral(SurpassNet).format('$0,0')}
                </div>
              </div>
              <div className="closing_costs_row">
                <div className="closing_costs_title closing_costs_highlight">
                  You Save:
                </div>
                <div className="closing_costs_item closing_costs_highlight">
                  {numeral(SurpassSave).format('$0,0')}
                </div>
              </div>
            </div>
            <div className="closing_costs_right_panel">
              <div className="form_title2" style={{ fontSize: '1.3rem' }}>
                &nbsp;
              </div>
              <div className="closing_costs_row">
                <div className="closing_costs_title">
                  Home Purchased through outside agent
                </div>
              </div>
              <div className="closing_costs_row">
                <div className="closing_costs_title">You Net:</div>
                <div className="closing_costs_item">
                  {numeral(AdvantageNet).format('$0,0')}
                </div>
              </div>
              <div className="closing_costs_row">
                <div className="closing_costs_title closing_costs_highlight">
                  You Save:
                </div>
                <div className="closing_costs_item closing_costs_highlight">
                  {numeral(AdvantageSave).format('$0,0')}
                </div>
              </div>
            </div>
          </div>

          {this.props.ReadOnly !== true && (
            <FooterMiddleStep
              SaveHandler={async () => {
                if (
                  (await this.props.SaveDataHandler(DataSections, true)) ===
                  true
                ) {
                  await this.props.GotoDashboardHandler();
                }
              }}
              PreviousStepHandler={() => {
                this.props.SwitchTabHandler(this.props.SelectedTab - 1);
              }}
              SaveNextHandler={async () => {
                if (
                  (await this.props.SaveDataHandler(DataSections, false)) ===
                  true
                ) {
                  this.props.SwitchTabHandler(this.props.SelectedTab + 1);
                }
              }}
            />
          )}

          {this.props.ReadOnly === true && (
            <FooterMiddleStepReadOnly {...this.props} />
          )}
        </div>
      </>
    );
  }
}

const DataSections = [Consts.DATA_SECTION_PROPERTY_LMF];
