// @flow
import { withErrorBoundary } from 'components/ErrorBoundary';
import React, { memo } from 'react';
import type { AbstractComponent, Element } from 'react';
import FormBuilder from 'components/FormBuilder';
import {
  formatSurveyData,
  onFilesUploadCallbackHandler,
  uploadFilesToS3,
} from 'helpers/SurveyJS';
import {
  SURVEYS_ERROR_MESSAGE,
  SURVEYS_SUCCESS_MESSAGE,
} from 'components/FormBuilder/Messages';
import { toastr } from 'react-redux-toastr';
import _get from 'lodash/get';
import { useFormSave } from 'hooks';
import _isEmpty from 'lodash/isEmpty';

type Props = {
  formId: number,
  itemId: string,
  saveExitAction: Function,
  onAfterRenderSurveyHandler: Function,
};

// we need use global variable for store files uploaded
let files = [];

const Form = (props: Props): Element<Object> => {
  const [isGoingToExit, setExitStatus] = React.useState(false);
  const [isUploadingFiles, setIsUploadingFiles] = React.useState(false);
  const {
    formId,
    itemId,
    isLoading,
    saveExitAction,
    onAfterRenderSurveyHandler,
  } = props;

  const { doSave, savedData, isSaving, doSubmit, submittedData, isSubmitting } =
    useFormSave();

  React.useEffect(() => {
    const urls =
      _get(submittedData, ['formdata_SubmitFormData', 'urls']) ||
      _get(savedData, ['formdata_SaveFormData', 'urls']) ||
      false;
    const uploadFiles = async () => {
      if (!_isEmpty(urls)) {
        setIsUploadingFiles(true);
        await uploadFilesToS3(files, urls);
        setIsUploadingFiles(false);
        files = [];
      }
    };
    uploadFiles();
    if (isGoingToExit) saveExitAction();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [submittedData, savedData, files, isGoingToExit]);

  const saveForm = async (survey, formTemplate) => {
    const formData = JSON.stringify(formatSurveyData(survey, formTemplate));
    return doSave({
      variables: {
        FormData: formData,
        FormId: formId,
        ItemId: itemId,
      },
    });
  };

  const onSaveExitHandler = async (survey, formTemplate) => {
    try {
      await saveForm(survey, formTemplate);
      setExitStatus(true);
      toastr.success(
        SURVEYS_SUCCESS_MESSAGE.COMMON,
        SURVEYS_SUCCESS_MESSAGE.FORM_DATA_SAVED_SUCCESS,
      );
    } catch {
      toastr.error(SURVEYS_ERROR_MESSAGE.COMMON);
    }
  };

  const onPageChangedHandler = (survey, formTemplate) => {
    return saveForm(survey, formTemplate);
  };

  const onCompleteHandler = async (survey, formTemplate) => {
    try {
      const formData = JSON.stringify(formatSurveyData(survey, formTemplate));
      await doSubmit({
        variables: {
          FormData: formData,
          FormId: formId,
          ItemId: itemId,
        },
      });
      setExitStatus(true);
      toastr.success(
        SURVEYS_SUCCESS_MESSAGE.COMMON,
        SURVEYS_SUCCESS_MESSAGE.FORM_DATA_SAVED_SUCCESS,
      );
    } catch {
      toastr.error(SURVEYS_ERROR_MESSAGE.COMMON);
    }
  };

  const handleUploadFile = file => {
    files.push(file);
  };

  const onFilesUploadHandler = ({ options }) => {
    return onFilesUploadCallbackHandler(options, handleUploadFile);
  };

  const isProcessing =
    isSaving || isSubmitting || isLoading || isUploadingFiles;

  return (
    <div className="container">
      <FormBuilder
        onSaveExitHandler={onSaveExitHandler}
        onPageChangedHandler={onPageChangedHandler}
        onFilesUploadHandler={onFilesUploadHandler}
        onCompleteHandler={onCompleteHandler}
        itemId={itemId}
        formId={formId}
        isProcessing={isProcessing}
        onAfterRenderSurveyHandler={onAfterRenderSurveyHandler}
      />
    </div>
  );
};

export default (withErrorBoundary(memo(Form)): AbstractComponent<Props>);
