// @flow
import React, { useContext, useEffect } from 'react';
import BlockUi from 'react-block-ui';
import { Loader } from 'react-loaders';
import withCustomAuthenticator from '../Authenticator/withCustomAuthenticator';
import Grid from '@material-ui/core/Grid';
import QuestionList from 'components/SellerProperty/QuestionList/Loadable';
import DocumentWidget from 'components/DocumentWidget/Loadable';
import PropertiesStatus from 'components/SellerProperty/PropertiesStatus';
import { PageWrapper } from 'components/PageWrapper';
import Contingencies from 'components/Contingencies';
import BuyerOffersSteps from 'components/BuyerOffersSteps';
import UserContext from 'contexts/UserContext';
import _get from 'lodash/get';
import {
  GET_BUYER_USER_OVERVIEW,
  GET_SELLER_USER_OVERVIEW,
} from 'graphql/queries/user/overview';
import { useQuery } from '@apollo/client';
import { toastr } from 'react-redux-toastr';
import { withErrorBoundary } from 'components/ErrorBoundary';
import type { Element } from 'react';
import type { Offer as TOffer } from 'types/Offer';
import type { Property as TProperty } from 'types/Property';

const Dashboard = () => {
  const user = useContext(UserContext);
  const isSeller = _get(user, 'IsSeller', false);
  const isBuyer = _get(user, 'IsBuyer', false);
  const skip = isBuyer === undefined;
  const {
    data,
    loading: isLoading,
    refetch,
    error,
  } = useQuery(isBuyer ? GET_BUYER_USER_OVERVIEW : GET_SELLER_USER_OVERVIEW, {
    errorPolicy: 'all',
    notifyOnNetworkStatusChange: true,
    fetchPolicy: 'network-only',
    skip,
  });

  const dataQuery = isBuyer
    ? 'getUserOverview.Offers.Items'
    : 'getUserOverview.Properties.Items';

  const offers: Array<TOffer | TProperty> = _get(data, dataQuery, []);

  useEffect(() => {
    const { message } = error || {};

    if (message) {
      toastr.error('Something went wrong', message);
    }
  }, [error]);

  return (
    <BlockUi
      tag="div"
      blocking={isLoading}
      loader={<Loader active type="ball-pulse" color="rgba(231,28,99,1)" />}
    >
      <PageWrapper>
        <Grid container spacing={3} justifyContent="center">
          <Grid item md={7} sm={8} xs={12}>
            {isBuyer && (
              <BuyerOffersSteps
                offers={offers}
                getBuyerOffers={refetch}
                isLoading={isLoading}
              />
            )}
            <Contingencies offers={offers} isLoading={isLoading} />
            {isSeller && <QuestionList />}
          </Grid>
          <Grid item md={3} sm={4} xs={12}>
            {isSeller && (
              <PropertiesStatus data={offers} isLoadingData={isLoading} />
            )}
            <DocumentWidget data={offers} isLoading={isLoading} />
          </Grid>
        </Grid>
      </PageWrapper>
    </BlockUi>
  );
};

export default (withCustomAuthenticator(
  withErrorBoundary(Dashboard),
): Element<'div'>);
