import React, { Component } from 'react';
import { CustomAuth } from './CustomAuth';
import * as Consts from '../../shared/Consts';
import * as AuthUtil from '../../shared/AuthUtil';

export default class AuthWrapper extends Component {
  constructor(props) {
    super(props);
    this.updateUsername = this.updateUsername.bind(this);

    this.state = {
      username: '',
      isAgent: false,
    };
  }

  updateUsername(newUsername) {
    this.setState({ username: newUsername });
  }

  async componentDidMount() {
    const isAgent = await AuthUtil.IsAgent();
    this.setState({ isAgent });
  }

  render() {
    const ChildComponent = this.props.ChildComponent;
    return (
      <>
        <div style={{ minHeight: '85vh' }}>
          <CustomAuth
            authState={this.props.authState}
            updateUsername={this.updateUsername}
            onStateChange={this.props.onStateChange}
            history={this.props.history}
          />
          {this.props.authState === Consts.AUTH_STATUS_SIGNED_IN && (
            <ChildComponent {...this.props} IsAgent={this.state.isAgent} />
          )}
        </div>
      </>
    );
  }
}
