import React from 'react';

export default class FooterSaveSubmit extends React.Component {
  render() {
    const {
      PreviousButtonText,
      SaveButtonText,
      SubmitButtonText,
      SaveHandler,
      SubmitHandler,
      PreviousStepHandler,
    } = this.props;

    return (
      <>
        <div className="prev">
          <div
            className="prev_btn animated_fastest"
            onClick={PreviousStepHandler}
          >
            {PreviousButtonText !== undefined ? PreviousButtonText : 'Previous'}
          </div>
        </div>
        <div className="nexts">
          <div className="save_btn animated_fastest" onClick={SaveHandler}>
            {SaveButtonText !== undefined ? SaveButtonText : 'Save & Exit'}
          </div>
          <div
            className="next_btn btn_disabled animated_fastest"
            onClick={SubmitHandler}
          >
            {SubmitButtonText !== undefined ? SubmitButtonText : 'Submit'}
          </div>
        </div>
        <div className="clearer" />
      </>
    );
  }
}
