export async function GetRequestConfig(Auth) {
  return {
    headers: {
      'Content-Type': HEADER_APPLICATION_JSON,
      Authorization: (await Auth.currentSession()).getIdToken().getJwtToken(),
    },
  };
}

export const ConvertDTPropertyAddress = propertyData => {
  if (_isEmpty(propertyData)) {
    return 'Unknown';
  }

  var zip = '';
  if (propertyData.Zip9 !== null && propertyData.Zip9 !== undefined) {
    const zipSections = propertyData.Zip9.split('-');
    if (zipSections.length > 0) {
      zip = ` ${zipSections[0]}`;
    }
  }
  return `${propertyData.StreetAddress}, ${propertyData.City}, ${propertyData.State}${zip}`;
};

