import { CALENDAR_ACTIONS } from './Constants';
import { convertAppointmentsTime } from '../helpers';

export const setConfig = data => {
  return {
    type: CALENDAR_ACTIONS.setConfig,
    payload: data,
  };
};

export const setStartOfWeek = data => {
  return {
    type: CALENDAR_ACTIONS.setStartOfWeek,
    payload: data,
  };
};

export const setEndOfWeek = data => {
  return {
    type: CALENDAR_ACTIONS.setEndOfWeek,
    payload: data,
  };
};

export const setError = data => {
  return {
    type: CALENDAR_ACTIONS.setError,
    payload: data,
  };
};

export const setIsLoading = data => {
  return {
    type: CALENDAR_ACTIONS.setIsLoading,
    payload: data,
  };
};

export const setProfileId = id => {
  return {
    type: CALENDAR_ACTIONS.setProfileId,
    payload: id,
  };
};

export const setProperty = data => {
  return {
    type: CALENDAR_ACTIONS.setProperty,
    payload: data,
  };
};

export const setAppointments = data => {
  return {
    type: CALENDAR_ACTIONS.setAppointments,
    payload: convertAppointmentsTime(data),
  };
};

export const addAppointments = data => {
  return {
    type: CALENDAR_ACTIONS.addAppointments,
    payload: convertAppointmentsTime(data),
  };
};

export const beginSelectingCell = date => {
  return {
    type: CALENDAR_ACTIONS.beginSelectingCell,
    payload: date,
  };
};

export const stopSelectingCell = () => {
  return {
    type: CALENDAR_ACTIONS.stopSelectingCell,
  };
};

export const addSelectedCell = date => {
  return {
    type: CALENDAR_ACTIONS.addSelectedCell,
    payload: date,
  };
};

export const setClientTimeZone = data => ({
  type: CALENDAR_ACTIONS.setClientTimeZone,
  payload: data,
});
