// @flow
import React, { memo } from 'react';
import type { Element, AbstractComponent } from 'react';
import {
  makeStyles,
  Container,
  Grid,
  AppBar,
  Typography,
  IconButton,
} from '@material-ui/core';
import KeyboardBackspaceIcon from '@material-ui/icons/KeyboardBackspace';
import { useHistory } from 'react-router-dom';
import style from 'components/Header/headerStyle';
import type { Offer as TOffer } from 'types/Offer';
import _get from 'lodash.get';
import { hideIfInvalidProps } from 'utils/RenderComponent';
import _isEmpty from 'lodash/isEmpty';
import SellerActions from './SellerActions';
import BuyerActions from './BuyerActions';
import { APP_PATHS } from 'shared/Consts';

type Props = {
  offer: TOffer,
  isSeller: Boolean,
  refetch: Function,
  isAcceptDisabled: boolean,
  isFetchingOffer: boolean,
};

const useAppBarStyles = makeStyles(style);
const useStyles = makeStyles(theme => ({
  wrapper: {
    marginTop: theme.spacing(1),
    marginBottom: theme.spacing(1),
  },
  leftHeader: {
    display: 'inline-flex',
  },
  backButton: {
    color: theme.palette.common.black,
    padding: 0,
    paddingRight: theme.spacing(1),
  },
}));

const checkProps = ({ offer }) => !_isEmpty(offer);

const Header = ({
  offer,
  isSeller,
  refetch,
  isAcceptDisabled,
  isFetchingOffer,
}: Props): Element<'div'> => {
  const appbarClasses = useAppBarStyles();
  const classes = useStyles();
  const history = useHistory();

  const offerNo = _get(offer, 'OfferNo');
  const offerStep = _get(
    offer,
    isSeller ? 'Step.messages.seller' : 'Step.messages.buyer',
  );

  const onBackClick = () => {
    history.push({
      pathname: isSeller ? APP_PATHS.offers : APP_PATHS.dashboard,
    });
  };

  return (
    <AppBar position="static" color="default" className={appbarClasses.header}>
      <Container className={appbarClasses.container}>
        <Grid
          container
          direction="row"
          justifyContent="space-between"
          alignItems="center"
          className={classes.wrapper}
        >
          <Grid item xs={12} lg={6}>
            <div className={classes.leftHeader}>
              <IconButton
                aria-label="back"
                className={classes.backButton}
                onClick={onBackClick}
              >
                <KeyboardBackspaceIcon />
              </IconButton>
              <Typography variant="h3">{offerNo}</Typography>
            </div>
            <Typography variant="body1">{offerStep}</Typography>
          </Grid>
          <Grid item xs={12} lg={6}>
            {isSeller && (
              <SellerActions
                offer={offer}
                refetch={refetch}
                isFetchingOffer={isFetchingOffer}
              />
            )}
            {!isSeller && (
              <BuyerActions
                offer={offer}
                refetch={refetch}
                isAcceptDisabled={isAcceptDisabled}
                isFetchingOffer={isFetchingOffer}
              />
            )}
          </Grid>
        </Grid>
      </Container>
    </AppBar>
  );
};

export default (hideIfInvalidProps(checkProps)(
  memo(Header),
): AbstractComponent<Props>);
