import { gql } from '@apollo/client';

const GET_SELLER_DOCUMENTS = gql`
  query getUserOverview {
    getUserOverview {
      Properties {
        Items {
          PropertyId
          PropertyData {
            FullAddress
          }
          Docs {
            DocType
            DocTypeName
            EventTime
            File {
              name
            }
            FileId
            ObjectKey
            OfferId
            OfferNo
            PropertyId
          }
        }
      }
    }
  }
`;

const GET_BUYER_DOCUMENTS = gql`
  query getUserOverview {
    getUserOverview {
      Offers {
        Items {
          OfferId
          OfferNo
          Docs {
            DocType
            DocTypeName
            EventTime
            File {
              name
            }
            FileId
            ObjectKey
            OfferId
            OfferNo
            PropertyId
          }
        }
      }
    }
  }
`;

const GET_DOC_DOWNLOAD_URL = gql`
  query GetDocsDownloadUrls($ObjectKeys: [String!]!) {
    docs_GetDownloadUrls(ObjectKeys: $ObjectKeys) {
      url
      name
    }
  }
`;

const GET_DOC_TYPES = gql`
  query GetDocTypes {
    docs_GetDocTypes {
      id
      name
    }
  }
`;

export {
  GET_SELLER_DOCUMENTS,
  GET_BUYER_DOCUMENTS,
  GET_DOC_DOWNLOAD_URL,
  GET_DOC_TYPES,
};
