// @flow
import React, { Fragment } from 'react';
import type { Element, AbstractComponent } from 'react';
import { Grid } from '@material-ui/core';
import { PageWrapper } from 'components/PageWrapper';
import _get from 'lodash.get';
import Header from './Header';
import Instruction from './Instruction';
import Details from './Details';
import History from './History';
import BlockUi from 'react-block-ui';
import { Loader } from 'react-loaders';
import _isEmpty from 'lodash/isEmpty';
import { Widget } from 'components/Widget';
import Typography from '@material-ui/core/Typography';
import {
  isBestPriceOffer,
  getLatestCounter,
  getOfferInstruction,
} from 'helpers/Offer';

type Props = {
  offer: Object,
  isSeller: Boolean,
  loading: Boolean,
  refetch: Function,
};

const OfferDetails = ({
  offer,
  isSeller,
  loading,
  refetch,
}: Props): Element<typeof Fragment> => {
  const isValidOffer = !_isEmpty(offer);
  const offerStepId = _get(offer, 'StepId');
  const counter = getLatestCounter(offer);
  const isBestOffer = isBestPriceOffer(_get(counter, 'IsBestOffer') || []);
  const isAcceptDisabled = !isSeller && isBestOffer;
  const instruction = getOfferInstruction({
    offerStepId,
    isBestOffer,
    isSeller,
  });

  return (
    <>
      <Header
        offer={offer}
        isSeller={isSeller}
        refetch={refetch}
        isAcceptDisabled={isAcceptDisabled}
        isFetchingOffer={loading}
      />
      <PageWrapper>
        <BlockUi
          tag="div"
          blocking={loading}
          loader={<Loader active type="ball-pulse" color="rgba(231,28,99,1)" />}
        >
          {!loading && !isValidOffer && (
            <Widget>
              <Typography variant="h3">Offer not found</Typography>
            </Widget>
          )}
          {!loading && isValidOffer && (
            <Grid container justifyContent="center" spacing={2}>
              <Grid item xs={12} lg={8}>
                <History offer={offer} />
              </Grid>
              <Grid item xs={12} lg={4}>
                <Grid
                  container
                  direction="column"
                  justifyContent="space-between"
                  alignItems="flex-start"
                  spacing={2}
                >
                  {instruction && (
                    <Grid item xs={12}>
                      <Instruction instruction={instruction} />
                    </Grid>
                  )}

                  <Grid item xs={12}>
                    <Details offer={offer} isSeller={isSeller} />
                  </Grid>
                </Grid>
              </Grid>
            </Grid>
          )}
        </BlockUi>
      </PageWrapper>
    </>
  );
};

export default (OfferDetails: AbstractComponent<Props>);
