import React from 'react';
// material-ui
import { makeStyles } from '@material-ui/core/styles';
import { Button, Card, CardContent, Grid, Typography } from '@material-ui/core';

const useStyles = makeStyles(theme => ({
  card: {
    background: theme.palette.background.gray,
    marginTop: '16px',
    marginBottom: '16px',
    overflow: 'hidden',
    borderRadius: 'inherit',
    boxShadow: 'none',
    position: 'relative',
    '&:after': {
      content: '""',
      position: 'absolute',
      width: '200px',
      height: '200px',
      border: '19px solid ',
      borderColor: theme.palette.primary.main,
      borderRadius: '50%',
      top: '80px',
      right: '-150px',
    },
    '&:before': {
      content: '""',
      position: 'absolute',
      width: '200px',
      height: '200px',
      border: '3px solid ',
      borderColor: theme.palette.primary.main,
      borderRadius: '50%',
      top: '145px',
      right: '-70px',
    },
  },
  tagLine: {
    color: theme.palette.grey[900],
    opacity: 1,
  },
  button: {
    height: '40px',
    variant: 'contained',
    borderRadius: 'inherit',
    color: 'white',
    fontSize: '1.3em',
    backgroundColor: theme.palette.primary.main,
    textTransform: 'capitalize',
    boxShadow: 'none',
    '&:hover': {
      backgroundColor: theme.palette.success.main,
    },
  },
  switcherRole: {
    fontWeight: 'bold',
  },
}));

const SWITCHER_MODE = {
  buyer: {
    allowedSwitcher: 'IsSeller',
    btnName: 'Seller Dashboard',
    switcherRole: 'Switch to Seller Mode',
    switcherDescription: (
      <div>
        You don&apos;t have properties on sale. <br />
        Please contact NILE if you wish to sell your properties.
      </div>
    ),
  },
  seller: {
    allowedSwitcher: 'IsBuyer',
    btnName: 'Buyer Dashboard',
    switcherRole: 'Switch to Buyer Mode',
    switcherDescription: (
      <div>
        You don&apos;t have submitted offers to any property. <br />
        Please contact NILE if you wish to buy a property.
      </div>
    ),
  },
};

const SwitcherPlanCard = ({ roles, selectedRole, witchMode }) => {
  const classes = useStyles();

  const planCard = SWITCHER_MODE[selectedRole];
  const isDisableSwitchButton = !Object.keys(roles)
    .map(key => ({ [key]: roles[key] }))
    .some(roleItem => roleItem[planCard.allowedSwitcher]);

  return (
    <Card className={classes.card}>
      <CardContent>
        <Grid container direction="column" spacing={2}>
          <Grid item>
            <Typography variant="h4" className={classes.switcherRole}>
              {planCard.switcherRole}
            </Typography>
          </Grid>
          <Grid item>
            <Typography variant="subtitle2" className={classes.tagLine}>
              {planCard.switcherDescription}
            </Typography>
          </Grid>
          <Grid item>
            <Button
              disabled={isDisableSwitchButton}
              onClick={witchMode[selectedRole]}
              variant="contained"
              className={classes.button}
            >
              {planCard.btnName}
            </Button>
          </Grid>
        </Grid>
      </CardContent>
    </Card>
  );
};

export default SwitcherPlanCard;
