// @flow
import * as React from 'react';
import { AppBar, makeStyles } from '@material-ui/core';
import {
  ShowChart,
  HomeWork,
  FileCopy,
  CalendarTodayOutlined,
  Description,
} from '@material-ui/icons';
import style from './headerStyle';
import { APP_PATHS } from 'shared/Consts';
import { ENABLE_CALENDAR } from '../../shared/Consts';
import { AuthDesktopHeader } from './AuthDesktopHeader';
import { AuthMobileHeader } from './AuthMobileHeader';
import { useIsMobile } from '../../hooks';

const useStyles = makeStyles(style);

const links = [
  {
    label: 'Dashboard',
    icon: <ShowChart key="chart" />,
    path: APP_PATHS.dashboard,
    showIf: () => true,
  },
  {
    label: 'My Properties',
    icon: <HomeWork key="homework" />,
    path: APP_PATHS.properties,
    showIf: role => role.IsSeller,
  },
  {
    label: 'Offers',
    icon: <FileCopy key="offers" />,
    path: APP_PATHS.offers,
    showIf: role => role.IsSeller,
  },
  {
    label: 'Calendar',
    icon: <CalendarTodayOutlined key="calendars" />,
    path: APP_PATHS.sellerCalendar,
    showIf: role => role.IsSeller && ENABLE_CALENDAR,
  },
  {
    label: 'Documents',
    icon: <Description key="documents" />,
    path: APP_PATHS.documents,
    showIf: () => true,
  },
];

export const AuthHeader = (): React.Element<typeof AppBar> => {
  const classes = useStyles();
  const isMobile = useIsMobile();
  const Component = isMobile ? AuthMobileHeader : AuthDesktopHeader;
  return (
    <AppBar position="static" color="default" className={classes.header}>
      <Component links={links} />
    </AppBar>
  );
};
