import React, { useEffect } from 'react';
import withCustomAuthenticator from '../Authenticator/withCustomAuthenticator';
import { PageWrapper } from '../../components/PageWrapper';
import { GET_PROPERTIES } from 'graphql/queries/user/overview';
import { useQuery } from '@apollo/client';
import { Grid } from '@material-ui/core';
import CalendarWidget from './CalendarWidget';
import { PROPERTY_ERROR_MESSAGE } from 'shared/Messages';
import { toastr } from 'react-redux-toastr';
import _get from 'lodash.get';

const SellerCalendarPage = props => {
  const { loading, error, data } = useQuery(GET_PROPERTIES);
  const properties = _get(data, ['getUserOverview', 'Properties', 'Items'], []);

  useEffect(() => {
    const { message } = error || {};
    if (message) {
      toastr.error(PROPERTY_ERROR_MESSAGE.fetchFailure, message);
    }
  }, [error]);

  return (
    <PageWrapper>
      <Grid container justifyContent="center">
        <Grid item xs={12}>
          <CalendarWidget loading={loading} properties={properties} />
        </Grid>
      </Grid>
    </PageWrapper>
  );
};

export default withCustomAuthenticator(SellerCalendarPage);
