import React from 'react';
import GlobalNavigator from './GlobalNavigator';
import GlobalActionsBar from './GlobalActionBar';

export default function GlobalDataPage({ ActionDef, GlobalProps }) {
  const { SelectedTab, HasThankYouPage, ThankYouPageComponent } = GlobalProps;
  const { DataPageConfig } = ActionDef;
  const dataPage = DataPageConfig.DataPages.find(
    dp => dp.PageNum === SelectedTab,
  );

  return (
    <>
      <GlobalNavigator ActionDef={ActionDef} GlobalProps={GlobalProps} />
      {dataPage !== undefined && dataPage.Components !== undefined && (
        <div className="main_form_info steppers">
          {dataPage.Components.map(Component => {
            return <Component {...GlobalProps} key={Component} />;
          })}
        </div>
      )}

      {SelectedTab === DataPageConfig.TotalPages + 1 &&
        HasThankYouPage === true &&
        ThankYouPageComponent !== undefined && (
          <ThankYouPageComponent {...GlobalProps} />
        )}

      <GlobalActionsBar ActionDef={ActionDef} GlobalProps={GlobalProps} />
    </>
  );
}
