import { createStore, combineReducers, compose } from 'redux';
import { reducer as toastrReducer } from 'react-redux-toastr';
import { propertiesReducer } from 'containers/Property/redux/Reducer';
import {
  sellerOffersReducer,
  sellerOfferReducer,
} from 'containers/SellerOffer/redux/Reducer';
import { calendarReducer } from 'components/Calendar/redux/Reducer';

const reducers = {
  toastr: toastrReducer, // <- Mounted at toastr.
  sellerOffer: sellerOfferReducer,
  sellerOffers: sellerOffersReducer,
  properties: propertiesReducer,
  calendar: calendarReducer,
};

const composeEnhancers =
  typeof window === 'object' && window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__
    ? window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__({})
    : compose;

const enhancer = composeEnhancers();

const reducer = combineReducers(reducers);
const store = createStore(reducer, enhancer);

export default store;
