import { useMutation } from '@apollo/client';
import { SAVE_FORM_DATA, SAVE_SUBMIT_FORM_DATA } from 'graphql/mutations/forms';

export const useFormSave = () => {
  const [doSave, { loading: isSaving, data: savedData }] =
    useMutation(SAVE_FORM_DATA);
  const [doSubmit, { loading: isSubmitting, data: submittedData }] =
    useMutation(SAVE_SUBMIT_FORM_DATA);

  return {
    doSave,
    isSaving,
    savedData,
    doSubmit,
    isSubmitting,
    submittedData,
  };
};
