import * as Consts from './Consts';

export const GetInputValue = (inputValues, fieldName) => {
  return inputValues[fieldName];
};

export const HasInputValue = (inputValues, fieldName, value) => {
  const fieldValue = inputValues[fieldName];

  if (fieldValue === undefined) {
    return false;
  } else {
    if (
      Array.isArray(fieldValue) &&
      fieldValue.find(item => item.value === value)
    ) {
      return true;
    } else if (Array.isArray(fieldValue) === false && fieldValue === value) {
      return true;
    } else {
      return false;
    }
  }
};

export const GenerateSelectOptions = ({ InputValues, Options, RowNumber }) => {
  if (Options.find(option => option.DependsOn !== undefined) === undefined) {
    return Options;
  }

  var newOptions = [];
  Options.forEach(option => {
    if (option.DependsOn === undefined) {
      newOptions.push({ label: option.label, value: option.value });
    } else {
      const dependsOn = option.DependsOn;
      if (Array.isArray(dependsOn)) {
        dependsOn.forEach(dependsOnItem => {
          var isDisabled = false;

          if (dependsOnItem.row_field !== undefined) {
            const rowField = `${dependsOnItem.row_field}_${RowNumber}`;
            const rowValue = dependsOnItem.row_value;

            if (InputValues[rowField] !== undefined) {
              if (Array.isArray(rowValue)) {
                isDisabled =
                  rowValue.find(item => item === InputValues[rowField]) ===
                  undefined;
              } else {
                isDisabled = rowValue !== InputValues[rowField];
              }
            } else {
              isDisabled = true;
            }
          } else if (dependsOnItem.field !== undefined) {
            const globalField = `${dependsOnItem.field}`;
            const globalValue = dependsOnItem.value;

            if (InputValues[globalField] !== undefined) {
              if (Array.isArray(globalValue)) {
                isDisabled =
                  globalValue.find(
                    item => item === InputValues[globalField],
                  ) !== undefined;
              } else {
                isDisabled = globalValue === InputValues[globalField];
              }
            } else {
              isDisabled = true;
            }
          }
          newOptions.push({
            label: option.label,
            value: option.value,
            isDisabled,
          });
        });
      }
    }
  });

  return newOptions;
};

export const IsDependsOnMet = (inputValue, dependsOnValue) => {
  if (inputValue === undefined) {
    return false;
  } else {
    if (Array.isArray(inputValue)) {
      if (Array.isArray(dependsOnValue)) {
        for (const value of dependsOnValue) {
          const inputValueItem = inputValue.find(
            item => `${item.value}`.toUpperCase() === `${value}`.toUpperCase(),
          );
          if (inputValueItem !== undefined) {
            return true;
          }
        }
      } else {
        const inputValueItem = inputValue.find(
          item =>
            `${item.value}`.toUpperCase() === `${dependsOnValue}`.toUpperCase(),
        );
        return inputValueItem !== undefined;
      }
    } else {
      if (dependsOnValue === Consts.OPTION_ANY_VALUE) {
        return true;
      } else {
        if (Array.isArray(dependsOnValue)) {
          for (const value of dependsOnValue) {
            if (`${inputValue}`.toUpperCase() === `${value}`.toUpperCase()) {
              return true;
            }
          }
        } else {
          return (
            `${inputValue}`.toUpperCase() === `${dependsOnValue}`.toUpperCase()
          );
        }
      }
    }
    return false;
  }
};
