import { OFFER } from './Constants';

export const sellerOffersReducer = (
  state = { list: [], isLoading: false },
  { type, payload },
) => {
  switch (type) {
    case OFFER.fetchOffers:
      return {
        ...state,
        isLoading: true,
        error: null,
      };
    case OFFER.fetchOffersSuccess:
      return {
        ...state,
        list: payload,
        isLoading: false,
        error: null,
      };
    case OFFER.fetchOffersError:
      return {
        ...state,
        list: [],
        isLoading: false,
        error: payload,
      };
    default:
      return state;
  }
};

export const sellerOfferReducer = (
  state = { isLoading: false, error: null, isUpdatedStep: false },
  { type, payload },
) => {
  switch (type) {
    case OFFER.saveOfferStep:
      return {
        ...state,
        isLoading: true,
        isUpdatedStep: false,
        error: null,
      };
    case OFFER.saveOfferStepSuccess:
      return {
        ...state,
        isLoading: false,
        isUpdatedStep: true,
        error: null,
      };
    case OFFER.saveOfferStepError:
      return {
        ...state,
        isLoading: false,
        isUpdatedStep: false,
        error: payload,
      };
    default:
      return state;
  }
};
